export interface PageInterface {
  title: string;
  name: string;
  component: any;
  icon: string;
  logsOut?: boolean;
  index?: number;
  tabName?: string;
  tabComponent?: any;
  eventOut?:boolean;
  activity?:string;
  onlyAdmin?:boolean;
  visible?:boolean;
}

@Component({
  templateUrl: 'app.template.html'
})
export class ConferenceApp {

  @ViewChild(Nav) nav: Nav;
  jetons:number=0;
  //now:number=new Date().getTime();
  handleUser: any;

  appPages: PageInterface[] = [];

  loggedInPages: PageInterface[] = [
    { title: 'LIB.FINDEVENTS', name: 'SeleventPage', component: SeleventPage, icon: 'compass',visible:true },
    { title: 'LIB.MYEVENTS', name: 'OldeventsPage', component: OldeventsPage, icon: 'list',visible:true },
    { title: 'LIB.SCREENS', name: 'ScreensPage', component: ScreensPage, icon: 'desktop',visible:true },
    { title: 'LIB.LOGOUT', name: "LoginPage", component: LoginPage, icon: 'log-out', logsOut: true,visible:true },
  ];

  loggedInPagesWithEvent: PageInterface[] = [
    { title: 'LIB.SCREENS', name: 'ScreensPage', component: ScreensPage, icon: 'desktop',onlyAdmin:true,visible:true}
  ];

  loggedOutPages: PageInterface[] = [
     { title: 'LIB.LOGIN', name: 'LoginPage', component: LoginPage, icon: 'log-in',visible:true }
  ];

  rootPage: any;


  constructor(
    private network: Network,
    public events: Events,
    public modalCtrl:ModalController,
    public userData: UserData,
    public menu: MenuController,
    public platform: Platform,
    public api:ApiProvider,public toastCtrl:ToastController,
    public storage: Storage,
    public translate: TranslateService,
    public eventData:EventDataProvider,
    public domSanitizer:DomSanitizer,
    public settings:SettingsProvider,
    public alertCtrl:AlertController,
    public loadingCtrl:LoadingController,
    private app:App
  ) {

    platform.ready().then(() => {
      // this.cache.setDefaultTTL(60 * 60 * 12);
      // this.cache.setOfflineInvalidate(false);

      translate.setDefaultLang("en");
      translate.use(this.platform.lang());
      getLibs().forEach(function(libs){
        translate.setTranslation(libs.lang,libs.labels,true);
      });
      app._setDisableScroll(true);


      this.initPages();

      domSanitizer.bypassSecurityTrustUrl("https://storage.googleapis.com/");

      //Récuperation des parametres de l'url
      var mode =platform.getQueryParam("mode");
      this.storage.set("notuto", platform.getQueryParam("notuto"));
      this.storage.set("withplayer", platform.getQueryParam("withplayer"));
      this.storage.set("inviteEvent", platform.getQueryParam("inviteEvent"));
      this.storage.set("from", platform.getQueryParam("from"));
      this.storage.set("from", platform.getQueryParam("login"));
      this.storage.set("sc", platform.getQueryParam("sc"));
      this.storage.set("location_lat", platform.getQueryParam("location_lat"));
      this.storage.set("location_lng", platform.getQueryParam("location_lng"));
      this.storage.set("location_zoom", platform.getQueryParam("location_zoom"));
      this.storage.set("flyer", decodeURIComponent(platform.getQueryParam("flyer")));

      this.settings.code=platform.getQueryParam("code");
      this.settings.dest=platform.getQueryParam("dest");

      this.storage.set("profil", platform.getQueryParam("profil"));
      this.storage.set("widgetFilter", platform.getQueryParam("widgetFilter"));
      this.storage.set("server", platform.getQueryParam("server"));
      this.storage.set("for", platform.getQueryParam("for"));
      this.storage.set("type", platform.getQueryParam("type"));
      this.storage.set("todo", platform.getQueryParam("todo"));
      this.storage.set("type_invite", platform.getQueryParam("type_invite"));
      //this.storage.set("connexion", platform.getQueryParam("connexion"));
      this.storage.set("message", platform.getQueryParam("message"));
      this.storage.set("instantEvent", platform.getQueryParam("instantEvent"));
      //this.storage.set("dest", platform.getQueryParam("dest"));
      this.storage.set("scaling", platform.getQueryParam("scaling"));
      if(platform.getQueryParam("raz")=="true")this.storage.remove("user");
      this.storage.set("anonymous", platform.getQueryParam("anonymous"));

      let vm=this;
      this.platform.setQueryParams("");


      $$("Analyse du reseau");
      let disconnectSubscription = this.network.onDisconnect().subscribe(() => {
        toast(vm.toastCtrl,"ERROR.LOSTNETWORK",vm.translate);
        vm.eventData.online=false;
      });

      this.network.onConnect().subscribe(()=>{
        vm.eventData.online=true;
      });

      $$("Check if the user has already seen the tutorial");
      this.storage.get('hasSeenTutorial').then((hasSeenTutorial) => {
        var vm=this;
        //Dans certains cas, on cherche a afficher uniquement la page public du profil
        this.api.connectToServer(platform.getQueryParam("server"),this.platform.userAgent(),rep => {
          this.eventData.socketServerAddress = rep.socketserver;
        },(err)=>{$$("error",err)});

        if(platform.getQueryParam("follower")){
          this.api.connectToServer(platform.getQueryParam("server"),this.platform.userAgent(),(r) => {
            this.eventData.socketServerAddress=r.socketserver;
            vm.nav.setRoot(PublicProfilPage,{userid:platform.getQueryParam("userid"),follower:platform.getQueryParam("follower")});
          },(err)=>{$$("error",err)})
        } else {
          this.rootPage = LoginPage;
        }
      });

      this.enableMenu("loggedOutMenu"); //Cohérent du fait de l'usage d'une promesse au dessucs
      this.listenToLoginEvents();       //Gestionnaire d'événement

      clearTimeout(this.handleUser);
      this.handleUser=setTimeout(()=>{
        this.events.subscribe("user",()=>{
          this.userData.get().subscribe(r=>{
            this.userData.user=r;
            if(this.userData.user.jetons!=null && this.eventData.event!=null && this.eventData.event.owner_id!=null)
              this.jetons=this.userData.user.jetons[this.eventData.event.owner_id];
          });
        });
      },1000)


    });


  }


  initPages(){
    this.appPages=[
      { title: 'LIB.SETTINGS', name: 'Profil', component: TabsPage, tabComponent: ProfilPage,icon: 'settings',activity:'',onlyAdmin:true,visible:true},
      { title: 'LIB.MUSIC', name: 'TabsPage', component: TabsPage, tabComponent: HomePage, index: 0, icon: 'musical-notes',activity:'music',onlyAdmin:false,visible:true},
      { title: 'Photos', name: 'TabsPage', component: TabsPage, tabComponent: PhotosPage, index: 1, icon: 'camera',activity:'photo',onlyAdmin:false,visible:true },
      { title: 'LIB.PLAY', name: 'TabsPage', component: TabsPage, tabComponent: BetsPage, index: 2, icon: 'baseball',activity:'bets,survey,loterie',onlyAdmin:false,visible:true },
      { title: 'LIB.COMMAND', name: 'TabsPage', component: TabsPage, tabComponent: CustomercommandPage, index: 3, icon: 'beer',activity:'command',onlyAdmin:false,visible:true },
      { title: 'Message', name: 'TabsPage', component: TabsPage, tabComponent: MessagesPage, index: 4, icon: 'chatbubbles',activity:'message',onlyAdmin:false,visible:true },
      { title: 'LIB.PRESENTATION', name: 'TabsPage', component: TabsPage, tabComponent: PresentationsPage, index: 5, icon: 'albums',activity:'presentation',onlyAdmin:false,visible:true },
      { title: 'LIB.PEOPLE', name: 'TabsPage', component: TabsPage, tabComponent: ChartsPage, index: 6, icon: 'people',activity:'',onlyAdmin:false,visible:true }
    ];
  }


  inviteEvent(){
    openModal(this.modalCtrl,InvitePage,{from:this.userData.user,event:this.eventData.event});
  }

  openPageByName(l_pages:PageInterface[],pageName:string,params:any={}){
    if(pageName==null){
      this.openPage(l_pages[0],params)
    } else {
      pageName=pageName.toLowerCase();
      var result:any=null;
      l_pages.forEach((p) => {
        if(p.name.toLowerCase()==pageName || (p.name=="TabsPage" && p.tabComponent.name.toLowerCase()==pageName))
          result=p;
      });
      if(result==null){
        $$("page "+pageName+" introuvable");
        this.openPage(l_pages[1],params);
      }
      else
        this.openPage(result,params);
    }
  }

  openPage(page: PageInterface,params:any={}) {
    if(page.tabComponent!=undefined)
      $$("Ouveture de : "+page.name+" avec tab="+page.tabComponent.name);
    else
      $$("Ouveture de : "+page.name);

    if (page.logsOut === true) {
      // Give the menu time to close before changing to logged out
      this.events.publish("user:logout",{});
      return;
    }

    // the nav component was found using @ViewChild(Nav)
    // setRoot on the nav to remove previous pages and only have this page
    // we wouldn't want the back button to show in this scenario
    var index=0;
    if (page.hasOwnProperty("index")) {

      //Recalcul de l'index en fonction des icons visibles

      var i=0;
      buildTabs(this.userData.user,this.eventData.event).forEach(p => {
        if(p.tabIcon==page.icon)index=i;
        i++;
      });
      params = { tabIndex: index,tabName:page.tabComponent.name };

      //params={tabIndex:page.index,tabName:page.tabComponent.name};
      if(page.tabComponent!=undefined)this.userData.selectTab=page.tabComponent.name;
    }

    // If we are already on tabs
    if (this.nav.getActiveChildNavs().length && page.index != undefined) {
      //just change the selected tab
      // don't setRoot again, this maintains the history stack of the
      // tabs even if changing them from the menu
      this.nav.getActiveChildNavs()[0].select(index);
    } else {
      // Set the root of the nav with params if it's a tab index
      this.nav.setRoot(page.name, params,{},()=>{
        if (this.nav.getActiveChildNavs().length>0)
          this.nav.getActiveChildNavs()[0].select(index);
      }).catch((err: any) => {
        $$(`Didn't set nav root: ${err}`);
      });
    }
  }

  openTutorial() {
    this.nav.setRoot(TutorialPage);
  }

  openSupport() {
    openModal(this.modalCtrl,SupportPage);
  }

  openAbout() {
    this.nav.setRoot(AboutPage);
  }

  async checkInvitation() {
    var vm = this;
    let evt=null;

    let resp = await this.userData.autoconnexion(this.settings.connexion).toPromise();
    $$("autoconnexion retourne", resp);

    if (resp != null) {
      vm.userData.user = resp;
      if (vm.userData.user.hasOwnProperty("currentEvent") && vm.userData.user.currentEvent.length > 0) {
        evt = await vm.api.getevent(vm.userData.user.currentEvent).toPromise();
        vm.storage.remove("inviteEvent");
        vm.storage.remove("for");
        return evt;
      }
      else {
        if (vm.userData.user.message.length > 0) {
          if (vm.userData.user.message.startsWith("SELEVENT.CLOSED")) {
            var idEvent = vm.userData.user.message.replace("SELEVENT.CLOSED:", "");
            let evt = await vm.api.getevent(idEvent).toPromise();

            vm.storage.remove("inviteEvent");
            //TODO: a tester
            openModal(vm.modalCtrl,ClosedeventPage,{event: evt, user: vm.userData.user},()=>{});
          }
        }
      }
    }
    return evt;
  }


  closeEvent(){
      var vm=this;
      showConfirm("LIB.EVENTCLOSING",this.alertCtrl,this.translate,  function () {
        vm.eventData.closeevent(vm.userData.user.id).subscribe((resp:any) => {
          vm.userData.user=resp;
          var delay=Number(resp.message)-new Date().getTime();
          // if(delay<10000)
          //   vm.events.publish("event:logout");
          // else {
            toast(vm.toastCtrl,"End in " + getDelay(Number(vm.userData.user.message)));
          // }
        });
      });
  }

  /**
   * Condition d'affichage de chaque option dans le menu
   * @param {PageInterface} p
   * @returns {boolean}
   */
  showPage(p:PageInterface):boolean {
    if(this.eventData.event==null || this.eventData.event.activities.length==0)return false;

    if(p.activity.length>0){
      var bc=false;
      p.activity.split(",").forEach((a)=>{
        if(!bc && this.eventData.event.activities.indexOf(a)>-1)bc=true;
      })
      if(!bc)return false;
    }

    if(p.activity=="command" && this.eventData.event.laCarte.length==0 && this.eventData.event.Preparators.indexOf(this.userData.user.id)==-1)return false;

    if(p.onlyAdmin)
      if(this.userData.user.id!=this.eventData.event.owner_id)return false;

    return true;
  }


  findPage(pages:any[],name:string){
    var rc=null
    pages.forEach(function(p){
      if(p.name.toLowerCase()==name.toLowerCase()){
        rc=p;
      }
    });
    return rc;
  }

  checkCGU(func_CGU_ok:any,func_CGU_ko:any){
    $$("Vérification de signature des CGUs");
    this.api.termofuse(this.userData.user.lang).subscribe((r)=>{
      if(Number(r.dtCGU)>this.userData.user.dtSignatureCGU && this.userData.user.email!=null && this.userData.user.email.length>0){
        openModal(this.modalCtrl,TermOfUsePage,{CG:r.body},(rep)=>{
          if(rep){
            this.userData.user.dtSignatureCGU=new Date().getTime();
            this.userData.senduser("dtSignatureCGU").subscribe((u)=>{
              if(u!=null)this.userData.user=u;
            });
            func_CGU_ok();
          } else {
            func_CGU_ko();
          }
        });
      }
      else
        func_CGU_ok();
    });
  }


  /**
   * Création d'un événement par défaut (quand le champs dest designe un page et qu'aucun n'événement n'existe)
   */
  createDefaultEvent(dest:string,func){
    var vm=this;

    var event_name="party";
    if(this.settings.ihm=="pro")event_name="meeting";

    showPopup({title:"ADDEVENT.GIVETITLE",default:this.userData.user.firstname+"' "+event_name,confirmButton:"LIB.OK",type:"text",translate:this.translate},this.alertCtrl,(res)=> {
      $$("Il n'y a auncun évenement actif, on créé un evénement par défaut:", event);
      vm.eventData.addbasic(this.userData.user.id, res, 0,8*60, "music,message,photo", "",this.userData.user.lat,this.userData.user.lng).subscribe((rep:any) => {
        if (rep.error.length == 0) {
          var l=loading(this);
          setTimeout(()=>{
            l.dismiss();
            this.api.getevent(rep.event).subscribe((evt) => {func(evt);});
          },2000);
        } else
          toast(this.toastCtrl, rep.message, this.translate);
      });
    });
  }


  joinEvent(dest:string,evt:any){
    this.events.publish("event:login",
      {event:evt, user:this.userData.user,type:1,url:"index.html?event="+evt.id+"&from="+this.userData.user.id,dest:dest}
    );
  }



  /**
   * Souscription aux principaux événements
   */
  listenToLoginEvents() {
    $$("Enregistrement des 4 principaux événements : event:login, user:login, event:logout, user:logout")
    var vm=this;

    this.events.subscribe("user:refresh", (user) => {
      $$("L'utilisateur a recupere un mail, on met a jour le menu");
      this.loggedInPages[3].visible=(user.email.length>0);
      if(user.currentEvent.length>0)
        vm.enableMenu("loggedInMenuWithEvent");
      else{
        vm.enableMenu("loggedInMenu");
      }
    });


    /**
     * Connexion de l'utilisateur
     */
    this.events.subscribe("user:login", (user:any) => {
      vm.userData.get(user.id).subscribe((user:any)=>{
        this.userData.user=user;
        this.translate.use(user.lang);

        $$("L'utilisateur vient de se connecter : ",this.userData.user);

        this.toastCtrl.create({
          message: this.translate.instant("LOGIN.WELCOME")+", "+this.userData.user.firstname,
          duration: 1500,
          position: 'bottom'
        }).present();

        vm.enableMenu("loggedInMenu");

        this.storage.set('user', vm.userData.user.id); //Stockage sur le device

        this.checkCGU(()=>{
          $$("Récupération du code d'écran s'il y en a un et traitement");
          if (this.settings.code != null) {
            $$("Mise a jour du user avec le code de l'écran pour assusrer le ratachement",this.settings);
            vm.userData.user.code = this.settings.code;
            vm.userData.senduser("code").subscribe(() => {this.settings.code=null;});
          }

          this.checkInvitation().then((evt)=>{
            if(this.userData.user.currentView.length>0)this.settings.dest=this.userData.user.currentView;
            if(this.settings.dest!=null && evt==null) {
              $$("Débranchement demandé vers " + this.settings.dest);
              $$("On essaye de récupérer un événement");
              this.api.geteventsfrom(this.userData.user.id, 1, true, true).subscribe((evts_actifs) => {
                if (evts_actifs == undefined || evts_actifs.items.length == 0) {
                  this.events.publish("screen:add");
                } else {
                  $$("l'utilisateur a des événements actifs, on se connect au premier");

                  this.joinEvent(this.settings.dest, evts_actifs.items[0]);
                }
              });
            }

            if(evt!=null){
              $$("Invitation à un événement ",evt);
              this.joinEvent(this.settings.dest,evt);
            } else {
              $$("Pas d'invitation à un événement particulier");
              vm.openPageByName(vm.loggedInPages,"SeleventPage");
            }



          });

        },()=>{this.events.publish("user:logout");})

      });

    });

    /**
     * Quit l'événements
     */
    this.events.subscribe("event:logout",(param)=>{
      var vm=this;
      if(param==null)param={};
      vm.userData.quitEvent().subscribe(resp=>{
        if(resp!=null){
          toast(this.toastCtrl,param.message,this.translate);
          this.initPages();
          vm.enableMenu("loggedInMenu");
          var p=this.findPage(vm.loggedInPages,"OldEventsPage");
          vm.openPage(p);
          vm.userData.user=resp;
          setTimeout(()=>{vm.eventData.quit();},1000);
        }
        else this.events.publish("user:logout");
      });
      //this.events.publish("event:logout",{id:this.eventData.event.id});
    });

    this.events.subscribe("moderator:add",()=>{
      let index=this.findPage(this.appPages,"ChartsPage");
      vm.openPage(this.appPages[index],{message:"CHARTS.CHOOSEMODERATOR"});
    });


    /**
     * Déconnexion de l'utilisateur
     * user:logout
     */
    this.events.subscribe('user:logout', () => {
      $$("Demande de deconnexion du l'utilisateur")
      showConfirm("INFO.ANONYMELOGOUT",this.alertCtrl,this.translate,()=>{
        var vm=this;
        this.enableMenu('loggedOutMenu');
        vm.userData.logout().subscribe(()=>{


          let url=location.href;
          if(url.indexOf("?")>-1)url=url.substr(0,location.href.indexOf("?"));
          if(url.indexOf("#/")>-1)url=url.substr(0,url.indexOf("#/"));
          if(isLocal())url=url+"?server=local";

          //url=url+"/#/login";

          openWindow(url,"_self");
        });
      },()=>{
        vm.openPage(this.loggedInPages[2]);
      },(this.userData.user.email.length>0))
    });



    this.events.subscribe('oldevents:open', () => {
      this.openPage(this.loggedInPages[1]);
    });

    this.events.subscribe("open:widget",(params)=>{
      openModal(this.modalCtrl,WidgetstorePage,{widgetFilter:params});
    });

    /**
     * Connexion à un événement event:login
     */
    this.events.subscribe("event:login", (params) => {
      $$("Demande de connexion a ",params.event);
      var l=loading(this,"Connexion "+params.event.title);

      vm.userData.join(params.event.id, params.password, "", "", vm.userData.user.lat + "," + vm.userData.user.lng, "", vm.userData.user.currentView)
        .subscribe((rep:any) => {
        l.dismiss();
        if (rep != null) {
          vm.userData.user = rep; //On récupére une mise a jour du user
          if (rep.message.length==0) { //Pas de message d'alerte

            //On met a jour eventData.event avec l'evement
            vm.eventData.join(params.event.id,this.userData.user.id,(rep)=> {

              if(vm.userData.user.jetons!=undefined)vm.jetons=vm.userData.user.jetons[vm.eventData.event.owner_id];
              $$("Suppression des options non présentes dans l'événements");
              var rc=[];
              var i=0;

              vm.enableMenu('loggedInMenuWithEvent');
              let dest=vm.userData.user.currentView.toLowerCase(); //Récupere les meilleurs vue du serveur
              if(dest=="undefined" || dest==null || dest.length==0)dest=params.dest;
              if(dest=="undefined" || dest==null || dest.length==0){
                dest=null;
                vm.appPages.forEach((page)=>{
                  if(dest==null && this.showPage(page)){
                    dest=page.tabComponent.name;
                  }
                });
              }

              if(vm.eventData.event.welcomePicture.length>0) {
                openModal(vm.modalCtrl, WelcomePage, {image: vm.eventData.event.welcomePicture,duration: vm.eventData.event.welcomeDuration * 1000}, () => {
                  this.openPageByName(vm.appPages,dest);
                })
              }
              else
                this.openPageByName(vm.appPages,dest);
            });

          } else {
            toast(this.toastCtrl,rep.message,this.translate);
            $$("Refus de connexion");
            this.events.publish("event:logout");
          }
        } else {
          $$("non reponse de join");
        }
      });




    });

    this.events.subscribe("screen:add",(params)=>{
      this.openPageByName(this.loggedInPagesWithEvent,ScreensPage.name,params);
    });

    this.events.subscribe("event:add",(params)=>{
      this.openPageByName(this.loggedInPages,AddeventPage.name,params);
    });


  }

  enableMenu(menuName:string) {
    this.menu.enable(false, 'loggedInMenuWithEvent');
    this.menu.enable(false, 'loggedInMenu');
    this.menu.enable(false, 'loggedOutMenu');
    this.menu.enable(true,menuName);
    if(menuName=="loggedInMenu" && this.userData.user!=null){
      if(this.userData.user.email==null || this.userData.user.email.length==0)
        this.loggedInPages[3].title=this.translate.instant("LIB.CLEARACCOUNT");
      else
        this.loggedInPages[3].title=this.translate.instant("LIB.LOGOUT");
    }
  }


  isActive(page: PageInterface) {
    let childNav = this.nav.getActiveChildNavs()[0];

    // Tabs are a special case because they have their own navigation
    if (childNav) {
      if (childNav.getSelected() && childNav.getSelected().root === page.tabComponent) {
        return 'primary';
      }
      return;
    }

    if (this.nav.getActive() && this.nav.getActive().name === page.name) {
      return 'primary';
    }
    return;
  }

  openProfil(){
    openModal(this.modalCtrl,PersoPage,{from:null});
  }

  openTrajet(){
    var mypos=this.userData.user.lat+","+this.userData.user.lng;
    openWindow("https://www.google.com/maps/dir/'"+mypos+"'/'"+this.eventData.event.lat+","+this.eventData.event.lng+"'");
  }



}



