import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import * as Tools from "../../app/Maintools"
import {NavController} from "ionic-angular";

/**
 * Generated class for the ShifutitleComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
  selector: 'shifutitle',
  templateUrl: 'shifutitle.html'
})
export class ShifutitleComponent implements OnInit {
  @Input("label") label: string="";
  @Input("title") title: string="";
  @Input("menu") menu:boolean=true;
  @Input("back") back:boolean=true;
  @Input("help") help:string="";

  @Output('onclick') onclickbutton: EventEmitter<any>=new EventEmitter();
  @Output('onclose') onclose: EventEmitter<any>=new EventEmitter();

  close:boolean=false;

  constructor(public navCtrl:NavController) {
  }

  ngOnInit(): void {
    if(this.onclose.observers.length>0)this.close=true;
  }


  showHelp(){
    if(this.help=="#"){
      Tools.openHelp("#"+this.navCtrl.getActive().id);
    }
    else
      Tools.openHelp(this.help);
  }

  navBarButton(){
    this.onclickbutton.next({value:"go"});
  }

}



