import { Component } from '@angular/core';
import {AlertController, IonicPage, NavController, NavParams, ToastController, ViewController} from 'ionic-angular';
import { UserData } from '../../providers/user-data';
import * as Main from "../../app/Maintools"
import {TranslateService} from "@ngx-translate/core";
import {toast} from "../../app/Maintools";
import {SettingsProvider} from "../../providers/settings/settings";

/**
 * Generated class for the LoginemailPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-loginemail',
  templateUrl: 'loginemail.html',
})
export class LoginemailPage {

  showpassword:boolean=false;
  password_ressended:boolean=false;
  login_user:any={email:"",pseudo:"",password:"",lang:"en"};

  constructor(public navCtrl: NavController, public navParams: NavParams,public alertCtrl:AlertController,
              public userData: UserData, public viewCtrl:ViewController,public settings:SettingsProvider,
              public toastCtrl:ToastController,public translate:TranslateService) {
  }

  ionViewDidLoad() {
    this.login_user.lang=navigator.language;
    if(this.settings.login_email.length>0){
      this.login_user.email=this.settings.login_email;
      this.email_login(13);
      this.settings.login_email="";
    }
  }

  add(emailserver:string){
    this.login_user.email=this.login_user.email+emailserver;
    this.email_login(13);
  }

  email_login(keycode){
    if(keycode!=13)return;

    var vm=this;
    if(vm.login_user.email.indexOf("@")==-1)vm.login_user.email+="@gmail.com";

    this.userData.getuserbyemail(vm.login_user.email).subscribe((resp) =>{
      if(resp==null) {//Le compte n'est pas encore créé

        var pseudo=this.navParams.get("pseudo");
        if(pseudo==null)pseudo=vm.login_user.email.split("@")[0];
        if(pseudo.indexOf(".")>-1)pseudo=pseudo.split(".")[0];
        // Main.showPopup({
        //   title: "LOGIN.PSEUDO",
        //   default: pseudo,
        //   confirmButton: "LIB.OK",
        //   cancelButton: "LIB.CANCEL",
        //   type: "text",translate:vm.translate
        // }, this.alertCtrl, function (pseudo) {
        //   vm.showpassword=true;
        //   if(vm.navParams.get("from")=="PersoPage"){
        //     vm.userData.user.firstname=pseudo;
        //     vm.userData.senduser("firstname").subscribe(()=>{});
        //   } else {
        var id=null;
        if(vm.userData.user!=null)id=vm.userData.user.id;
            vm.userData.add({
              id:id,
              pseudo:pseudo,
              email:vm.login_user.email,
              lang:vm.login_user.lang
            },"mail").subscribe(data => {},error => {
              console.log(error.status);
            });
        //
        // }
      }
      vm.showpassword=true;
    });
  };

  password_login(keycode){
    if(keycode!=13)return;
    var vm=this;
    this.userData.loginbyemail(vm.login_user.email,vm.login_user.password).subscribe(resp =>{
      if(resp==null){
        this.password_ressended=false;
        Main.toast(vm.toastCtrl,"LOGIN.WRONGPASSWORD");
      } else {
        vm.viewCtrl.dismiss(resp);
      }
    });
  };

  resend_password(){
    toast(this.toastCtrl,"Secret code sended");
    this.userData.resend_password(this.login_user.email,false).subscribe(() => {
      this.login_user.password="";
    });
  };

  checkemail(){
    var vm=this;
    var s=vm.login_user.email.toLowerCase();
    var h=null;
    if(s.indexOf("gmail.com")!=-1)h=window.open("https://mail.google.com/mail/","_blank");
    if(s.indexOf("yahoo.com")!=-1)h=window.open("https://mail.yahoo.com","_blank");
    if(s.indexOf("hotmail.com")!=-1)h=window.open("https://hotmail.com","_blank");
    if(h==null)window.open(s.split("@")[1],"_blank");
  };

  valide(){
    if(this.showpassword)
      this.email_login(13);
    else
      this.password_login(13);
  }

}



