import { Component } from '@angular/core';
import {IonicPage, ModalController, NavController, NavParams, ToastController, ViewController} from 'ionic-angular';
import {UserData} from "../../providers/user-data";
import {TranslateService} from "@ngx-translate/core";
import {DOMAIN_SERVER, onCopy, openModal, ROOT_API, showConfirm, toast} from "../../app/Maintools";
import {ImageCreatorPage} from "../image-creator/image-creator";
import {ApiProvider} from "../../providers/api/api";
import {WidgetstorePage} from "../widgetstore/widgetstore";

/**
 * Generated class for the AddeventAdvancedPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-addevent-advanced',
  templateUrl: 'addevent-advanced.html',
})
export class AddeventAdvancedPage {
  magiklink: string="";
  user:any={connexions:[]};
  newevent:any={};


  constructor(public modalCtrl:ModalController,public translate:TranslateService,public params: NavParams,public viewCtrl: ViewController,
              public userData:UserData,public api:ApiProvider,public toastCtrl:ToastController) {
    this.newevent=this.params.get("newevent");
    this.newevent["lst_activities"]=[];
    this.userData.get().subscribe(r => this.user=r);
    this.newevent.lst_activities = [
      { text: "ADDEVENT.ACT_MUSIC", checked: (this.newevent.activities.indexOf("music")>-1), value:"music"},
      { text: "ADDEVENT.ACT_PHOTO", checked: (this.newevent.activities.indexOf("photo")>-1),value:"photo"},
      { text: "ADDEVENT.ACT_MESSAGE", checked: (this.newevent.activities.indexOf("message")>-1),value:"message"},
      { text: "ADDEVENT.ACT_SONDAGE", checked: (this.newevent.activities.indexOf("survey")>-1),value:"survey"},
      { text: "ADDEVENT.ACT_COMMAND", checked: (this.newevent.activities.indexOf("command")>-1),value:"command"},
      { text: "ADDEVENT.ACT_BOARD", checked: (this.newevent.activities.indexOf("board")>-1),value:"board"},
      { text: "ADDEVENT.ACT_LOTERIE", checked: (this.newevent.activities.indexOf("loterie")>-1),value:"loterie"},
      { text: "ADDEVENT.ACT_BETS", checked:  (this.newevent.activities.indexOf("bets")>-1),value:"bets"},
      { text: "ADDEVENT.ACT_PRESENTATION", checked:  (this.newevent.activities.indexOf("presentation")>-1),value:"presentation"}
    ];
  this.refresh();
  }

  refresh(){
    var vm=this;
    this.newevent.activities=[];
    this.newevent.lst_activities.forEach(function(item){
      if(item.checked)vm.newevent.activities.push(item.value);
    })
    this.generateCreateEventLink();
  }

  save(){
    this.refresh();
    this.viewCtrl.dismiss(this.newevent);
  }

  selImage(){
    var vm=this;
    openModal(this.modalCtrl,ImageCreatorPage,{ratio:3/2,upload:true},rep => {
      if(rep!=null) {
        vm.newevent.welcomePicture = rep.value;
      }
    })
  }

  generateCreateEventLink() {
    this.api.urlshortener(DOMAIN_SERVER+"/magiklink?title="+encodeURIComponent(this.newevent.title)+
      "&activities="+this.newevent.activities.join(",")+"&flyer="+this.newevent.templateFlyer+"&noprompt=true&autologin=1").subscribe((res)=>{
      if(res.url==null)
        this.magiklink=res.long_url;
      else
        this.magiklink=res.url;
    });
  }

  onCopy() {
    onCopy(this.toastCtrl,this.translate);
  }

  setDefaultWidget() {
    openModal(this.modalCtrl,WidgetstorePage,{from:AddeventAdvancedPage.name},(w)=>{
      this.newevent.widgetDefault=w.name;
      toast(this.toastCtrl,"ADDEVENT.DEFAULTWIDGETSET",this.translate);
    });
  }
}



