import {AfterContentInit,  SimpleChanges, ViewChild} from '@angular/core';
import {Component, ElementRef,  Input, OnChanges} from '@angular/core';

/**
 * TODO : integrer https://davidwalsh.name/css-flip
 */
@Component({
  selector: 'shifuflyer',
  templateUrl: 'shifuflyer.html'
})
export class ShifuflyerComponent implements OnChanges,AfterContentInit {

  @Input("icon") icon:string="";
  @Input("flyer") flyer:string="";
  @Input("size") size:string="";
  @Input("closed") closed:boolean=false;
  @Input("reverse") reverse:boolean=false;
  @Input("autoreturn") autoreturn:number=0;
  @ViewChild("card") card:ElementRef;
  // @ViewChild("ref") ref:ElementRef;
  @ViewChild('flyerPaper') flyerPaper: ElementRef;
  // @Output('onflip') onflip:  EventEmitter<any>=new EventEmitter();

  showSVG:boolean=false;
  @Input("width") width="";
  @Input("height") height="";

  // side:number=0;

  constructor() {
    if(this.width.length==0)this.width=this.size;
  }


  flip(obj){
    if(this.reverse)
      obj.parentNode.classList.toggle('hover');
  }


  ngAfterContentInit(): void {
    setTimeout(()=>{
      if(this.card.nativeElement.clientWidth>0){
        this.height=(this.card.nativeElement.clientWidth*(3/2))+"px";
        this.width=this.card.nativeElement.clientWidth+"px";
      }
    },100);

  }


  ngOnChanges(changes: SimpleChanges): void {
    if(this.flyer!=undefined){
      if(this.flyer.indexOf("<?xml")==0){
        this.showSVG=true;
        this.flyerPaper.nativeElement.innerHTML=this.flyer;
      }
      else {
        if(this.flyer.indexOf("getfile?code")==-1)
          this.flyerPaper.nativeElement.innerHTML="<img style='position:absolute;top:0px;left:0px;height:100%;' src='"+this.flyer+"'>";
        else
          this.flyerPaper.nativeElement.innerHTML="<object height='100%' width='100%' type='image/svg+xml' data='"+this.flyer+"'>";

      }
    } else {
      this.flyerPaper.nativeElement.innerHTML="<br><br><br><div style='width:100%;text-align: center'><img src='./assets/img/wait.gif' style='width:50px'></div>";
    }
  }

}



