import {Component, ElementRef, ViewChild} from '@angular/core';
import {FabContainer, NavController, NavParams, ToastController} from 'ionic-angular';
import {AlertController, Events, IonicPage, ModalController} from 'ionic-angular';
import { Storage } from '@ionic/storage';
import {DELAY_TO_REFRESH, TYPE_BET, TYPE_QUESTION, TYPE_SONDAGE} from "../../app/Maintools";
import {showPopup, subscribe, toast} from "../../app/Maintools";
import {$$, getEnv, openModal, showConfirm} from "../../app/Maintools";
import {AddbetsPage} from "../addbets/addbets";
import {InvitePage} from "../invite/invite";
import {AddloteriePage} from "../addloterie/addloterie";
import {EventDataProvider} from "../../providers/event-data/event-data";
import {UserData} from "../../providers/user-data";
import {DomSanitizer} from "@angular/platform-browser";
import {TranslateService} from "@ngx-translate/core";

/**
 * Generated class for the BetsPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-bets',
  templateUrl: 'bets.html',
})
export class BetsPage {

  bets=[];
  lot={validation:false};

  bet={from:{}};
  loteries=[];
  env:number=0;
  now=new Date().getTime();

  end_loteries=[];
  loteries_forvalidation=[];

  bets_tovalidate=[];
  bets_toevaluate=[];
  @ViewChild("fab") fab:FabContainer;

  constructor (public events: Events,public modalCtrl:ModalController,public storage:Storage,
               public navCtrl: NavController,public navParams: NavParams,public toastCtrl:ToastController,
               public eventData:EventDataProvider,public userData:UserData,public translate:TranslateService,
               public sanitizer: DomSanitizer,public alertCtrl:AlertController) {
    this.env=getEnv();
  }

  ionViewDidLoad() {
    var vm=this;
    subscribe("bets",this,2000);

    this.storage.get("todo").then(r => {
      if(r=="survey"){
        this.newbet(5);
        this.storage.remove("todo");
      }
    });
  }

  ionViewDidEnter () {
    setTimeout(()=>{this.refresh(true);},DELAY_TO_REFRESH);
  }


  newbet(type){
    this.fab.close();
    var vm=this;
    //On vérifie si quelqu'un a des crédits
    this.eventData.getnuserswithscoin((rc)=>{
      if(rc<3 && type==4)
        toast(this.toastCtrl,"BETS.GIVESOMESCOINBEFORECREATEBETS",this.translate,null,"middle",5);

      var delay=(vm.eventData.event.dtEnd-new Date().getTime())/60000;
      if(delay>3) //On ne peut pas faire de pari quand il reste moins de 3 minutes
        openModal(this.modalCtrl,AddbetsPage,{type:type},function(){
          vm.refresh();
        });
      else
        toast(vm.toastCtrl,"BETS.NOTENOUGHTTIMETOBETS");

    });
  };

  newloterie(){
    var vm=this;
    this.eventData.getnuserswithscoin((rc)=>{
      if(rc==0)
        toast(this.toastCtrl,"BETS.GIVESOMESCOINBEFORECREATEBETS",this.translate);

      var delay=(vm.eventData.event.dtEnd-new Date().getTime())/60000;
      if(delay>3) //On ne peut pas faire de pari quand il reste moins de 3 minutes
        openModal(this.modalCtrl,AddloteriePage,{},()=>{
          vm.refresh();
        });
      else
        toast(vm.toastCtrl,"BETS.NOTENOUGHTTIMETOBETS");

    });
  };

  updateEvent(){
    this.eventData.updateevent("addBetOnlyByOwner,limitOptions,needValidate").subscribe(r=>{});
  }

  remove(bet){
    var vm=this;
    var index=vm.bets.indexOf(bet);
    vm.userData.removebet(bet.id).subscribe(function(resp){
      if(vm.userData.user.message.length==0)vm.bets.splice(index,1);
    });
  };

  close(bet){
    this.userData.closebet(bet.id).subscribe((resp:any) => {
     this.refresh(true);
    });
  };

  shareBet(bet=null){
      if(bet==null)
        openModal(this.modalCtrl,InvitePage,{from:this.userData.user,event:this.eventData.event});
      else
        openModal(this.modalCtrl,InvitePage,{from:this.userData.user,event:this.eventData.event,tab:"BetsPage",type:bet.type,message:bet.id});
  };

  getTotals(bet,id){
    bet.votants=[];
    for(var i=0;i<bet.options.length;i++)
      bet.options[i].totaluser=0;

    if(bet.hasOwnProperty("votes")){
      bet.votes.forEach(function(v){
        bet.votants.push(v.from);
        if(v.from===id){
          var index=Number(v.description);
          bet.options[index].totaluser+=v.value;
        }
      });
    }
  }

  buy(l){
    var vm=this;
    if(l.maxCredits==1) //Dans ce cas la on parle plutôt d'une inscription
      vm.userData.buyticket(l.id, 1).subscribe((resp:any)=>{
        l.hasTicket=(resp.message=="")
        if(resp.message=="")resp.message="LIB.SUBSCRIBE";
        toast(vm.toastCtrl,resp.message,vm.translate);
        vm.userData.user=resp;
      });
    else{
      showPopup({
          title:vm.translate.instant("BETS.LOTERIE")+Math.round(vm.userData.user.credit),
          value:Math.min(10,Math.round(vm.userData.user.credit)),
          confirmButton:vm.translate.instant("LIB.BUY"),
          cancelButton:vm.translate.instant("LIB.CANCEL"),
          type:"number"},vm.alertCtrl,

        function (res) {
          if (res > 0) {
            vm.userData.buyticket(l.id,res).subscribe((resp:any) => {
              vm.userData.user=resp;
              if(resp.message.length>0)
                toast(vm.toastCtrl,resp.message);
            })
          }
        });
    }
  };

  changeRespons(b,evt){
    if(evt.keyCode==13)this.sendRespons(b);
  }

  sendRespons(b:any){
    if(b.freetext!=undefined && b.freetext.length>0)
      this.eventData.sendRespons(b.id,this.userData.user.id,b.freetext).subscribe(()=>{
        this.refresh(true);
      })
  }

  mise(b,opt){
    var index=b.options.indexOf(opt);
    var vm=this;
    let v={from:vm.userData.user.id,value:1,description:index};
    if(b.type==TYPE_BET){
      v.value=0;
      showPopup({
          title:vm.translate.instant("BETS.MESSAGEMISE")+Math.round(vm.userData.user.jetons[vm.eventData.event.owner_id]),
          value:Math.min(10,Math.round(vm.userData.user.credit)),
          confirmButton:"LIB.BET",
          cancelButton:"LIB.CANCEL",
          type:"number",translate:vm.translate},vm.alertCtrl,

        function (res) {
          v.value= res;
          if(v.value>0){
            vm.userData.user.jetons[vm.eventData.event.owner_id] -= v.value;
            b.options[index].totaluser += v.value;
            vm.userData.sendvote(b.id,v).subscribe(function(resp){
              if(resp!=null)vm.userData.user=resp;
            });
          }
        }
      );
    }

    if(b.type==TYPE_SONDAGE && b.canVote==true){
      b.nRestVote--;
      b.options[index].total+= v.value;
      b.canVote= (b.nRestVote>0);
      vm.userData.sendvote(b.id,v).subscribe(function(resp) {
       if(resp!=null)vm.userData.user=resp;
      });
    }

  };


  validate(b){

    var vm=this;
    var index=0;
    for(index=0;index<b.options.length;index++){
      if(b.options[index].lib==b.result)break;
    }

    var i=0;
    if(b.result==-1){
      showConfirm(vm.translate.instant("BETS.CANCELCONFIRMATION"),vm.alertCtrl,this.translate,function(){
        //vm.bets_tovalidate.splice(index,1);
        vm.eventData.validebet(b.id, -1).subscribe(function(){
          vm.refresh(true);
        });
      },function(){
        //tuto(vm.user,"BETS.TUTO_VALIDATE");
      });
    }
    else{
      //vm.bets_tovalidate.splice(index,1);
      vm.eventData.validebet(b.id, index).subscribe(function(){
        vm.refresh(true);
      });
    }
  };


  /**
   *
   * @param bets
   */
  programTimeout(bets){
    var vm=this;
    var nextTimeout=null;
    bets.forEach(function(bet){
      if(bet.dtStart>new Date().getTime()){
        if(nextTimeout==null)
          nextTimeout=bet.dtStart;
        else
          nextTimeout=Math.min(bet.dtStart,nextTimeout);
      }
    });
    if(nextTimeout!=null){
      var delay=(nextTimeout-new Date().getTime());
      if(delay>0){
        $$("refresh dans "+delay/1000+" secondes");
        setTimeout(function(){
          vm.refresh(true,function(){
            vm.programTimeout(bets);
          });
        },delay+1000);
      }
    }
  };

  validateLoterie(lot){
    var vm=this;
    vm.userData.validate(lot.id).subscribe((resp:any)=>{
      if(resp.message==""){
        lot.validate=true;
        vm.refresh();
      }

    });
  };

  refresh(force=true,func=null){
    var vm=this;
    this.now=new Date().getTime();

    if(!force && this.userData.selectTab!=undefined && this.userData.selectTab!="BetsPage")return;


    /**
     * Chargement des loteries
     */

    vm.eventData.getloteries(vm.userData.user.id).subscribe((resp:any)=>{
      if(resp!=null){
        vm.loteries=[];
        vm.loteries_forvalidation=[];
        vm.end_loteries=[];

        resp.items.forEach(function(lot){

          //Ajoute la liste des participants
          lot.buyers=[];
          if(lot.tickets!=undefined){
            for(let id in lot.tickets){
              if(lot.buyers.indexOf(id)==-1)
                lot.buyers.push(id)
            }
          }

          if(!lot.available){ //Indique que la loterie est en cours de validation
              if(vm.userData.user.id==lot.from.id || vm.eventData.event.Moderators.indexOf(vm.userData.user.id)>-1)vm.loteries_forvalidation.push(lot);
          }else{
            if(lot.dtStart<=vm.now || lot.from.id==vm.userData.user.id){
              if(lot.dtEnd>vm.now){
                if(lot.maxCredits==1){
                  if(lot.hasOwnProperty("tickets")){
                    if(lot.tickets.hasOwnProperty(vm.userData.user.id))
                      lot.hasTicket=true; else lot.hasTicket=false;
                  }
                }
                else lot.hasTicket=false;
                vm.loteries.push(lot);
              }
              else{
                //TODO: a corriger pour les questions de fuseau
                var delay=(new Date().getTime()-lot.dtEnd)/1000;
                if(lot.status==2 && lot.validate==false && (delay<120 && lot.buyers.indexOf(vm.userData.user.id)>-1)){
                  vm.end_loteries.push(lot);
                }
              }
            }
          }
        });
      }
    });

    vm.eventData.getbets(vm.userData.getId(),false,false,"").subscribe((resp:any) => {
      vm.bets=[];
      vm.bets_tovalidate=[];
      vm.bets_toevaluate=[];

      var nVote=0;

      if(resp!=undefined){
        //vm.programTimeout(resp.items);
        for(var i=0;i<resp.items.length;i++){
          var b=resp.items[i];

          b.opened=(Number(b.status)>-1);
          b.closed=(Number(b.status)>=1);
          /**
           * Chargement des sondages
           */

          if(b.type==TYPE_SONDAGE || b.type==TYPE_QUESTION){
            if(b.options!=undefined && b.options.length>0)
              b.optionByImage=(b.options[0].lib.indexOf("http")==0);

            if(!b.closed) {
              b.canVote = true;
              b.priority = 0;
              nVote=0;
              if (b.hasOwnProperty("votes")){
                b.votes.forEach(function (v) {
                  if (v.from === vm.userData.user.id)nVote++;
                });
                if(nVote>=b.nVotes){
                  b.canVote = false;
                  b.priority = 1; //définie les priorités d'affichage
                }
              }
              b.nRestVote= b.nVotes-nVote;
              //Le moderateur peut voire le sondage / pari si la validation est nécessaire
              if(
                b.opened ||
                b.from.id==vm.userData.user.id ||
                (vm.eventData.event.needValidate && vm.eventData.event.Moderators.indexOf(vm.userData.user.id)>-1)
              ){
                vm.bets.push(b);
              }
            }
          }

          /**
           * Chargement des paris
           */
          if(b.type==TYPE_BET){
            var tot=[];
            vm.getTotals(b,vm.userData.user.id);

            b.options.forEach(function(opt){
              tot.push(opt.totaluser*opt.quot);
            });
            b.canVote=true;
            b.gainMax=Math.max.apply(null,tot);
            b.gainMin=Math.min.apply(null,tot);

            if(b.dtEnd>vm.now && (b.dtStart<vm.now || b.from.id==vm.userData.user.id)){
              vm.bets.push(b);
            }

            if (b.status==1 && (b.votants.indexOf(vm.userData.user.id) > -1 || b.from.id == vm.userData.user.id)) {
              $$("Pari à valider ", b);
              b.result = -1;
              vm.bets_tovalidate.push(b);
            }

            if(b.status==2){
              $$("Parieur à évaluer", b);
              var index = Number(b.text);

              if (index == -1)
                b.lib_response = vm.translate.instant("BETS.NORESPONSE");
              else
                b.lib_response = vm.translate.instant("BETS.RESPONSE") + " : " + b.options[index].lib;

              if ((!b.hasOwnProperty("reputations") || b.reputations.indexOf(vm.userData.user.id) == -1) && b.votants.indexOf(vm.userData.user.id) > -1)
                vm.bets_toevaluate.push(b);
            }
          }

          b.visibleCard=(this.bets.length==1 || (b.type==5 && b.nRestVote>0) || (b.type==4) || (b.type==9));
        }

        if(func!=undefined)func();
      }

    });
  };

  makeLoterieAvailable(lot,value){
    this.eventData.makeLoterieAvailable(lot.id,value).subscribe(r=>{
      this.refresh(true);
    });
  }

  start(bet){
    this.eventData.startbet(bet).subscribe(()=>{
      setTimeout(()=>{this.refresh(true);},2000);
    });
  }

  closeLoterie(lot: any) {
    this.userData.closebet(lot.id).subscribe(()=>{});
  }
}



