import { Component } from '@angular/core';
import {AlertController, Events, IonicPage, LoadingController, ToastController} from 'ionic-angular';
import * as Tools from "../../app/Maintools"
import {UserData} from "../../providers/user-data";
import {EventDataProvider} from "../../providers/event-data/event-data";
import {getDelay, loading, openModal, showPopup, toast} from "../../app/Maintools";
import {ModalController, NavController,NavParams} from 'ionic-angular';
import {InvitePage} from "../invite/invite";
import {PublicProfilPage} from "../public-profil/public-profil";
import {TranslateService} from "@ngx-translate/core";
import {WholikemePage} from "../wholikeme/wholikeme";
import {ApiProvider} from "../../providers/api/api";

/**
 * Generated class for the ChartsPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-charts',
  templateUrl: 'charts.html',
})
export class ChartsPage {
  user:any={connexions:[]};
  orderby:any={critere:"scoreEvent"};
  users:any[]=[];
  invites:any[]=[];
  delay:string="";
  target:any=null;

  constructor( public events: Events,public navCtrl: NavController, public navParams: NavParams,
               public translate:TranslateService,public loadingCtrl:LoadingController,public toastCtrl:ToastController,
               public modalCtrl:ModalController,public alertCtrl: AlertController,public api:ApiProvider,
               public eventData:EventDataProvider,public userData:UserData) {
  }

  update(critere,index){
    this.orderby=critere;
  }


  showUser(person){
    openModal(this.modalCtrl,PublicProfilPage,{userid:person.id,follower:this.userData.user.id});
  }

  changeEvent(){
    this.eventData.updateevent("maxOnline").subscribe((rep) => {});
  }


  ionViewDidLoad() {
    Tools.subscribe("charts",this);
    if(this.navParams.get("message")!=undefined)
      toast(this.toastCtrl,this.navParams.get("message"),this.translate);
  }

  ionViewDidEnter () {
    setTimeout(()=>{this.refresh(true);},500);
  }

  openInvite(){
    let delay:number=Tools.getDelayInMinutes(this.eventData.event.dtEnd);
    if(delay>10)
      openModal(this.modalCtrl,InvitePage,{from:this.userData.user,event:this.eventData.event});
    else
      toast(this.toastCtrl,"ERROR.NOTENOUGHTTIME",this.translate);
  }

  setTarget(u){
    this.target=u;
    this.refresh();
  }

  clearTarget(){
    this.target=null;
    this.refresh();
  }

  openWholikeme(target){
    openModal(this.modalCtrl,WholikemePage,{target:target});
  }

  refresh(force:boolean=false){
    if(!force && this.userData.selectTab!=undefined && this.userData.selectTab!="ChartsPage")return;

    this.delay=Tools.getDelay(this.eventData.event.dtEnd);

    var id="";
    if(this.target!=null)id=this.target.id;

    this.eventData.getinvites().subscribe((r:any) => {this.invites=r;});

    this.eventData.getpresents(true,id).subscribe((resp:any) => {
      //this.users=$filter("orderBy")(resp.result.items,$scope.orderby.critere,true);
      if(resp!=null){
        this.users=[];
        resp.items.forEach(it=>{
          it.total=it.nbDislike-it.nbLike; //Permet le classement inversé
          this.users.push(it);
        })
      }
    });
  };

  GiveToAll(){
    showPopup({title:"CHARTS.GIVETOKENTOALLTITLE",placeholder:"LIB.AMOUNT",confirmButton:"LIB.OK",cancelButton:"LIB.CANCEL",type:"number",translate:this.translate},this.alertCtrl,(res)=>{
      if(res!=null)
        this.api.chargecredits(this.userData.user.id,this.eventData.event.id,res).subscribe(r=>{});
    });
  }

  GiveToNewcomer(){
    showPopup({title:"CHARTS.GIVETONEWCOMERTITLE",placeholder:"LIB.AMOUNT",confirmButton:"LIB.OK",cancelButton:"LIB.CANCEL",type:"number",translate:this.translate},this.alertCtrl,(res)=>{
      if(res!=null){
        this.eventData.event.startJetons=res;
        this.eventData.updateevent("startJetons").subscribe(r=>{});
      }
    });
  }

}



