import {Component, Input, OnInit} from '@angular/core';
import {getDate} from "../../app/Maintools";

/**
 * Generated class for the ShifudateComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
  selector: 'shifudate',
  templateUrl: 'shifudate.html'
})
export class ShifudateComponent implements OnInit {

  @Input("lang") lang: string="fr-FR";
  @Input("value") dt: number=0;
  @Input("onlyHour") onlyHour:boolean=false;
  text:string="";

  constructor() {

  }

  ngOnInit(): void {
    this.text=getDate(this.dt,this.lang,this.onlyHour);
  }

}



