import { Component } from '@angular/core';
import {ModalController, NavController, NavParams, ToastController, ViewController} from 'ionic-angular';
import { AlertController, Events, IonicPage} from 'ionic-angular';
import {EventDataProvider} from "../../providers/event-data/event-data";
import {openModal, showPopup, subscribe, toast} from "../../app/Maintools";
import {ImageCreatorPage} from "../image-creator/image-creator";
import {UserData} from "../../providers/user-data";
import {InvitePage} from "../invite/invite";
import {TranslateService} from "@ngx-translate/core";
import {ApiProvider} from "../../providers/api/api";

/**
 * Generated class for the PreparecommandPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-preparecommand',
  templateUrl: 'preparecommand.html',
})
export class PreparecommandPage {
  commands:any[]=[];
  command:any={items:[]};
  user:any={};

  constructor(public viewCtrl:ViewController,public api:ApiProvider,public translate:TranslateService,public alertCtrl:AlertController,public toastCtrl:ToastController,public modalCtrl:ModalController,public userData:UserData,public events: Events,public navCtrl: NavController, public navParams: NavParams,public eventData:EventDataProvider) {
    var vm=this;
  }

  ionViewDidLoad(){
    subscribe("command",this);
    this.refresh();
  }

  updateCommand(command,status){
    this.eventData.updatecommand(command.id,status).subscribe((resp:any) => {this.refresh();});
  };


  refresh(){
    var vm=this;
    this.eventData.getcommands().subscribe((resp:any) => {
      vm.commands=[];
      resp.items.forEach(function(r){
        for(var i=0;i<r.items.length;i++){
          r.items[i].x=r.items[i].x+"%";
          r.items[i].y=r.items[i].y+"%";
          r.order=i;
        }
        vm.commands.push(r);
      });

    });
  };

  generateTableQRCode(){
    var vm=this;
    openModal(this.modalCtrl,InvitePage,{dest:"command"},function(){
      toast(vm.toastCtrl,"LIB.INVITE");
    });
  }


  updateEvent(){
    this.eventData.updateevent("laCarte,selfservice,loc_query").subscribe((rep) => {});
  }

  uploadLaCarte(){
    openModal(this.modalCtrl,ImageCreatorPage,{upload:true,ratio:13/9,autocrop:true,maxsize:800},res => {
      if(res!=null){
        this.eventData.event.laCarte=res.value;
        this.eventData.event.activities.push("command");
        this.eventData.updateevent("laCarte,activities").subscribe(r=>{});
      }
    });
  }

  pay(command){
    var vm=this;
    showPopup({title: "LIB.COST",confirmButton: "LIB.OK",cancelButton: "LIB.CANCEL",type: "number",translate:this.translate},this.alertCtrl,
      (res) => {
        if(res!=null){
          res=Math.abs(res);
          vm.api.chargecredits(vm.eventData.event.owner_id,command.from.id,-res).subscribe(r=>{
            vm.refresh();
            vm.updateCommand(command,5);
          });
        }

      }
    );
  }

}



