import {IonicPage, LoadingController, ToastController, ViewController} from 'ionic-angular';
import {Component} from '@angular/core';
import * as Tools from "../../app/Maintools"
import {UserData} from "../../providers/user-data";
import {$$, DOMAIN_APPLI, DOMAIN_SERVER, loading, onCopy, openGeneral} from "../../app/Maintools";
import {TranslateService} from "@ngx-translate/core";
import {ApiProvider} from "../../providers/api/api";


/**
 * Generated class for the AddeventModelePage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-addevent-modele',
  templateUrl: 'addevent-modele.html',
})
export class AddeventModelePage {

  facebook_events=[];
  google_events=[];
  myevents=[];
  shifumix_template=[];
  typeModel="shifumix_template";


  constructor(public viewCtrl: ViewController,
              public userData:UserData,public api:ApiProvider,
              public loadingCtrl:LoadingController,
              public toastCtrl:ToastController,
              public translate:TranslateService) {
    this.refresh();
  }

  refresh(){
    //Pas de récupération pour l'instant des événements facebook suite a mise a jour côté facebook
    this.userData.getFacebookPageAndEvents().subscribe((events:any) => {
      this.facebook_events=events.items;
    });

    this.userData.geteventsfrom(true,20).subscribe((resp:any) => {
      this.myevents=[];
      resp.items.forEach((evt)=>{
        evt.magiklink=DOMAIN_SERVER+"/magiklink?event_model="+evt.id;
        this.myevents.push(evt);
      });
    });

    $$("Récupération des modeles Shifumix")
    this.api.gettemplates().subscribe((res:any)=>{
      if(res!=null){
        this.shifumix_template=[];
        res.templates.forEach((evt)=>{
          if(evt.ihm=="pro")evt.type="pro"; else evt.type="perso";
          if(evt.templateFlyer==null)evt.templateFlyer=evt.flyer;
          if(evt.templateTitle==null)evt.templateTitle=evt.title;
          if(evt.templateDescription==null)evt.templateDescription=evt.title;

          if(evt.templateFlyer!=null){
            evt.title=evt.title.replace("%from",this.userData.user.firstname);
            if(this.userData.user.address!=undefined)evt.address=evt.address.replace("%address",this.userData.user.address);
            evt.type="basic";
            this.shifumix_template.push(evt);
          }
        });
      }
    });
  }

  connectTo(service){
    Tools.openGeneral(service,this.userData.user).then(()=>{this.refresh();})
  }

  openFacebookEvents(){Tools.openWindow("https://www.facebook.com/events/discovery/");};
  openFacebookPages(){Tools.openWindow("https://www.facebook.com/pages/discovery/");};

  showFacebookEvents(events){
    this.facebook_events=events;
  };

  selectModele(modele){
    if(modele.type=="basic")
      this.viewCtrl.dismiss(modele);
    else{
      this.userData.initevent(modele).subscribe((resp:any) => {
        resp.option={title:true,teaser:true,address:resp.address!="",date:true};
        resp.type="oldevent";
        this.viewCtrl.dismiss(resp); //Retourne l'evenement modele
      });
    }
  };

  close(){
    this.viewCtrl.dismiss();
  }

  onCopy() {
    onCopy(this.toastCtrl,this.translate);
  }

}



