import {Component, ElementRef, EventEmitter, Input, Output, ViewChild, OnInit} from '@angular/core';
import {TranslateService} from "@ngx-translate/core";

/**
 * Generated class for the ShifucameraComponent component.
 *
 * see https://angular-2-training-book.rangle.io/handout/directives/ng_style_directive.html
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
  selector: 'shifucamera',
  templateUrl: 'shifucamera.html'
})
export class ShifucameraComponent {

  @Input("sizeiconwidth") sizeiconwidth:string="30px";
  @Input("sizeiconheight") sizeiconheight:string="30px";
  @Input("icon") icon:string="";
  @Input("tips") tips:string="";
  @Input("onlyfile") onlyFile:boolean=false;
  @Input("photo") photo:string="";
  @Input("size") size:string="";
  @Input("button") button:string="";
  @Output('onTake') onTake: EventEmitter<any>=new EventEmitter();
  @ViewChild('inputElement') inputElement: ElementRef;

  constructor(public translate:TranslateService) {
    if(!this.sizeiconheight.endsWith("px"))this.sizeiconheight+="px";
    if(!this.sizeiconwidth.endsWith("px"))this.sizeiconwidth+="px";
    if(this.button.indexOf(".")>-1)this.button=this.translate.instant(this.button);
  }

  onChange(event){
    var vm=this;
    var file=event.target.files[0];
    var reader=new FileReader();
    reader.onload=function(){
      vm.onTake.emit({value:this.result});
    }
    reader.readAsDataURL(file);
  }


  takePhoto(){
    this.inputElement.nativeElement.click();
  }

}



