import {Component, DoCheck, ElementRef, ViewChild} from '@angular/core';
import { Geolocation } from '@ionic-native/geolocation';
import {ModalController, NavController, NavParams,ToastController} from 'ionic-angular';
import {AlertController, Events, IonicPage, LoadingController} from 'ionic-angular';
import {AddeventPage} from "../addevent/addevent";
import {PersoPage} from "../perso/perso";
import * as Tools from "../../app/Maintools"
import {UserData} from "../../providers/user-data";
import { Storage } from '@ionic/storage';
import { DomSanitizer} from '@angular/platform-browser';
import {PublicProfilPage} from "../public-profil/public-profil";
import {$$, openModal, openWindow, showPopup, toast} from "../../app/Maintools";
import {ApiProvider} from "../../providers/api/api";
import {InvitePage} from "../invite/invite";
import {TranslateService} from "@ngx-translate/core";

declare var google: any;
/**
 * Generated class for the SeleventPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-selevent',
  templateUrl: 'selevent.html',
})
export class SeleventPage {

  target:any=null;
  eventsonmap:any[]=[];
  @ViewChild('mapCanvas') mapElement: ElementRef;
  urlInvitation: string;
  markers: any[]=[];
  circles: any[]=[];
  event_code:string=null;
  preview: any={title:""};
  location:any={};
  map:any=null;
  bestforme:any[]=[];
  handle=null;
  laCarte=null;
  handle2=null;
  message:string="";
  from:string="";
  todo=null;
  lastrefresh:number=0;
  dtCGU:number=0;

  constructor(public navCtrl: NavController, public navParams: NavParams,public events:Events,public toastCtrl:ToastController,
              public alertCtrl: AlertController,public api:ApiProvider,public translate:TranslateService,
              public storage:Storage,public sanitizer: DomSanitizer,public modalCtrl:ModalController,public loadingCtrl:LoadingController,
              public userData:UserData,public geolocation: Geolocation) {

    var vm=this;

    this.storage.get("todo").then(r=>{
      vm.todo=r;
      this.storage.get("location_lat").then(r=>{
        vm.location.lat=r;
        this.storage.get("location_lng").then(r=> {
          vm.location.lng = r;
          this.storage.get("location_zoom").then(r => {
            vm.location.zoom = r;
            this.storage.get("message").then(r => {
              vm.message = r;
              this.storage.get("from").then(r => {
                vm.from = r;
                this.storage.remove("location_lat");
                this.storage.remove("location_lng");
                this.storage.remove("location_zoom");
                this.storage.remove("message");
                this.storage.remove("from");
              });
            });
          });
        });
      });
    });
  }


  //Lancement au téléchargement
  ionViewDidEnter(){
    var vm=this;
    //On vérifie qu'on ne doit pas se positionner directement sur une zone contenu dans une invitation
    if(vm.location.lat!=undefined){
      vm.initMap(Number(vm.location.lat),Number(vm.location.lng),Number(vm.location.zoom),()=>{
        vm.initTarget();
        vm.location={};
        vm.showEventsOnMap();
      });
    }else{
      setTimeout(()=>{
        if(this.userData.user.email!=null && this.userData.user.email.length>0 && this.userData.user.acceptLocalize){
          vm.localize(vm.geolocation).then((zoom:number) => {
            if(vm.todo=="survey")vm.addEvent("survey");

            $$("Evaluation du zoom : "+zoom);
            if(zoom>-1){
              vm.initMap(vm.userData.user.lat,vm.userData.user.lng,zoom,()=>{
                vm.initTarget();
                vm.showEventsOnMap();
              });
            } else
              Tools.$$("Aucun événement proche, donc pas de carte");
          },(err)=>{
            vm.initMap(vm.userData.user.lat,vm.userData.user.lng,8,()=>{
              vm.initTarget();
              vm.showEventsOnMap();
            });
          });
        }
      },1000);
    }
  }


  refreshPreview(): void {
    this.preview["address"]=this.preview["address"]+" ";
  }

  inviteEvent(evt){
    openModal(this.modalCtrl,InvitePage,{event:evt});
  }



  initMap(lat:number,lng:number,zoom:number,func:Function){
    var vm=this;

    if(this.userData.user.email.length==0){//on rejete les utilisateurs anonymes
      func();return;
    }

    if(vm.map==null)
      vm.map = new google.maps.Map(vm.mapElement.nativeElement, {center: {lat:lat,lng:lng} ,zoom: zoom});

    //Initialisation des événements de la carte
    vm.map.addListener("center_changed",function(evt){
      vm.initTarget();
      setTimeout(function(){vm.showEventsOnMap();},500);
    });

    vm.map.addListener("zoom_changed",function(evt){
      vm.showEventsOnMap();
    });

    vm.map.setOptions({
      disableDefaultUI:true,
      rotateControl:false,
      clickableIcons:false
    });

    setTimeout(func,500);
  }


  initTarget(force:boolean=false,func:Function=null){
    var vm=this;
    if(vm.map!=null){
      var pos=vm.map.getCenter();
      if(vm.target!=null && !force){
        vm.target.setPosition(pos);
        if(func!=null)func();
      }else{
        setTimeout(()=>{
          vm.target = new google.maps.Marker({
            user: vm.userData.user,
            position: pos,
            icon:"./assets/img/me.png",
            name: "You",
            draggable: true,
            map:vm.map
          });
          if(func!=null)func();
        },500);
      }
    }
    else {
      $$("La carte est null");
      if(func!=null)func();
    }
  }

  openTrajet(preview){
    var mypos=this.userData.user.lat+","+this.userData.user.lng;
    openWindow("https://www.google.com/maps/dir/'"+mypos+"'/'"+preview.lat+","+preview.lng+"'");
  }

  showLaCarte(evt){
    this.laCarte=evt.laCarte;
  }

  logout_shifumix () {
    this.storage.remove("login");
    this.storage.remove("user");
    this.storage.remove("login_email");
    this.storage.remove("login_password");
    this.userData.logout();
    this.clearMap();
    this.navCtrl.pop();
  };


  openprofil(){
    this.navCtrl.push(PersoPage,{user:this.userData.user});
  };


  calcZoom(lat,lng) {
    return new Promise((resolve) => {
      let zoom:number;
      this.api.closestevents(lat, lng).subscribe((events)=>{
        zoom = 18;
        if (events.items.length == 0)
          zoom = -1;
        else {
          var distanceMin = events.items[0].distanceInMeterFrom;
          zoom=8;
          //TODO: calcul du zoom en fonction de la distance à refaire car ça n'est pas proportionnel
          // if (distanceMin == 0)
          //   zoom = 15;
          // else
          //   zoom = Math.round(700000 / distanceMin);

          if (zoom < 3) zoom = 3;
          if (this.userData.user.precision > 10000 && zoom < 8) zoom = 8;
          if (zoom > 40) zoom = -1;
        }
        resolve(zoom);
      });
    });
  }


  async localize(geolocation:any):Promise<number> {
    $$("Déclenchement de la localisation");
    var vm = this;

    if(vm.userData.user.lat==undefined)vm.userData.user.lat=48;
    if(vm.userData.user.lng==undefined)vm.userData.user.lng=2;
    vm.userData.user.precision = 100000;

    let zoom;
    if (geolocation == null) geolocation = navigator.geolocation;

    if (geolocation) {
      try {
        let position = await geolocation.getCurrentPosition({
            enableHighAccuracy:true,
            timeout:15*1000,
            maximumAge:60*60*1000
        });

        $$("Géolocalisation ok",position.coords);
        vm.userData.user.lat = position.coords.latitude;
        vm.userData.user.lng = position.coords.longitude;
        vm.userData.user["dtLastPosition"] = position.timestamp;
        vm.userData.user["precision"] = position.coords.accuracy;

        zoom=await vm.calcZoom(position.coords.latitude, position.coords.longitude);
        if(zoom!=-1)zoom=15;

      } catch (error) {
          $$("Probleme de géolocalisation",error);
        if(error.code==1)  {
              $$("L'utilisateur refuse de donner sa position");
            this.confirmLocalize(false);
            zoom=-1;
        } else
          zoom=await vm.calcZoom(vm.userData.user.lat, vm.userData.user.lng);
      }
    } else {
      $$("Pas de géolocalisation disponible");
      this.confirmLocalize(false);
      zoom=await vm.calcZoom(vm.userData.user.lat, vm.userData.user.lng);
    }
    return zoom;
  }


  onEnterCode(code:any){
    if(code.value!="")
      this.userData.findeventbycode(code.value).subscribe(evt => {
        if(evt!=null)
          this.joinEvent(evt,"code");
        else{
          if(code.value!=null && code.value.length>0)
            toast(this.toastCtrl,"OLDEVENTS.NOEVENTFORTHISCODE",this.translate);
        }
      })
  }

  joinEventByCode(){
    showPopup({title:"OLDEVENTS.EVENTCODETITLE",placeholder:"000000",confirmButton:"LIB.OK",cancelButton:"LIB.CANCEL",type:"number",translate:this.translate},this.alertCtrl,(res)=>{
      this.onEnterCode({value:res});
    });
  }


  centerOnLocation(){
    var vm=this;
    //si la distance minimum  à l'evenement est elevée on désactive la localisation
    vm.localize(this.geolocation).then((zoom) => {
      vm.map.setCenter({lat:vm.userData.user.lat,lng:vm.userData.user.lng});
      if(vm.map.getZoom()!=zoom)vm.map.setZoom(zoom);
    });
  };



  openPublicProfil(id:string){
    Tools.openModal(this.modalCtrl,PublicProfilPage,{userid:id,follower:this.userData.user.id});
  }


  /**
   * permet de rejoindre un événement par le carte ou par code (type_invite)
   * @param evt
   * @param {string} type_invite
   * @returns {boolean}
   */
  joinEvent(evt:any,type_invite:string="map"){
    if(evt==undefined)return false;
    if(evt.hasOwnProperty("password") && evt.password.length>0 && evt.owner_id!=this.userData.user.id)
      Tools.showPopup({
          title:"SELEVENT.PRIVATE",
          placeholder:"password",
          confirmButton:"LIB.OK",
          cancelButton:"LIB.CANCEL",
          type:"text",translate:this.translate},this.alertCtrl,
        (password)=>{
          this.events.publish("event:login",{user:this.userData.user,event:evt,password:password,type_invite:type_invite});
        });
    else
      this.events.publish("event:login",{user:this.userData.user,event:evt,type_invite:type_invite});
  };


  clearMap(){
    var vm=this;
    if(vm.markers!=undefined && vm.markers.length>0){
      vm.markers.forEach(function(marker){
        marker.setMap(null);
      });
      vm.circles.forEach(function(circle){circle.setMap(null);});
      vm.markers=[];
      vm.circles=[];
    }
  };


  showEventsOnMap(pos:any=null,func:Function=null){
    var vm=this;

    if(vm.map!=null && vm.map.getBounds()!=undefined){
      if(pos!=null){
        vm.map.setCenter(pos);
      }

      //Eviter les appels redondants
      var now=new Date().getTime();
      if(now-vm.lastrefresh<3000)return;
      vm.lastrefresh=now;

      vm. urlInvitation=Tools.DOMAIN_APPLI+"/index.html?location_lat="+vm.map.getCenter().lat()+"&location_lng="+vm.map.getCenter().lng()+"&location_zoom="+vm.map.getZoom();

      var p2=vm.map.getBounds().getNorthEast();
      var p1=vm.map.getBounds().getSouthWest();

      //On considere que l'utilisateur se déplace avec la carte
      this.userData.user.lat=vm.map.getCenter().lat();
      this.userData.user.lng=vm.map.getCenter().lng();
      this.userData.senduser("position").subscribe(()=>{});


      this.api.loadinsquare(this.userData.user.id,p1.lat(),p1.lng(),p2.lat(),p2.lng()).subscribe((resp:any) => {
        vm.clearMap();
        vm.eventsonmap=[];
        if(resp!=null && resp.items!=undefined){

          Tools.$$("ShowEventsOnMap:"+resp.items.length+" items");

          resp.items.forEach(function(evt){
            //Pour chaque événement on calcul sa distance à l'utilisateur
            if(vm.userData.user.lat!=null && vm.userData.user.lat!=undefined){
              evt["distance"]=1000*Tools.distance(vm.userData.user.lat,vm.userData.user.lng,evt.lat,evt.lng);
            }

            var icon="./assets/img/party.png";
            evt.delay="";
            if(evt.dtStart>new Date().getTime()){
              icon="./assets/img/invitation.png";
              evt.delay=Tools.getDelay(evt.dtStart,vm.userData.user.lang);
            }

            if(vm.markers==undefined)vm.markers=[];

            if(evt.visibleOnMap || evt.owner_id==vm.userData.user.id){
              //Positionnement de chaque événement
              vm.markers.push(new google.maps.Marker({
                position: {lat:evt.lat,lng:evt.lng},
                user: vm.userData.user,
                title : evt.title,
                caption:evt.title,
                evt: evt,
                icon: icon,
                map: vm.map,
                id:evt.Id,
                max_size:new google.maps.Size(30,30)
              }));

              var size=0;
              if(evt.hasOwnProperty("presents"))size=evt.presents.length;
              if(size>0)
                vm.circles.push(new google.maps.Circle({
                  center: {lat:evt.lat,lng:evt.lng},
                  map: vm.map,
                  strokeColor: '#FF0000',
                  strokeOpacity: 0.1,
                  fillColor: '#FF0000',
                  fillOpacity: 0.1,
                  radius: size*10
                }));

              vm.markers[vm.markers.length-1].addListener("dblclick",function(){
                vm.joinEvent(this.evt,"map");
              });

              if(evt.password.length>0) evt.icon="key"; else evt.icon="";
              vm.eventsonmap.push(evt);
            }
          });
        } else {
          vm.eventsonmap=[];
        }
      });
    }

    setTimeout(()=>{
      this.userData.bestEventsForMe().subscribe((bests:any) => {
        vm.bestforme = [];
        if (bests != null){
          bests.items.forEach((it)=>{
              var result=true;
              vm.eventsonmap.forEach((e)=>{
                if(result && e.id==it.id)result=false;
              });
              if(result)vm.bestforme.push(it);
          });
        }
      });
    },2000);

    clearTimeout(this.handle);
    this.handle=setTimeout(function(){
      if(vm.navCtrl.getActive().name=="SeleventPage")vm.showEventsOnMap();
    },1000*60*15); //Refresh toutes les 30 secondes

    if(func!=null)func();
  };


  showEventForMe() {
    var vm=this;
    this.userData.bestEventsForMe().subscribe((r: any) => {
      if (r != null && r.items.length>0){
        let zone=new google.maps.LatLngBounds();
        //calcul du square pour representé les événements interessant
        if(r.items.length==1){
          vm.map.setCenter({lat:r.items[0].lat,lng:r.items[0].lng});
        } else {
          r.items.forEach((item)=>{
            zone.extend({lat:item.lat,lng:item.lng});
          });
          vm.map.fitBounds(zone);
          setTimeout(()=>{
            vm.showEventsOnMap()
          },500);
        }
      }
    });
  }


  zoomOut(){
    this.map.setZoom(this.map.getZoom()-2);
  }



  /**
   * Ajoute un événement
   * @param {string} todo
   */
  addEvent(todo:string=""){
    var vm=this;
    var pos={lat:this.userData.user.lat,lng:this.userData.user.lng};

    var zoom=8;
    if(this.userData.user.precision<10000)zoom=16; //Si la géolocalisation a fonctionnée

    if(this.map!=null){
      pos={lat:this.map.getCenter().lat(),lng:this.map.getCenter().lng()};
      zoom=this.map.getZoom();
    }

    Tools.openModal(this.modalCtrl,AddeventPage,
      {
        todo:todo,
        user:this.userData.user,
        precision:this.userData.user.precision,
        lat:pos.lat,
        lng:pos.lng,
        zoom:zoom
        },(evt) => {
      //if(vm.todo!="survey")vm.ionViewWillEnter();
      if (evt != undefined) {
        if(vm.todo=="survey"){
          vm.events.publish("event:login");
          return;
        } else {
          if(this.userData.user.email.length==0 || evt.visibleOnMap==false){
            this.events.publish("oldevents:open");
            //this.navCtrl.setRoot(OldeventsPage);
          }else{
            zoom=18;
            vm.initMap(evt.lat, evt.lng, zoom, () => {
              vm.initTarget(false, () => {
                setTimeout(() => {
                  vm.showEventsOnMap({lat: evt.lat, lng: evt.lng}, () => {
                  });
                }, 1000);
              });
            });
          }
        }
      }
    });
  }

  showEvent(evt){
    this.preview=evt;
  }

  shareZone(){
    openModal(this.modalCtrl,InvitePage,{url:this.urlInvitation,from:this.userData.user,tab:"home"});
  }

  centerOnEvent(lt,lg){
    if(this.target==null)this.initTarget(false,()=>{
      if(this.target=null)this.target.setPosition({lat:lt,lng:lg});
    });
  }

  confirmLocalize(rep:boolean){
    this.userData.user.acceptLocalize=rep;
    this.userData.senduser("acceptLocalize").subscribe(()=>{});
    this.ionViewDidEnter();
  }
}



