import { Component } from '@angular/core';
import {NavController,NavParams, ToastController, ViewController} from 'ionic-angular';
import {AlertController, Events, IonicPage, LoadingController, ModalController} from 'ionic-angular';
import {ApiProvider} from "../../providers/api/api";
import {EventDataProvider} from "../../providers/event-data/event-data";
import {TranslateService} from "@ngx-translate/core";
import {UserData} from "../../providers/user-data";
import {openWindow, showPopup, subscribe, toast, TYPE_MESSAGE} from "../../app/Maintools";
import {$$, DELAY_TO_REFRESH, getDate, loading, openModal} from "../../app/Maintools";
import {AddpresentationPage} from "../addpresentation/addpresentation";
import {SettingsProvider} from "../../providers/settings/settings";
import {RemotePage} from "../remote/remote";
import {AddbetsPage} from "../addbets/addbets";
import {b} from "../../../node_modules/@angular/core/src/render3";

/**
 * Generated class for the PresentationsPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-presentations',
  templateUrl: 'presentations.html',
})
export class PresentationsPage {

  presentations:any[]=[];

  constructor(
    public events: Events,
    public navCtrl: NavController,
    public navParams: NavParams,
    public loadingCtrl:LoadingController,
    public api:ApiProvider,
    public alertCtrl: AlertController,
    public modalCtrl:ModalController,
    public toastCtrl:ToastController,
    public eventData:EventDataProvider,
    public userData:UserData,
    public settings:SettingsProvider,
    public viewCtrl:ViewController,
    public translate:TranslateService
  ) {
  }

  refresh(){
      if(!this.settings.mustRefresh(PresentationsPage.name))return;
      //if(this.userData.selectTab!=undefined && this.userData.selectTab!=PresentationsPage.name)return;

      $$("Récupération des presentations");
      this.eventData.getpresentations().subscribe((res)=>{
        var b=false;
        for(var i:number=0;i<this.presentations.length;i++)
          if(res.items[i].id!=this.presentations[i].id)b=true;

        if(res.items.length!=this.presentations.length || b)this.presentations=[];

        $$("Appel du refresh",res);
        var bAlreadyActivate=false;
        res.items.forEach((p)=>{
          if(p.active && !p.ended)bAlreadyActivate=true;

          //Gere la possibilité de voter
          p.canVote=true;
          if(p.votes!=null){
            p.votes.forEach((v)=>{
              if(v.from==this.userData.user.id)p.canVote=false;
            });
          }


          p.title_complete=getDate(p.dtStart,"FR-fr",true)+" - "+p.title;
          if(!p.ended){
            if(p.pages!=null)p.title_complete+=" ("+p.page+"/"+p.pages.length+")";
          }
          else
              p.title_complete+=" - "+this.translate.instant("LIB.TERMINATED");

          if(!bAlreadyActivate && !p.ended && !p.inTreatment && !p.active && this.eventData.event.Moderators.indexOf(this.userData.user.id)>-1){
            bAlreadyActivate=true;
            p.canActivate=true;
          }

          for(var i:number=0;i<this.presentations.length;i++){
              if(this.presentations[i].id==p.id){
                  this.presentations[i].active=p.active;
                  this.presentations[i].ended=p.ended;
                  this.presentations[i].inTreatment=p.inTreatment;
                  this.presentations[i].canActivate=p.canActivate;
                  this.presentations[i].page=p.page;
                  this.presentations[i].dtEnd=Number(p.dtEnd);
                  this.presentations[i].dtStart=Number(p.dtStart);
                  this.presentations[i].votes=p.votes;
                  this.presentations[i].title_complete=p.title_complete;
                  break;
              }
          }
          if(i>=this.presentations.length)this.presentations.push(p);
        });
      });
  }

  ionViewDidEnter(){
    $$("ionViewDidEnter");
    setTimeout(()=>{this.refresh();},DELAY_TO_REFRESH);
  }

  ionViewDidLoad() {
    $$("ionViewDidLoad");
    subscribe("presentation",this);
  }

  addPresentation(evt) {
    openModal(this.modalCtrl,AddpresentationPage,null,(res)=>{
      if(res!=null){
        var message="Presentation upload";
        if(res.mega>10)message=message+" ... it will take more than one minute";
        var l=loading(this,message);
        this.eventData.sendpresentation(res).subscribe((res)=>{
            if(res==null)
                toast(this.toastCtrl,"ERROR.PRESENTATIONUPLOAD",this.translate);
            l.dismiss();
        });
      }
    });
  }

  setCurrentPresentation(pres: any,bStart) {
    this.eventData.activepresentation(pres,bStart).subscribe((rep:any)=>{
      if(rep.code!=200)
        toast(this.toastCtrl,rep.message,this.translate);
    });
  }

  removePres(pres: any) {
    this.eventData.delpresentation(pres.id).subscribe(()=>{});
  }

  download(pres:any){
    openWindow(pres.support,"download");
  }


  updateEvent(){
    this.eventData.updateevent("maxPresDuration,pricePerMinute,priceToPresent,canDownloadBeforePresentation,delayBetweenPresentationInMinutes").subscribe(r=>{});
  }

  /**
   * Modifie les priorités des présentation (donc l'ordre de passage)
   * @param pres
   * @param {Number} sens
   */
  changePriority(pres: any, sens: Number) {
    pres.priority=pres.priority+sens;
    this.eventData.sendpresentation(pres).subscribe(()=>{
      toast(this.toastCtrl,"Priorité modifiée");
      this.presentations=[];
    });
  }

  onSetScore(step: Number, pres:any ) {
    pres.canVote=false;
    var vote={target:pres.id,event:this.eventData.event.id,from:this.userData.user.id,value:step,description:""};
    this.userData.sendvote(pres.id,vote).subscribe(()=>{
      toast(this.toastCtrl,"Vote enregistré");
    })
  }

  changeDuration(pres: any) {
    showPopup({
        title:"Nouvelle durée (en minutes)",
        placeholder:"",
        confirmButton:"LIB.OK",
        cancelButton:"LIB.CANCEL",
        type:"number",
        translate:this.translate
    },this.alertCtrl,(res)=>{
      res=Number(res);
      if(res!=null && res<600 && res>0){
        pres.dtEnd=Number(pres.dtStart)+res*1000*60;
        this.eventData.sendpresentation(pres).subscribe(()=>{
          toast(this.toastCtrl,"Durée modifiée");
          this.presentations=[];
        });
      }
    });
  }

    openRemote(pres:any) {
        openModal(this.modalCtrl,RemotePage,{pres:pres});
    }

    addQuestion(pres: any) {
        showPopup({
            title:"LIB.QUESTION",
            confirmButton:"LIB.OK",
            cancelButton:"LIB.CANCEL",
            translate:this.translate,
            type:"text"},this.alertCtrl,(res)=> {
          if(res!=null){
            let message:any={
              text:pres.title+":"+res,
              photo:"",
              type:TYPE_MESSAGE,
              title:pres.title,
              tag:"message question presentation "+this.eventData.event.id,
              from:this.userData.user
            };

            this.eventData.sendmessage(0,message).subscribe(resp => {
                if(resp==null){
                    toast(this.toastCtrl,"MESSAGE.ERRORLIMIT",this.translate);
                } else {
                  toast(this.toastCtrl,"PRESENTATION.QUESTIONADDTOMESSAGE")
                }
            });
          }
        });
    }

    addSurvey(){
      openModal(this.modalCtrl,AddbetsPage,{type:5},function(){

      });
    }
}



