import {Component, ElementRef, ViewChild} from '@angular/core';
import {EventDataProvider} from "../../providers/event-data/event-data";
import {Content, LoadingController, NavController, NavParams, Scroll, ToastController} from "ionic-angular";
import {AlertController, Events, ModalController, } from "ionic-angular";
import {UserData} from "../../providers/user-data";
import * as Tools from "../../app/Maintools"
import {SearchPage} from "../search/search";
import { DomSanitizer} from '@angular/platform-browser';
import {PublicProfilPage} from "../public-profil/public-profil";
import {DELAY_TO_REFRESH, MUSICSERVER_LOCAL, openModal, openWindow, showPopup, toast} from "../../app/Maintools";
import {$$, loading, MUSICSERVER_PERSO, MUSICSERVER_YOUTUBE} from "../../app/Maintools";
import {InvitePage} from "../invite/invite";
import {MusicserverPage} from "../musicserver/musicserver";
import {TranslateService} from "@ngx-translate/core";
import {WidgetPage} from "../widget/widget";
import {ApiProvider} from "../../providers/api/api";
import {PushNotificationsService} from "ng-push";
@Component({
  selector: 'home',
  templateUrl: 'home.html'
})
export class HomePage {

  songs:any[]=[];
  flyer:string="";
  catalogues:string[]=[];
  //now:number;
  delay:string;
  lastRefresh:number=0;
  toSend:any[]=[];
  handleToSend=null;
  handle:any=null;
  musicServers=["Spotify","Deezer","Cloud","YouTube","Perso"];
  lastsong:string="";
  @ViewChild(Content) content: Content;


  constructor(  public events: Events,public modalCtrl:ModalController,
                public navCtrl: NavController,public navParams: NavParams,public api:ApiProvider,
                public eventData:EventDataProvider,public userData:UserData,public loadingCtrl:LoadingController,
                public sanitizer: DomSanitizer,public translate:TranslateService,public notifs:PushNotificationsService,
                public alertCtrl:AlertController,public toastCtrl:ToastController) {
    //this.now=new Date().getTime();
  }


  ionViewDidLoad() {
    var vm=this;
    Tools.subscribe("playlist",this);
    this.delay=Tools.getDelay(this.eventData.event.dtStart,this.userData.user.lang,"LIB.DAY");
    this.api.getcatalogues().subscribe(r=>{
      this.catalogues=r.result.split(",");
    });
    if(this.eventData.event.owner_id==this.userData.user.id){
      if(this.eventData.event.musicServer==null)this.openMusicServer();
      //Cas de la création d'un event par root avec musicServer=3
      if(this.eventData.event.musicServer==0 && (this.userData.user.accessTokens==null || this.userData.user.accessTokens.spotify==null))this.openMusicServer()
    }
  }

  ionViewDidEnter () {
    setTimeout(()=>{this.refresh(true);},DELAY_TO_REFRESH);
  }

  openPublicProfile(item:any){
    Tools.openModal(this.modalCtrl,PublicProfilPage,{userid:item.from.id});
  }

  addModerator(){
    this.events.publish("moderator:add");
  }





  runPlayer() {
    if(this.eventData.event.musicServer==MUSICSERVER_PERSO){
      this.djplay(this.songs[0]);
      return;
    }

    this.userData.getwidgets("music").subscribe((resp:any) => {
      var rc = null;
      resp.items.forEach((w) => {
        if ((this.eventData.event.musicServer != MUSICSERVER_YOUTUBE && w.name == "player") || (this.eventData.event.musicServer == MUSICSERVER_YOUTUBE && w.name == "videos"))
          rc = w;
      });
      if (rc != null)
        this.userData.getwidget(rc.id, true).subscribe((resp:any) => {
          openModal(this.modalCtrl, WidgetPage, {widget: resp, user: this.userData.user, event: this.eventData.event,preview:false});
        });
    });
  }




  openMusicServer(){
    if(this.eventData.event.musicServer==MUSICSERVER_LOCAL)this.userData.playlist.clear();
    openModal(this.modalCtrl,MusicserverPage,{from:"home"},(rep) => {
      if(rep.user!=null){
        this.userData.user=rep.user;
        this.eventData.event.musicServer=rep.user.musicServer;
      }
    });
  }


  /**
   * Ajout des titres en récurents pour ajout multiple
   * @param vm
   * @param l_songs
   * @param position
   * @param func
   */
  addsongs(vm,l_songs,position,func){
    if(vm.songs.indexOf(l_songs[position])==-1){
      vm.songs.push(l_songs[position]);

      vm.lastsong=l_songs[position].Id;

      vm.eventData.addsong(l_songs[position]).subscribe(rep => {
        Tools.toast(vm.toastCtrl, rep.message, vm.translate);
        if(position<l_songs.length-1 && this.songs.length<this.eventData.event.playlistLimits) {
          vm.addsongs(vm,l_songs,position+1,func);
          return;
        }
        else
          func();
      },(err)=>{
        Tools.toast(vm.toastCtrl,"LIB.ERRORRETRY",vm.translate);
        return;
      });
    }
  }

  addsongauto(){
    showPopup({
        title:this.translate.instant("HOME.ADDSONGAUTOTITLE"),
        value:10,
        confirmButton:this.translate.instant("LIB.OK"),
        cancelButton:this.translate.instant("LIB.CANCEL"),
        type:"number"},this.alertCtrl,

      (res) => {
        if (res > 0) {
          var l=loading(this);
          this.eventData.addsongsauto(res).subscribe(r=>{
            if(l!=null)l.dismiss();
            this.refresh(true);
          },()=>{
            if(l!=null)l.dismiss();});
        }
      });
  }


  addsong(author:string=null){
    var vm=this;
    var delay=(vm.eventData.event.dtEnd-new Date().getTime())/60000;
    if(delay<3) { //On ne peut pas faire de pari quand il reste moins de 3 minutes
      Tools.toast(vm.toastCtrl,"BETS.NOTENOUGHTTIMETOSONG",this.translate);
    }else{
      if (vm.userData.user.score < vm.eventData.event.minScore)
        Tools.toast(vm.toastCtrl,"ERROR.NOTENOUGHTSCORE",this.translate,() => {
          vm.navCtrl.push("ChartsPage",{type:"score"});
        });
      else{

        if(!this.eventData.event.opened && this.songs.length>=this.eventData.event.prePlaylistLimits){
          Tools.toast(vm.toastCtrl,"ADDEVENT.PREPLAYLISTLIMIT",this.translate);
          return;
        }

        var l=null;
        Tools.openModal(this.modalCtrl,SearchPage,{query:author,catalogues:this.catalogues},(r) => {
          if(r) {
            if(r.songs.length>2)l=loading(this);
            vm.addsongs(vm,r.songs,0,()=>{
              if(l!=null)l.dismiss();
            });
          }
        });
      }
    }
  };

  searchArtist(song:any){
    this.addsong(song.author);
  }

  openInvite(){
      openModal(this.modalCtrl,InvitePage,{type:Tools.INVITE_PLAYLIST,event:this.eventData.event,from:this.userData.user,dest:"home"});
  }

  openInfo(song:any){
    if(song.url!="")
      Tools.openWindow(song.url,"_blank");
  }

  updateEvent(){
    this.eventData.updateevent("minSongsInPlaylist,addSongOnlyByOwner,limitPlaylistSongsByUser,limitSearchToOwnerPlaylist,playlistLimits,prePlaylistLimits,musicServer").subscribe(r=>{});
  }

  delSong(song){
    var index=this.songs.indexOf(song);
    var vm=this;
    if(song.from.id==vm.userData.user.id || vm.userData.user.id==vm.eventData.event.owner.id) {
      Tools.showConfirm("HOME.CONFIRMDELETE", vm.alertCtrl,this.translate,  ()=> {
          vm.songs.splice(index, 1);
          vm.eventData.deletemessage(song.id,vm.userData.user.id).subscribe( (resp)=> {
            //vm.refresh();
          });
        });
    }
  };


  onSetScore(step,song){
    var index=this.songs.indexOf(song);
    var vm=this;
    var vote={target:song.id,event:vm.eventData.event.id,from:vm.userData.user.id,value:step,description:""};
    this.toSend.push(vote); //Ajout du vote à la liste
    vm.songs[index].canVote=false;
    vm.songs[index].score=vm.songs[index].score+step;

    if(this.handleToSend!=null)clearTimeout(this.handleToSend);
    this.handleToSend=setTimeout(()=>{
      vm.userData.sendvotes({votes:this.toSend}).subscribe((rep) => {
        this.toSend=[];
        if(rep==null)
          Tools.toast(vm.toastCtrl,"ERROR.RETRY",vm.translate);
        else{
          Tools.toast(vm.toastCtrl,vm.userData.user.message,vm.translate);
          //if(vm.handle!=null)clearTimeout(vm.handle);
          //vm.handle=setTimeout(() => {vm.refresh();},2000);
        }
      },(err) => {
        $$("error",err);
        Tools.toast(vm.toastCtrl,"ERROR.RETRY",vm.translate);
      });
    },2000); //Donc envoi des votes par paquet toutes les 2 secondes
  };


  nextSong(){
    this.eventData.nextsong(this.eventData.event.secretCode).subscribe(r=>{});
  }


  refresh(force:boolean=false){
    var vm=this;
    $$("Appel de refresh sur HomePage");
    if(!force && this.userData.selectTab!=undefined && this.userData.selectTab!="HomePage")return;
    $$("Confirmation de refresh");

    this.eventData.update();

    this.eventData.getplaylist(vm.eventData.event.id).subscribe((resp:any) => {
      if(resp!=null){
        if(resp.hasOwnProperty("items"))
          vm.songs = resp.items;
        else
          vm.songs=[];

        //S'il y a des titres
        var message:any={};
        if(vm.songs.length>0){
          vm.flyer="";
          for(var i=0;i<vm.songs.length;i++){
            vm.songs[i].canVote=(vm.songs[i].from.id != vm.userData.user.id);

            if(vm.songs[i].dtCreate>this.lastRefresh && vm.songs[i].from.id!=this.userData.user.id){
              message={
                title:"Shifumix - Playlist",
                body:"New track "+vm.songs[i].title+" from "+vm.songs[i].from.firstname,
                image:vm.songs[i].picture,
                vibrator:[200, 100, 200]
              };
              this.lastRefresh=vm.songs[i].dtCreate;
            }

            if(vm.songs[i].canVote) {
              if(vm.songs[i].hasOwnProperty("votes"))
                vm.songs[i].votes.forEach( (vote)=> {
                  if (vm.songs[i].canVote && vote.from == vm.userData.user.id){
                    vm.songs[i].canVote = false;
                  }
                });
            }
          }

          if(!force && message.hasOwnProperty("title"))Tools.notif(this.notifs,message,this.translate);

        }
        if(vm.songs.length>5 && vm.lastsong.length>0){
          setTimeout(()=>{vm.scrollTo(vm.lastsong);vm.lastsong="";},500);
        }
      }
    });
  };


  scrollTo(elementId:string) {
    $$("Recherche de "+elementId);
    let yOffset = document.getElementById(elementId);
    if(yOffset!=null)
      this.content.scrollTo(0, yOffset.offsetTop, 2000)
  }


  djplay(song: any) {
    if(this.eventData.event.musicServer==MUSICSERVER_PERSO){
      this.eventData.djplay(song).subscribe(()=>{
        toast(this.toastCtrl,song.title+" is playing",this.translate);
      });
    }
  }

  stopcurrentsong() {
    this.eventData.stopcurrentsong().subscribe(()=>{});
  }
}



