import { Component } from '@angular/core';
import {AlertController, IonicPage, NavController, NavParams, ToastController, ViewController} from 'ionic-angular';
import {UserData} from "../../providers/user-data";
import * as Tools from "../../app/Maintools"
import {showPopup} from "../../app/Maintools";
import {TranslateService} from "@ngx-translate/core";


@IonicPage()
@Component({
  selector: 'page-tarifs',
  templateUrl: 'tarifs.html',
})
export class TarifsPage {

  tarifs:any[]=[];
  newUsers:number=0;
  reduction:number=0;


  constructor(public viewCtrl:ViewController,public navCtrl: NavController,
              public navParams: NavParams,public userData:UserData,public alertCtrl:AlertController,
              public toastCtrl:ToastController,public translate:TranslateService) {
  }


  ionViewDidLoad() {
    this.userData.getmynewusers(this.userData.user.tarif.dtLastPayment).subscribe((r:any)=>{
      if(r.items!=undefined){
        this.newUsers=r.items.length;
        this.reduction=this.newUsers*0.25;
      }

      this.userData.getAllTarifs().subscribe((resp:any) => {
        this.tarifs=resp.items;
      });
    })
  }

  selTarif(t){
    Tools.showConfirm("TARIF.CHANGECONFIRMATION",this.alertCtrl,this.translate,()=> {
        var index = this.tarifs.indexOf(t);
        var vm = this;
        this.userData.updatetarif(this.tarifs[index].tarif).subscribe((resp:any) => {
          this.userData.user=resp;
          Tools.toast(vm.toastCtrl, resp.message, vm.translate);
          this.viewCtrl.dismiss();
        });
      }, () => {this.viewCtrl.dismiss();}
    );
  }


  openDoc(item){
    Tools.openWindow(item.doc);
  }


  pay(){
    showPopup({title:"Payment",placeholder:"",confirmButton:"LIB.OK",cancelButton:"LIB.CANCEL",type:"number",translate:this.translate},this.alertCtrl,(res)=>{
      if(res!=null)
        this.userData.creditaccount(res).subscribe((r)=>{
          this.userData.user=r;
        });
    });
  }

}



