import {AfterViewInit, Component, ElementRef, EventEmitter, Input, OnInit, Output, ViewChild} from '@angular/core';
import {TextInput} from "ionic-angular";

/**
 * Generated class for the ShifuinputComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
  selector: 'shifuinput',
  templateUrl: 'shifuinput.html'
})
export class ShifuinputComponent implements AfterViewInit {
  @Input('placeholder') placeholder: string="";
  @Input('label') label: string="";
  @Input('size') size: number=300;
  @Input('fontsize') fontsize:string="normal";
  @Input('elt') elt: string="";
  @Input('clearInput') clearInput=false;
  @Input('align') align: string="left"; //ou right ou center
  @Input('type') _type: string="text";
  @Input('focus') focus: boolean=false;
  @Output('onchange') onchange: EventEmitter<any>=new EventEmitter();
  @Output('onclick') onclick: EventEmitter<any>=new EventEmitter();
  @Output('onblur') onblur: EventEmitter<any>=new EventEmitter();
  @Output('onenter') onenter: EventEmitter<any>=new EventEmitter();
  @ViewChild("myinput") myinput:TextInput;
  len_text=0;

  constructor() {

  }

  onkeypress(evt){
    if(evt.keyCode==13){
      this.onenter.emit({value:this.myinput.value});
      return;
    } else {
      this.onchange.emit();
    }

    if(this.myinput.value!=null)
      this.len_text=this.myinput.value.length;
    else
      this.len_text=0;

     if(this.len_text>this.size){
       this.myinput.value=this.myinput.value.substring(0,this.size-1);
       this.len_text=this.myinput.value.length;
       evt.preventDefault();
     }

  }

  ngAfterViewInit(): void {
    this.myinput.setElementStyle("font-size",this.fontsize);
    this.myinput.setElementStyle("text-align",this.align);

    if(this.focus){
      //this.myinput.setElementAttribute("autofocus","");
      setTimeout(()=>{
        this.myinput.setFocus();
      },500);

    }
  }




}



