import { Component } from '@angular/core';
import {NavParams, ToastController,ViewController} from 'ionic-angular';
import {IonicPage, LoadingController, ModalController, NavController} from 'ionic-angular';
import {EventDataProvider} from "../../providers/event-data/event-data";
import {UserData} from "../../providers/user-data";
import { Storage } from '@ionic/storage';
import {TranslateService} from "@ngx-translate/core";
import * as Tools from "../../app/Maintools"
import {WidgetPage} from "../widget/widget";
import {HtmleditorPage} from "../htmleditor/htmleditor";
import {$$, loading, toast} from "../../app/Maintools";
import {SettingsProvider} from "../../providers/settings/settings";

@IonicPage()
@Component({
  selector: 'page-widgetstore',
  templateUrl: 'widgetstore.html',
})
export class WidgetstorePage {

  widgets:any[]=[];
  query:string="";
  close=false;
  widget:any=null;
  categories:string="";
  _filter:string="";
  _query:string="";
  _defaultCategories:string="";

  constructor(public navCtrl: NavController, public navParams: NavParams,public loadingCtrl:LoadingController,public settings:SettingsProvider,
              public eventData:EventDataProvider,public userData:UserData,public viewCtrl:ViewController,public storage:Storage,
              public translate:TranslateService,public modalCtrl:ModalController,public toastCtrl:ToastController) {
    if(navCtrl.canGoBack())this.close=true;

    this.categories=this.translate.instant("WIDGET.CATEGORIESFILTER");
    if(this.navParams.get("widgetFilter")!=null){
      this._defaultCategories=this.navParams.get("widgetFilter");
      this._filter=this._defaultCategories;
    }

  }

  ionViewWillEnter(){
    toast(this.toastCtrl,this.navParams.get("message"),this.translate);
    this.refresh();
  }

  refresh(){
    var vm=this;
    var l=loading(vm);
    this.userData.getwidgets(null).subscribe((resp:any) => {
      vm.widgets=[];
      l.dismiss();
      resp.items.forEach(function(w){
        w.qrcode="";
        if(w.description.indexOf("PROFIL.")==0)w.description=vm.translate.instant(w.description,vm.userData.user.lang);
        w.tag= w.description+ " "+w.title+" "+w.activity;
        if(Tools.isFavorite(vm.userData.user,w)){
          w.tag= w.tag+" favorite";
          w.favorite=true;
        }
        vm.widgets.push(w);
      });
    });
  }

  addWidget(){
    var vm=this;
    var title=vm.translate.instant("WIDGETSTORE.NEWWIDGET_TITLE");
    var desc=vm.translate.instant("WIDGETSTORE.NEWWIDGET_DESCRIPTION");
    vm.userData.getwidgetfrommodel(title,desc).subscribe(r=>{
      vm.widget=r;
      vm.widget.parentid=vm.widgets[0].id;
      Tools.openModal(this.modalCtrl,HtmleditorPage,{user:vm.userData.user.id,widget:vm.widget,mode:"clone"},(resp)=>{
        if(resp!=null && resp.widget!=undefined)
          vm.userData.sendwidget(resp.widget).subscribe(r => {
            vm.refresh();
          });
      });
    });
  }

  filterFavorite(){
    var vm=this;
    vm.query="favorite";
  };

  openWidget(link){
    var vm=this;
    if(vm.navParams.get("from")=="AddeventAdvancedPage"){
      this.viewCtrl.dismiss(link);
      return;
    }

    if(vm.navParams.get("screen")==undefined){
      this.userData.getwidget(link.id,true).subscribe((resp:any) => {
        if(resp!=null){
          Tools.openModal(this.modalCtrl,WidgetPage,{widget:resp,user:this.userData.user,event:this.eventData.event,preview:true},function(resp){
            vm.refresh();
          });
        }
      });
    }
    else {
      this.viewCtrl.dismiss(link);
    }
  };

  onclose(){
    this.viewCtrl.dismiss()
  }

  updateTextQuery(){
    //this._filter="";
    this.updateQuery();
  }

  updateQuery(){
    this._query="";
    if(this._filter.length>0)this._query=this._filter;
    if(this.query.length>0)this._query=this.query;
    if(this._filter.length>0 && this.query.length>0)this._query=this.query+" AND "+this._filter;
    this._query=this._query.trim();
  }

  updateFilter(f){
    this._filter=f.value.replace(","," AND ");
    this.updateQuery();
  }

}



