import { Component } from '@angular/core';
import {Events, NavController, NavParams, ToastController, ViewController} from 'ionic-angular';
import {AlertController, IonicPage, ModalController} from 'ionic-angular';
import * as Tools from "../../app/Maintools"
import {HtmleditorPage} from "../htmleditor/htmleditor";
import {UserData} from "../../providers/user-data";
import {DomSanitizer} from "@angular/platform-browser";
import {askforaddscreen, DOMAIN_SERVER, ROOT_API, toast, VERSION_API} from "../../app/Maintools";
import {TranslateService} from "@ngx-translate/core";
import {EventDataProvider} from "../../providers/event-data/event-data";
import {ApiProvider} from "../../providers/api/api";


@IonicPage()
@Component({
  selector: 'page-widget',
  templateUrl: 'widget.html',
})
export class WidgetPage {
  hWndMusicPlayer: any=null;
  url=null;
  widgetPreview=null;
  widget:any={qrcode:"",width:400,height:200,onlythisevent:true,_public:false,favorite:false,shorturl:""};
  user:any={};
  event:any={};
  widget_link:string="";
  newevent_link:string="";
  screen_titles:string="";
  screen_ids:string="";


  constructor(public modalCtrl:ModalController,public userData:UserData,public alertCtrl:AlertController,public eventData:EventDataProvider,
              public viewCtrl:ViewController,public navCtrl: NavController,public toastCtrl:ToastController,public events:Events,public api:ApiProvider,
              public navParams: NavParams,private domSanitizer : DomSanitizer,public translate:TranslateService) {

    this.widget = navParams.get("widget");
    this.widget.code = Tools.extract(this.widget.code, "<body>", "</body>");

    this.user = navParams.get("user");
    this.event = navParams.get("event");

    this.widget.favorite = Tools.isFavorite(this.user, this.widget);
    this.widget.onlythisevent = true;

    if (this.userData.user.currentEvent != undefined && this.userData.user.currentEvent.length > 0 && this.navParams.get("preview")) {
      var sep = "?";
      if (this.widget.long_url.indexOf("?") > 0) sep = "&";
      this.widgetPreview = this.domSanitizer.bypassSecurityTrustResourceUrl(this.widget.long_url + sep + "event=" + this.userData.user.currentEvent);
      setTimeout(()=>{this.widget.preview="ok"},5000);
    }

    //dest=PhotoPage&flyer=https://www.rapid-cadeau.com/9229-large_default/cube-photos-rotatif.jpg&activities=photo,message&widget=photocube
    var duration=Math.round((this.eventData.event.dtEnd-this.eventData.event.dtStart)/(3600*1000));
    var url=DOMAIN_SERVER+"/createeventandrunwidget.html"+
      "?duration="+duration+
      "&autologin="+this.eventData.event.autologin+
      "&lat="+this.eventData.event.lat+"&lng="+this.eventData.event.lng+
      "&title=" + encodeURI(this.eventData.event.title)+
      "&widget="+this.widget.name+
      "&public="+this.eventData.event.visibleOnMap+
      "&activities=" + this.eventData.event.activities +
      "&flyer=" + this.eventData.event.templateFlyer;

    //this.newevent_link=url;
     this.api.urlshortener(url).subscribe((r)=>{
       this.newevent_link=r.url;
    });

    if(this.eventData.event.id==null){
      this.widget_link=DOMAIN_SERVER+"/widgets/freeScreen.html?widget="+this.widget.name+"&user="+this.userData.user.id+"&sc="+this.userData.user.secretCode;
    } else {
      this.widget_link=DOMAIN_SERVER+"/widgets/freeScreen.html?widget="+this.widget.name+"&event="+this.eventData.event.id+"&user="+this.userData.user.id+"&sc="+this.userData.user.secretCode;
    }



      this.userData.get().subscribe((rep)=>{
        this.userData.user=rep;
          for(let p in this.userData.user.screens){
              this.screen_ids=this.userData.user.screens[p].id+",";
              this.screen_titles+=this.userData.user.screens[p].title+",";
          }
      });

  }

  ionViewDidLoad(){

  }


editHTML(){
    var vm=this;
    if(this.widget.owner==this.userData.user.id)
      Tools.openModal(this.modalCtrl,HtmleditorPage,{user:this.userData.user.id,widget:this.widget,mode:"edit"},function(resp){
        if(resp!=null){
          vm.widget=resp.widget;
          vm.saveWidget();
        }
      });
    else
      this.clone();
  }



  clone(){
    this.widget.title=this.widget.title+"_copy";
    this.widget.parentid=this.widget.id;
    this.widget.id="";
    this.widget.owner=this.userData.user.id;
    this.widget.description="";
    this.widget.score=0;

    var vm=this;
    Tools.openModal(this.modalCtrl,HtmleditorPage,{user:this.userData.user.id,widget:this.widget,mode:"edit"},function(resp){
      if(resp!=undefined){
        vm.widget=resp.widget;
        vm.saveWidget();
      }
      else
        vm.viewCtrl.dismiss();
    });
  }

  delWidget(){
    this.userData.delwidget(this.widget.id).subscribe(resp => {
      this.viewCtrl.dismiss();
    });
  }

  widgetPerso(){
    // this.userData.sendwidgetbymail(this.widget.widget).subscribe(()=> {
    //   Tools.toast(this.toastCtrl,"WIDGET.CHECKYOUREMAIL");
    // });
  }

  changeFavorite(){
    // if(this.widget.favorite)
    //   this.userData.addtofavoritewidget(this.widget.id).subscribe((res) => {this.user=res;});
    // else
    //   this.userData.delfavoritewidget(this.widget.id).subscribe((res) => {
    //     this.user=res;
    //   });
  }

  changePublic(){
    this.userData.sendwidget(this.widget).subscribe(() => {});
  }

  saveWidget(){
    var vm=this;
    vm.userData.sendwidget(this.widget).subscribe(r => {
      vm.viewCtrl.dismiss({refresh:true});
    });
  }

  openLink() {
    if(this.eventData.event==undefined || this.eventData.event.owner_id!=this.userData.user.id)
      toast(this.toastCtrl,"WIDGET.WARNINGNOACTIFEVENT",this.translate);

    if (this.widget.state != "" && this.widget.state != undefined){
      //$state.go(url.state);
    }else{
      var h=window.screen.availHeight;
      var w=window.screen.availWidth;

      // if(this.widget.long_url.indexOf("MusicPlayer")>0){
      //   this.hWndMusicPlayer=Tools.openMusicPlayer(this.event,this.event.secretCode,this.userData.user.lang);
      // }else{
      var handle=window.open(this.widget.url,"_blank","titlebar=no,status=no,menubar=no,height="+h+",width="+w);
      // }
    }

    this.viewCtrl.dismiss({refresh:true});
  };


  enterScreenCode() {
    askforaddscreen(this,this.widget.name,(res,widget)=> {
      this.userData.user.code = res;
      this.userData.user.widgetToAdd = widget;
      this.userData.senduser("code,widgetToAdd").subscribe(() => {
        this.viewCtrl.dismiss({refresh:false});
      });
    });
  }

  setOnScreen(screens) {
    this.userData.setwidgetonscreen(screens.value,this.widget.id).subscribe(() => {
      this.viewCtrl.dismiss({refresh:false});
    });
  }

  confirmCopy() {
    toast(this.toastCtrl,'WIDGET.URLINCLIPBOARD',this.translate);
  }

  showScreen() {
    for(let k in this.userData.user.screens){
      var s=this.userData.user.screens[k];
      s.title=s.title+" ";
      this.userData.updatescreen(s).subscribe(r=>{});
    }
  }
}



