export function extract(str, start, end) {
  var i = str.indexOf(start) + start.length;
  var j = str.length;

  if (i < start.length) i = 0;

  if (end.length > 0) {
    j = str.indexOf(end, i);
    if (j == -1)return ("");
  }
  return (str.substring(i, j));
}


export function getParam(): any {
  var vars = {}
  window.location.href.replace(location.hash, '')
    .replace(/[?&]+([^=&]+)=?([^&]*)?/gi, (m, key, value) => vars[key] = value !== undefined ? value : '');
  return vars
}


