function loadLink(url,type,type_doc) {
  var x = document.createElement("LINK");
  x.setAttribute("rel", type_doc);
  x.setAttribute("type", type);
  x.setAttribute("href", url);
  document.head.appendChild(x);
}

function loadScript(url, callback,func_failed=null){   // Adding the script tag to the head as suggested before
  var head = document.getElementsByTagName('head')[0];
  var script = document.createElement('script');
  script.type = 'text/javascript';
  script.src = url;
  script.addEventListener("readystatechange",callback);
  script.onerror=func_failed;
  script.onload = callback;
  head.appendChild(script);
}

function objToStrMap(obj) {
  let strMap = new Map();
  for (let k of Object.keys(obj)) {
    strMap.set(k, obj[k]);
  }
  return strMap;
}

function MapToStr(obj):String {
  let rc="";
  for (let k of Object.keys(obj)) {
    rc+=k+"="+obj[k]+"&"
  }
  return rc.substr(0,rc.length-1);
}

export function api(service:string,param:string,encode:boolean=true):string  {
  if(encode)param=encodeURI(param);
  return(ROOT_API+'/shifumix'+VERSION_API+"/"+service+"?"+param)
}

export function httpGet(service,func,func_error=null,asynchron=true){
  if(asynchron==undefined)asynchron=true;

  let xhr = new XMLHttpRequest();
  if(service.indexOf("http")!=0)service=ROOT_API+'/shifumix'+VERSION_API+'/'+service;

  xhr.open('GET', service, asynchron);

  xhr.onreadystatechange = function() {
    if (xhr.readyState == 4) {
      var resp={status:xhr.status,result:""};
      if(xhr.status!=404 && xhr.responseText.length>0){
        if(
          xhr.responseText.indexOf("{")==0)
          resp.result=JSON.parse(xhr.responseText);
        else
          resp.result=xhr.responseText;
      }
      if(func!=undefined)func(resp);
    }
    else
      if(func_error!=undefined)func_error(xhr);
  };
  xhr.send();
}


function _hasPopupBlocker(poppedWindow) {
  var result = false;

  try {
    if (typeof poppedWindow == 'undefined') {
      // Safari with popup blocker... leaves the popup window handle undefined
      result = true;
    }
    else if (poppedWindow && poppedWindow.closed) {
      // This happens if the user opens and closes the client window...
      // Confusing because the handle is still available, but it's in a "closed" state.
      // We're not saying that the window is not being blocked, we're just saying
      // that the window has been closed before the test could be run.
      result = false;
    }
    else if (poppedWindow && poppedWindow.test) {
      // This is the actual test. The client window should be fine.
      result = false;
    }
    else {
      // Else we'll assume the window is not OK
      result = true;
    }

  } catch (err) {
    //if (console) {
    //    console.warn("Could not access popup window", err);
    //}
  }

  return result;
}


function trunc(n : Number) : Number {
  var s = n+".0";
  return Number(s.split(".")[0]);
}

/**
 * shoz the delay between a date and now with a perfect format
 * @param dtStart
 * @param lang
 * @param label_day
 * @returns {*}
 */
export function getDelay(dtStart,lang="en",label_day="jours",serverNow=null){
  if(dtStart==undefined)return "";
  if(serverNow==null)serverNow=new Date().getTime();
  var delay=Math.abs(dtStart-serverNow);

  if(delay>24*3600*1000){
    var nbJours=trunc(delay/(24*3600*1000));
    return nbJours+" "+label_day;
  }

  if(lang==undefined)lang="fr";
  var affichage =new Date(delay).toUTCString().replace(/.*(\d{2}:\d{2}:\d{2}).*/, "$1");
  if(affichage.indexOf("00:")==0)
    affichage=affichage.split(":")[1]+":"+affichage.split(":")[2];
  else
    affichage=affichage.split(":")[0]+"h"+affichage.split(":")[1];

  return affichage;
}


