var hTimeout;

export function _copy(obj1:any):any{
  return Object.assign({},obj1);
}

export function transfert(obj1,obj2,strict_copy=true){
  if(obj1==undefined)return null;
  if(obj2==undefined)
    obj2=JSON.parse(JSON.stringify(obj1))
  else {
    for (prop in obj1)
      obj2[prop] = JSON.parse(JSON.stringify(obj1[prop]))

    if(strict_copy)
      for (var prop in obj2)
        if(!obj1.hasOwnProperty(prop))
          delete obj2[prop];

  }

  return obj2;
}

