var boundary = '-------314159265358979323846';
var delimiter = "\r\n--" + boundary + "\r\n";
var close_delim = "\r\n--" + boundary + "--";

function httpPost(service,params,body,jauge,func){
  var xhr = new XMLHttpRequest();
  xhr.open('POST', ROOT_API+'/shifumix/'+VERSION_API+'/'+service+"?"+params, true);

  /*
   xhr.upload.addEventListener("progress", function(e) {
   jauge.value=parseFloat(e.loaded / e.total * 100).toFixed(2);
   }, false);
   */

  xhr.onreadystatechange = function(e) {
    var obj={result:"",status:0};
    if (xhr.readyState == 4) {
      if(xhr.responseText.length>0)obj.result=JSON.parse(xhr.responseText);
      obj.status=200;

      func(obj);
    }
  };
  xhr.send(JSON.stringify(body));
}



