export function showPopup(popup,alertCtrl,func) {
  if(popup.placeholder==undefined)popup.placeholder="";
  if(popup.cancelButton==undefined)popup.cancelButton="Cancel";
  if(popup.confirmButton==undefined)popup.confirmButton="Ok";
  if(popup.value==undefined)popup.value="''";
  if(popup.subtitle==undefined)popup.subtitle="";
  if(popup.default==undefined)popup.default="";
  if(popup.type==undefined)popup.type="text";

  if(popup.translate!=undefined){
    popup.cancelButton=popup.translate.instant(popup.cancelButton);
    popup.confirmButton=popup.translate.instant(popup.confirmButton);
    popup.title=popup.translate.instant(popup.title);
    if(popup.hasOwnProperty("placeholder") && popup.placeholder.length>0)popup.placeholder=popup.translate.instant(popup.placeholder);
  }

  const alert = alertCtrl.create({
    title: popup.title,
    subTitle:popup.subtitle,
    inputs: [{value:popup.default,type:popup.type,name: 'value',placeholder: popup.placeholder}],
    buttons: [
      {text: popup.cancelButton,role: 'cancel',handler: data => {func(null)}},
      {text: popup.confirmButton,handler: data => {
        func(data.value)
      }
      }
    ]
  });
  alert.present();
};


export function contain(evts:any[],evt:any):boolean {
  if(evts!=null)
    evts.forEach((e)=>{
      if(e.id===evt.id)return true;
    });
  return false;
}

/**
 * Chargement d'une fenetre d'attente
 * @param vm designe la fenetre courrante, doit contenir un objet loadingCtrl
 * @param {string} message
 * @param {number} delayInSec
 * @returns {Loading}
 */
export function loading(vm:any,message:string="Please wait...",delayInSec:number=60) {
  if(vm.translate!=null)message=vm.translate.instant(message);
  if(vm.loadingCtrl==undefined)throw("Erreur:loadingCtrl not defined");
  let loading = vm.loadingCtrl.create({content: message});
  loading.present();
  if(delayInSec>0)
    setTimeout(() => {loading.dismiss();}, delayInSec*1000);
  return loading;
}



export function showConfirm(message:string,alertCtrl:AlertController,translate:TranslateService,func_yes,func_no=null,forceYes=false) {
  if(forceYes)
    func_yes();
  else{
    message=translate.instant(message);
    const alert = alertCtrl.create({
      title: 'Confirm',
      message: message,
      buttons: [
        {text: 'Cancel',role: 'cancel',handler: () => {if(func_no!=null)func_no()}},
        {text: 'Ok',handler: () => {func_yes()}}
      ]
    });
    alert.present();
  }
}

