function buildGrid(cels){
  var rows=Math.round(Math.sqrt(cels)-0.5)+1;
  var rc="<table style='width:100%;text-align:center;vertical-align: middle;border-color: #000000 ;'>";
  for(var i=0;i<rows;i++){
    rc+="<tr>";
    for(var j=0;j<rows;j++){
      var id="cel"+(i*rows+j);
      rc+="<td><div id='"+id+"'></div></td>";
    }
    rc+="</tr>";
  }
  return rc+"</table>";
};

export function tirage(max):number{
  return Math.round(Math.random()*max);
};


export function openMusicPlayer(evt,secretCode,lang){
  var h=window.screen.availHeight;
  var w=window.screen.availWidth;

  var widget="MusicPlayer.html";
  if(evt.musicServer==YOUTUBE)widget="widgetVideo.html";

  var url=DOMAIN_SERVER+"/widgets/"+widget+"?event="+evt.id+"&sc="+secretCode+"&lang="+lang;
  url=url.replace("https","http");
  if(evt.musicServer==DEEZER_SERVER)url+="&filter=deezer";

  var hwnd=this.openWindow(url,"_blank","titlebar=no,status=no,height=500,width=430,menubar=no");
  hwnd.moveTo(0,0);
  return hwnd;
}

