export function openGeneral(item, user)  {
  return new Promise((resolve,reject)=>{
    if (user!=null && user.accessTokens != undefined && user.accessTokens[item] != null)
      resolve(user.id);
    else {
      let url = DOMAIN_SERVER + "/connectTo?user=" + user.id + "&service=" + item;
      $$("ouverture de " + url);

      let hwnd = openWindow(url, "Login", "menubar=0,status=0");
      window.addEventListener("message", (event:  any)=>{
        if (event.origin !== url.split("/connect")[0] && event.origin.indexOf("shifumixweb")==-1)
          reject();
        else{
          resolve(event.data);
        }
      }, false);
    }
  });
}


