export function openWindow(url, title = "_blank", option = "") {
  if (option == undefined) option = "location=no, titlebar=no, menubar=no, status=no, scrollbars=no, menubar=no, left=50, top=50, width=400, height=600, center=yes";
  if (title == undefined) title = "_blank";
  return window.open(url, title, option);
}

