export function createPhoto(user: any, contenu: string, text: string = "", tags: string = ""): any {
  var photo = {
    text: text,
    tags: [],
    from: user,
    legend: "",
    format: "jpeg",
    anonymous: user.anonymous,
    photo: contenu,
    type: TYPE_PHOTO
  };
  if(contenu=="")$$("Probleme de contenu sur la photo");
  if (tags == undefined)
    photo.tags = [];
  else
    photo.tags = tags.split(",");

  return photo;
};

