export function getDelayInDays(dtStart) {
  return Math.round((new Date().getTime() - dtStart) / (3600 * 1000 * 24));
}

export function getDelayInMinutes(dt:number):number {
  return Math.round((dt-new Date().getTime()) / (60 * 1000));
}

function radians(degrees) {
  return degrees * Math.PI / 180;
};

function drawRotated(canvas, image, degrees) {
  var ctx = canvas.getContext("2d");
  ctx.clearRect(0, 0, canvas.width, canvas.height);
  ctx.save();
  ctx.translate(canvas.width / 2, canvas.height / 2);
  ctx.rotate(degrees * Math.PI / 180);
  ctx.drawImage(image, -image.width / 2, -image.height / 2);
  ctx.restore();
}

export function rotate(src: string, angle: number, quality: number, func) {
  if (angle == 0)
    func(src);
  else {
    var img = new Image();
    img.onload = function () {
      var canvas = document.createElement('canvas');
      canvas.width = img.height;
      canvas.height = img.width;
      drawRotated(canvas, this, angle);
      var rc = canvas.toDataURL("image/jpeg", quality);
      func(rc);
    }
    img.src = src;
  }
}

function isFacebookApp() {
  var ua = navigator.userAgent || navigator.vendor;
  return (ua.indexOf("FBAN") > -1) || (ua.indexOf("FBAV") > -1);
}

export function geocode(googlemaps, address, func) {
  var geocoder = new googlemaps.Geocoder();
  geocoder.geocode({'address': address}, function (infos) {
    if (infos.length == 1)
      func({lat: infos[0].geometry.location.lat(), lng: infos[0].geometry.location.lng()});
    else
      func();
  });
  func();
}


export function reverse_geocode(googlemaps, lat, lng, func) {
  var pos = new googlemaps.LatLng(lat, lng);
  var geocoder = new googlemaps.Geocoder();
  geocoder.geocode({location: pos}, function (results, status) {
    if (status === googlemaps.GeocoderStatus.OK) {
      if (results[0])
        func(results[0].formatted_address);
      else
        func("");
    }
    else func("");
  });
}


