export function notif(notif:PushNotificationsService,message:any,translate:TranslateService){
  if(notif!=null && notif.permission=="granted"){
    if(translate!=null){
      message.body=translate.instant(message.body);
      message.title=translate.instant(message.title);
    }

    notif.create(message.title,message).subscribe(()=>{},()=>{});
  }
}

export function getDate(dtStart:number=null,lang="FR-fr",onlyHour:boolean=false){
  if(dtStart==null)dtStart=new Date().getTime();
  var rc="";

  var dt= new Date();
  dt.setTime(dtStart);

  if(onlyHour){
    rc=dt.toLocaleTimeString(lang);
    rc=rc.split(":")[0]+"h"+rc.split(":")[1];
  }
  else
    rc=dt.toLocaleString(lang);

  return rc;
}



