import {Component, EventEmitter, Input, Output} from '@angular/core';

/**
 * Generated class for the ShifunumpadComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
  selector: 'shifunumpad',
  templateUrl: 'shifunumpad.html'
})
export class ShifunumpadComponent {
  code: string="";
  @Input("prefix") prefix="";
  @Output('onenter') onenter: EventEmitter<any>=new EventEmitter();

  constructor() {}

  addKey(v){
    this.code+=v;
    if(this.code.length==6){
      this.onenter.emit({value:this.code});
      this.clearCode();
    }
  }

  clearCode(){
    this.code="";
  }

}



