import { Component } from '@angular/core';
import {
  AlertController, IonicPage, ModalController, NavController, NavParams, ToastController,
  ViewController
} from 'ionic-angular';
import {TranslateService} from "@ngx-translate/core";
import {UserData} from "../../providers/user-data";
import {EventDataProvider} from "../../providers/event-data/event-data";
import {openModal, toast} from "../../app/Maintools";
import {ImageCreatorPage} from "../image-creator/image-creator";

/**
 * Generated class for the AddloteriePage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-addloterie',
  templateUrl: 'addloterie.html',
})
export class AddloteriePage {

  newlot:any={type:6,question:"",respons:"",from:"",title:"",description:"",picture:"",dtStart:0,startDelay:5,delay:10,minCredits:0,maxCredits:1};
  now=new Date().getTime();
  mindelay=3;

  constructor (public modalCtrl:ModalController,public viewCtrl:ViewController,public translate:TranslateService,
               public navCtrl: NavController,public navParams: NavParams,public toastCtrl:ToastController,
               public eventData:EventDataProvider,public userData:UserData,
               public alertCtrl:AlertController) {

    //Pas de délai minimum de visibilité pour les modérateurs
    if(this.eventData.event.Moderators.indexOf(this.userData.user.id)>-1){
      this.mindelay=0;
      this.newlot.startDelay=0;
    }

    this.newlot.question=this.translate.instant("LOTERIE.DEFAULTQUESTION");
    this.newlot.respons=this.translate.instant("LOTERIE.DEFAULTRESPONS");

    this.newlot.from=this.userData.user;
    if(this.eventData.event.dtStart>this.now){
      this.newlot.maxdelay=(this.eventData.event.dtEnd-this.eventData.event.dtStart)/60000; //Un pari ne peut aller au dela de la fin de l'evenement
    }else
      this.newlot.maxdelay=(this.eventData.event.dtEnd-this.now)/60000; //Un pari ne peut aller au dela de la fin de l'evenement

    this.getDtStart();

  }

  addLot(){
    var vm=this;
    this.getDtStart();
    this.eventData.addloterie(this.newlot).subscribe((resp:any)=>{
      if(resp.message.length>0){
        toast(vm.toastCtrl,resp.message,vm.translate);
      }
      vm.viewCtrl.dismiss();
    });
  };

  getDtEnd=function(){
    this.newlot.dtEnd=Number(this.newlot.dtStart)+(Number(this.newlot.delay))*1000*60;
  };

  getDtStart=function(){
    var start=Math.max(this.now,Number(this.eventData.event.dtStart));
    this.newlot.dtStart=start+(Number(this.newlot.startDelay))*1000*60;
    this.getDtEnd();
  };

  selImage(){
    var vm=this;
    openModal(this.modalCtrl,ImageCreatorPage,{ratio:1,upload:true},rep => {
      if(rep!=null) {
        vm.newlot.picture = rep.value;
      }
    })
  }


}



