import { Component } from '@angular/core';
import {Events, IonicPage, NavParams,ToastController, ViewController} from 'ionic-angular';
import {ApiProvider} from "../../providers/api/api";
import {TranslateService} from "@ngx-translate/core";
import {UserData} from "../../providers/user-data";
import {EventDataProvider} from "../../providers/event-data/event-data";
import {DomSanitizer} from "@angular/platform-browser";

/**
 * Generated class for the RemotePage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-remote',
  templateUrl: 'remote.html',
})
export class RemotePage {
    pres:any=null;

    constructor(
                public userData:UserData,
                public eventData:EventDataProvider,
                public viewCtrl:ViewController,
                public toastCtrl:ToastController,
                public events:Events,
                public api:ApiProvider,
                public navParams: NavParams,
                private domSanitizer : DomSanitizer,
                public translate:TranslateService) {
      this.pres=this.navParams.get("pres");
    }


    page(pres:any,sens:number){
      if(pres.page>0){
            pres.page=pres.page+sens;
            this.eventData.gotopage(pres).subscribe(()=>{});
      }
    }

}



