import {Component, ElementRef, ViewChild} from '@angular/core';
import {AlertController, LoadingController, ModalController, Platform, ToastController} from 'ionic-angular';
import {Events,NavController, NavParams} from 'ionic-angular';
import {TranslateService} from "@ngx-translate/core";
import { SeleventPage } from '../selevent/selevent';
import {UserData} from "../../providers/user-data";
import * as Main from "../../app/Maintools"
import {LoginemailPage} from "../loginemail/loginemail";
import {ApiProvider} from "../../providers/api/api";
import {$$, getEnv, isLocal, loading, openModal, openWindow, toast} from "../../app/Maintools";
import { Storage } from '@ionic/storage';
import {EventDataProvider} from "../../providers/event-data/event-data";
import {LoginavatarPage} from "../loginavatar/loginavatar";
import {SettingsProvider} from "../../providers/settings/settings";

declare var window:any;

@Component({
  selector: 'page-user',
  templateUrl: 'login.html'
})
export class LoginPage {
  test = {value: 10};
  temp_user: any=null;
  env: number = 1;
  welcome_message="";
  backimage:string="";
  @ViewChild("container") container:ElementRef;
  connexion:string="";
  allowAnonymous:boolean=true;
  pseudo:string="";

  constructor(public navParams: NavParams,public api:ApiProvider,public eventData:EventDataProvider,
              public navCtrl: NavController, public userData: UserData,public storage: Storage,
              public loadingCtrl:LoadingController,public settings:SettingsProvider,
              public platform: Platform, public translate: TranslateService,public modalCtrl:ModalController,
              public events: Events,public alertCtrl:AlertController,public toastCtrl:ToastController,
              public plt: Platform) {

    this.settings.loadtheme();
    this.connexion=navParams.get("connexion");
    this.storage.get("anonymous").then(anonym=>{
      if(anonym!=null)this.allowAnonymous=(anonym=="true");
    });

  }

  ionViewDidEnter() {
    var vm=this;
    this.storage.get("server").then(r=>{
      Main.$$("Demande de connexion a "+r);
      this.api.connectToServer(r,this.platform.userAgent(),rep => {
        var temp=JSON.parse(rep.services);
        this.settings.services={'services':[]};
        temp.services.forEach(s=>{
          if(s.mode==null || s.mode!="dev" || isLocal())
            this.settings.services.services.push(s);
        });

        this.eventData.socketServerAddress=rep.socketserver;
        vm.env=Main.getEnv();

          this.userData.load((ok) => {
            if (ok) {
              Main.$$("Chargement du user ok");
              this.userData.get().subscribe((rep:any) => {
                  Main.$$("Récupération du compte ",rep);
                  if(rep==null || rep.temp){ //On connait le joueur
                    Main.$$("Effacement de la clé user");
                    this.storage.remove("user");
                    this.ionViewDidEnter();
                    return;
                  } else {
                    Main.$$("Reconnexion automatique de ",rep);
                    this.nextScreen(rep);
                    return;
                  }
                },
                err => {
                  Main.$$("Erreur de récupération du compte");
                  vm.userData.setId(null);
                  vm.createTempUser();
                });
            }else{ //On ne connait pas le participant sur ce device
                if(this.settings.autologin>0)
                  this.anonymousPrompt(null,this.settings.autologin);
                else{
                  if(this.settings.login_email.length>0){
                    this.showEmailLogin();
                  }else{
                    Main.$$("Chargement du user KO !");
                    vm.createTempUser();
                    this.storage.get("flyer").then( flyer=> {
                      if (flyer != null && flyer!="undefined"){
                        vm.backimage = flyer;
                        var img=new Image();
                        img.onload=()=>{
                          vm.container.nativeElement.style.backgroundImage="url(\""+flyer+"\")";
                          vm.container.nativeElement.style.backgroundSize="cover";
                          if((screen.availWidth-img.width)/2<0){
                            vm.container.nativeElement.style.backgroundPositionX=(screen.availWidth-img.width)/2+"px";
                            vm.container.nativeElement.style.backgroundPositionY=(screen.availHeight-img.height)/2+"px";
                          }
                          vm.container.nativeElement.style.backgroundRepeat="no-repeat";
                          vm.container.nativeElement.style.filter="grayscale(70%);blur(2px);saturate(90%)";
                        }
                        img.src=flyer;
                      }
                      else
                        vm.backimage="";

                      vm.storage.remove("flyer");
                    });
                  }
                }
            }
          });
      },err => {
        $$("Probleme de connexion au serveur :"+err)
        setTimeout(()=>{
          this.ionViewDidEnter();
        },5000)
      });
    });

    this.storage.get("type").then( r=> {
      if(r=="create_event")this.welcome_message="LOGIN.WELCOMECREATEEVENT";
    });

  }


  /**
   * Connexion à l'écran principal
   * @param u
   */
  nextScreen(u){
    Main.$$("NextScreen. Vérification de la possibilité de connexion a l'écran principal pour ",u);
    this.storage.remove("anonymous");
    this.userData.setId(u.id);
    let l=loading(this,"Connexion ...");
    this.userData.canlogin().subscribe((r:any)=>{
      toast(this.toastCtrl,r.message,this.translate);
      l.dismiss();
      if(r.message==""){
        Main.$$("Pas de probleme de connexion. Connexion avec ",u);
        this.events.publish("user:login",u);
      }
    },()=>{l.dismiss();});
  }

  showEmailLogin(){
    openModal(this.modalCtrl,LoginemailPage,null,(r) => {
      if(r!=null){
        this.nextScreen(r);
      }
    });
  }

  facebook_login(){
    if(getEnv()==1)
      this.ServiceLogin("facebook");
     else
      this.ServiceLogin("fb_local");
  }

  /**
   * Service de connexion
   * @param token_name
   * @constructor
   */
  ServiceLogin(token_name) {
    $$("Connexion a "+token_name);
    var vm=this;

    Main.openGeneral(token_name,vm.temp_user).then((id:string)=>{
      vm.userData.get(id).subscribe((resp:any) => { //Ouvre le user temporaire
        if(resp==null){
          vm.events.publish("user:loggout");
          return;
        }

        if(resp.hasOwnProperty("accessTokens") && resp.accessTokens[token_name]!=null && resp.accessTokens[token_name].token!=null){
          var l=loading(this);
          vm.userData.setId(resp.id);
          vm.userData.user["profil"]=Main.PROFIL_PERSO;
          if(vm.navParams.get("profil")==="pro")vm.userData.user.profil=Main.PROFIL_PROPRIVE;
          if(vm.navParams.get("profil")==="public")vm.userData.user.profil=Main.PROFIL_PROPUBLIC;
          vm.userData.senduser("profil").subscribe((resp2) => {
            l.dismiss();
            vm.nextScreen(vm.userData.user);
            return;
          },(error)=>{l.dismiss();return;});
        } else {
          if(l!=null)l.dismiss();
          toast(this.toastCtrl,"ERROR.RETRY",this.translate);
          Main.$$("Error : "+token_name+" ne retourne pas de token");
        }
      });
    });
  };



  createTempUser(){
    Main.$$("Create Temp user");
    var vm=this;
    //var obj={device: this.plt.userAgent(), time: new Date().getTime(), lang: this.plt.lang(),app_name:this.settings.app_name};
    var obj={device: this.plt.userAgent(), time: new Date().getTime(), lang: "fr",app_name:this.settings.app_name};
    this.userData.add(obj, "temp")
      .subscribe((temp_user:any)  => { //Ajoute un user temporaire juste pour récupérer le token
        Main.$$("Temp user created");
        vm.userData.setId(temp_user.id);
        vm.temp_user=temp_user;
        vm.events.publish("platform:ready");
      },
      err => {
          setTimeout(()=>{
            Main.$$("En attente de connexion");
            Main.toast(vm.toastCtrl,"En attente de connexion");
            vm.createTempUser();
            return;
          },5000);
      });
  }

  loginTest(){
    this.userData.login("hhoareau@gmail.com","4271").subscribe((res) =>{
      this.navCtrl.push(SeleventPage,{user:res});
    });
  }

  anonymousPrompt(flyer:string="",autologin=0){
    openModal(this.modalCtrl,LoginavatarPage,{autologin:autologin,pseudo:true,flyer:flyer,message:"INFO.ANONYMOUSLIMIT"},(avatar)=>{
      if(avatar!=null)
        this.userData.add({picture:avatar.picture,pseudo: avatar.pseudo,computer_id: "computer",lang: this.plt.lang()}, "anonymous").subscribe(res => {
          if(res)this.nextScreen(res);
        });
    });
  }


  openShifumixWebSite() {
    openWindow("http://shifumix.com");
  }
}



