import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {TranslateService} from "@ngx-translate/core";

/**
 * Generated class for the ShifubuttongroupComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
  selector: 'shifubuttongroup',
  templateUrl: 'shifubuttongroup.html'
})
export class ShifubuttongroupComponent implements OnInit {

  @Output('onchange') onchange: EventEmitter<any>=new EventEmitter();
  @Input('size') size: string="80px";
  @Input('fontsize') fontsize: string="normal";

  @Input('labels') labels: string=null;
  @Input('images') images: string=null;

  @Input('exclude') exclude: boolean=false;
  @Input('values') values: string="";
  private _default:string="";
  buttons:any[]=[];
  realValues=[];

  constructor(public translate:TranslateService) {

  }

  get default(): string {
    return this._default;
  }

  @Input("default")
  set default(val: string) {
    this._default=val;
    this.refresh();
  }

  refresh() {
      if(this.labels!=null && this.labels.length>0 && this.labels[this.labels.length-1]==",")this.labels=this.labels.substr(0,this.labels.length-1);

      this.buttons=[];

    let size=0;
    if(this.images==null){
        if(this.labels.length>0)
          size=this.labels.split(",").length;
    }
    else{
        if(this.images.length>0)
          size=this.images.split(",").length;
    }


    for(let i=0;i<size;i++){
      var selected=this._default.indexOf(this.values.split(",")[i])>-1;
      var bt:any={
          value:this.values.split(",")[i],
          selected:selected,
          borderstyle:"none",
          opacity:"0.5"
      };

      if(this.labels!=null)bt.label=this.translate.instant(this.labels.split(",")[i]);
      if(this.images!=null){
        bt.src=this.images.split(",")[i];
        if(selected){
          bt.borderstyle="solid";
          bt.opacity="1";
        }
      }

      this.buttons.push(bt);
    }
  }

  ngOnInit(): void {
    //Transfert les valeurs transmise dans des tableaux
    this.refresh();
  }


  switch(bt){
    if(this.exclude){
      this.buttons.forEach((b)=>{b.selected=false});
    }
    bt.selected=!bt.selected;
    var rc="";
    this.buttons.forEach((bt)=>{
      if(bt.selected)rc=rc+bt.value+",";
    });
    if(rc.endsWith(","))rc=rc.substr(0,rc.length-1);
    this._default=rc;
    this.refresh();
    this.onchange.emit({value:rc});
  }

}



