import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {TranslateService} from "@ngx-translate/core";
import {openHelp} from "../../app/Maintools";

/**
 * Generated class for the ShifucardComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
  selector: 'shifucard',
  templateUrl: 'shifucard.html'
})
export class ShifucardComponent {

  @Input("visible") visible:boolean=false;
  @Input("label") label:string="";
  @Input('showButton') showButton: boolean=true;
  @Input("icon") icon:string="";
  @Input("tuto") tuto:string="";
  @Input("id") id:string="";
  @Input("help") help:string="";
  @Input("title") title:string="";
  @Input("color") color:string="primary";
  @Output('onclick') onclick: EventEmitter<any>=new EventEmitter();

  click(){
    this.visible=true;
    if(this.onclick!=undefined)this.onclick.emit();
  }

  flipVisibility(){
    this.visible=!this.visible;
  }

  onHelp(){
    openHelp(this.help);
  }

  constructor(public translate:TranslateService) {
  }

}



