import {Component, ElementRef, ViewChild} from '@angular/core';
import {FabContainer, NavParams, ToastController, ViewController} from 'ionic-angular';
import {IonicPage, LoadingController, ModalController} from 'ionic-angular';
import * as Tools from "../../app/Maintools"
import {EventDataProvider} from "../../providers/event-data/event-data";
import {UserData} from "../../providers/user-data";
import {AddeventModelePage} from "../addevent-modele/addevent-modele";
import {AddeventAdvancedPage} from "../addevent-advanced/addevent-advanced";
import {AddeventDatePage} from "../addevent-date/addevent-date";
import {AddeventFlyerPage} from "../addevent-flyer/addevent-flyer";
import {MusicserverPage} from "../musicserver/musicserver";
import {$$, getDelay, join, loading, openModal, toast} from "../../app/Maintools";
import {TranslateService} from "@ngx-translate/core";
import {ImageCreatorPage} from "../image-creator/image-creator";
import {ApiProvider} from "../../providers/api/api";
import {SettingsProvider} from "../../providers/settings/settings";
declare const google: any;

/**
 * Generated class for the AddeventPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-addevent',
  templateUrl: 'addevent.html',
})
export class AddeventPage {

  @ViewChild('mapCanvas') mapElement: ElementRef;
  newevent:any={address:"",option:{title: true, date: true, address: true, teaser: true}};
  target:any;
  map:any;
  showTypeEvent:boolean=true;
  activities:string="Music,Photos,Messages,Sondages,Paris,Loterie,Presentation";
  showAddEventButton:boolean=true;
  delay:string="";
  defaultActivities:string="";
  defaultTypeOfEvent:string="";
  delayToStart:number=0;
  @ViewChild("fab") fab:FabContainer;

  constructor(public modalCtrl: ModalController,public translate:TranslateService,
              public viewCtrl:ViewController,public loadingCtrl:LoadingController,
              public navParams: NavParams,public eventData:EventDataProvider,
              public toastCtrl:ToastController,public api:ApiProvider,public settings:SettingsProvider,
              public userData:UserData) {
  }

  ionViewDidLoad() {
    let zoom=this.navParams.get("zoom");
    if(zoom==null)zoom=10;
    if(zoom<15)zoom=15;

    this.userData.initdefaultevent(
      this.settings.version,
      this.navParams.get("lat"),
      this.navParams.get("lng"),
      this.navParams.get("precision")
    ).subscribe((r:any) => {

      r.option={title: true, date: true, address: true, teaser: true};

      this.newevent = r;
      this.updateCategorie(this.defaultTypeOfEvent);
      this.defaultActivities=join(this.newevent.activities,",");

      if(this.navParams.get("modele")!=null)this.newevent=this.navParams.get("modele");

      this.newevent.duration=this.newevent.dtEnd-this.newevent.dtStart;
      this.newevent.type="";

      this.computeAddress(r);
      this.refreshDelay();
      this.initMap(r.lat,r.lng,zoom);
    });

    if(this.settings.ihm=="pro")
      this.defaultTypeOfEvent="meeting";
    else
      this.defaultTypeOfEvent="";
  }


  computeAddress(evt=null,delay=0){
    if(this.userData.user.email.length>0)
      setTimeout(()=>{
        if(evt==null)evt=this.newevent;
        Tools.reverse_geocode(google.maps,evt.lat, evt.lng, (addr) => {
          this.newevent.address = addr;
          this.refreshFlyer();
        });
      },delay*1000)
    else{
      this.newevent.address="";
      this.newevent.option.address=false;
      this.refreshFlyer();
    }
  }


  initTarget(){
    var vm=this;
    var pos=vm.map.getCenter();
    if(vm.target!=null){
      vm.target.setPosition(pos);
    }else{
      vm.target = new google.maps.Marker({
        icon:"./assets/img/party.png",
        position: pos,
        name: "Soiree",
        draggable: true,
        map:vm.map
      });
    }
  }

  refreshDelay(){
    if(this.newevent.dtStart-new Date().getTime()>20000)
      this.delay=getDelay(this.newevent.dtStart,this.userData.user.lang);
    else
      this.delay="";
  }

  /**
   * Initialise la carte google map avec un léger décalage
   * @param {number} lat
   * @param {number} lng
   * @param {number} zoom
   * @param {any} func
   */
  initMap(lat:number,lng:number,zoom:number,func=null){
    let mapEle = this.mapElement.nativeElement;
    let vm=this;

    vm.map = new google.maps.Map(mapEle, {center: {lat:lat,lng:lng} ,zoom: zoom});

    vm.map.addListener("mousedown",function(evt){vm.newevent.address="";});
    vm.map.addListener("mouseup",function(evt){
      var pos=this.getCenter();
      vm.newevent.lat=pos.lat();
      vm.newevent.lng=pos.lng();
      Tools.reverse_geocode(google.maps,pos.lat(),pos.lng(),function(addr){
        vm.newevent.address=addr;
        vm.refreshFlyer();
      });
    });

    vm.map.addListener("bounds_changed",function(evt){
      vm.initTarget();
      var pos=vm.map.getCenter();
      if(pos!=undefined){
        vm.newevent.lat=pos.lat();
        vm.newevent.lng=pos.lng();
      }
    });

    vm.map.setOptions({
      disableDefaultUI:true,
      rotateControl:false,
      clickableIcons:false
    });

    vm.initTarget();
    setTimeout(()=>{
      if(func!=null)func();
    },500);
  }

  searchAddress(code){
    var vm=this;
    if(code==13){
      var address=this.newevent.address;
      if(address!=null && address.length>0){
        Tools.geocode(google.maps,address,function(pos){
          vm.map.setCenter(pos);
          vm.newevent.address=address;
          vm.refreshFlyer();
        });
      }
    }
  }

  refreshFlyer(func=null){
    if(this.newevent.templateFlyer==null){
      if(this.newevent.activities.indexOf("bets")>-1)this.newevent.templateFlyer="bets";
      if(this.newevent.activities.indexOf("photo")>-1)this.newevent.templateFlyer="photo";
      if(this.newevent.activities.indexOf("music")>-1)this.newevent.templateFlyer="music";
      if(this.newevent.type=="meeting")this.newevent.templateFlyer="pro";
    }

    this.userData.makeFlyer(this.newevent,this.newevent.option.title,this.newevent.option.address,this.newevent.option.date,this.newevent.option.teaser,"white",this.newevent.templateFlyer).subscribe((resp:any) => {
      this.newevent.flyer=resp.photo;
      this.newevent.templateFlyer=resp.text;
      if(func!=null)func();
    });
  };


  quitAddEvent(bCreateEvent:boolean=true){
    var vm=this;
    if(bCreateEvent){
      var l=loading(this,this.translate.instant("ADDEVENT.BUILDINGYOUREVENT"),20);
      vm.eventData.add(vm.userData.user.id,vm.newevent).subscribe((resp:any) => {
        this.userData.user=resp;
        toast(vm.toastCtrl,resp.message,vm.translate);
        if(resp.message==""){
          setTimeout(()=>{l.dismiss();vm.viewCtrl.dismiss(vm.newevent);},100);
        }
        else
          vm.viewCtrl.dismiss();
      });
    }
    else
      vm.viewCtrl.dismiss();
  }

  /**
   * Fabrique l'événement
   */
  createEvent(){
    var vm=this;
    this.showAddEventButton=false;
    vm.refreshFlyer(()=>{
      var photo = {from: this.userData.user, idEvent:this.newevent.id, photo: this.newevent.flyer, format: "jpeg"};
      this.userData.sendflyer(photo).subscribe((resp:any) => {
        vm.newevent.flyer= resp.url;
        //Si l'option music est active on demande la plateforme
        if (vm.newevent.activities.indexOf("music")>-1 && vm.userData.user.musicServer==undefined){
          openModal(this.modalCtrl, MusicserverPage, {message:""}, (rep) => {
            if(rep.user==null)
              vm.newevent.activities.splice(vm.newevent.activities.indexOf("music"),1);
            vm.quitAddEvent(true);
          });
        } else
          this.quitAddEvent(true);

      },(err) => {
        toast(vm.toastCtrl,"ERROR.GENERAL",vm.translate);
        vm.quitAddEvent(false);
      });
    });
  };

  addEvent_advanced(){
    this.fab.close();
    Tools.openModal(this.modalCtrl,AddeventAdvancedPage,{ newevent:this.newevent},data => {
      if(data!=null){
        this.newevent=data;
        this.defaultActivities=join(data.activities,",");
      }
    });
  };

  addEvent_flyer(){
    this.fab.close();
    Tools.openModal(this.modalCtrl,AddeventFlyerPage,{ newevent:this.newevent},data => {if(data!=null)this.newevent=data;})
  };


  addEvent_date(){Tools.openModal(this.modalCtrl,AddeventDatePage,{ newevent:this.newevent},data => {
    this.fab.close();
    if(data!=null)this.newevent=data;
      this.refreshDelay();
      this.refreshFlyer();
    }
  )};


  openModele(){
    this.fab.close();
    Tools.openModal(this.modalCtrl,AddeventModelePage,{ newevent:this.newevent},(data:any) => {
      if(data!=null){
        this.showTypeEvent=false;
        if(data.type=="basic"){
          this.newevent.type=data.type;
          this.newevent.templateFlyer=data.templateFlyer;
          this.newevent.activities=[];
          data.activities.split(",").forEach((a)=>{this.newevent.activities.push(a);});
          this.newevent.title=data.title;
          this.newevent.autologin=data.autologin;
          this.newevent.musicserver=data.musicserver;
          if(data.duration==null)data.duration=8*60;
          this.newevent.dtStart=Number(this.newevent.dtStart);
          this.defaultActivities=data.activities;
          this.newevent.dtEnd=this.newevent.dtStart+Number(data.duration*60000);
        } else {
          this.defaultActivities=data.activities.join(",");
          this.newevent=data;
        }

        this.searchAddress(13);
        this.refreshDelay();
        this.refreshFlyer();
        $$("newevent=",this.newevent);
      }
    });
  }

  /**
   *
   */
  changeDefaultAddress(){
    this.userData.user.defaultAddress=this.newevent.address;
    this.userData.senduser("defaultAddress",this.userData.user).subscribe( (rep) =>{
      this.searchAddress(13)
    });
  }


  /**
   *
   */
  selImage(){
    var vm=this;
    openModal(this.modalCtrl,ImageCreatorPage,{ratio:3/2,upload:true},rep => {
      if(rep!=null) {
        vm.newevent.templateFlyer = rep.value;
        vm.refreshFlyer();
      }
    })
  }

  refreshTemplateFlyer() {
    this.newevent.templateFlyer=null;
    this.refreshFlyer();
  }

  updateAct(e:any){
    this.newevent.activities=e.value.split(",");
  }

  updateCategorie(val: any) {
    this.newevent.type=val.value;
        if(val.value=="meeting"){
            this.newevent.title=this.userData.user.firstname+"' meeting";
            this.newevent.activities=["presentation","message","survey"];
        }
        else{
            this.newevent.title=this.userData.user.firstname+"' party";
            this.newevent.activities=["music","photo","message","survey"];
        }
    this.newevent.templateFlyer=null;
    this.refreshFlyer();
  }


}



