import { Component } from '@angular/core';
import {
  AlertController, IonicPage, LoadingController, ModalController, NavController, NavParams,
  ViewController
} from 'ionic-angular';
import {EventDataProvider} from "../../providers/event-data/event-data";
import {TranslateService} from "@ngx-translate/core";

/**
 * Generated class for the WholikemePage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-wholikeme',
  templateUrl: 'wholikeme.html',
})
export class WholikemePage {

  target:any={};
  users:any[]=[];

  constructor(public navCtrl: NavController, public navParams: NavParams,public viewCtrl:ViewController,
              public translate:TranslateService,public loadingCtrl:LoadingController,
              public modalCtrl:ModalController,public alertCtrl: AlertController,
              public eventData:EventDataProvider) {
  }

  ionViewDidLoad() {
    this.target=this.navParams.get("target");
    this.eventData.getuserslikeme(this.target.id).subscribe((r:any)=>{
      if(r.items!=undefined) {
        this.users = [];
        r.items.forEach(it => {
          it.total = it.nbDislike - it.nbLike; //Permet le classement inversé
          if(it.nbDislike>0 || it.nbLike>0)
            this.users.push(it);
        })
      }else
        this.viewCtrl.dismiss();
    });
  }

}



