import { Component } from '@angular/core';
import {
  AlertController, IonicPage, ModalController, NavController, NavParams, ToastController,
  ViewController
} from 'ionic-angular';
import * as Tools from "../../app/Maintools"
import {UserData} from "../../providers/user-data";
import {ApiProvider} from "../../providers/api/api";
import {EventDataProvider} from "../../providers/event-data/event-data";
import {openModal, showPopup, toast} from "../../app/Maintools";
import {TranslateService} from "@ngx-translate/core";
import {PersoPage} from "../perso/perso";

/**
 * Parametres : userid,reader
 */
@IonicPage()
@Component({
  selector: 'page-public-profil',
  templateUrl: 'public-profil.html',
})
export class PublicProfilPage {

  old_events:any[]=[];
  next_events:any[]=[];
  follow:boolean=false;
  reader:string=null;
  roles=[false,false,false,false];

  photos:any[]=[];
  jetons:number=0;
  reader_jetons:number=0;

  user:any={id:"",firstname:"",picture:"",score:0}; //celui qui lit le profil

  constructor(public api:ApiProvider,public eventData:EventDataProvider,public userData:UserData,public translate:TranslateService,
              public viewCtrl:ViewController,public navCtrl: NavController,public toastCtrl:ToastController,public modalCtrl:ModalController,
              public navParams: NavParams,public alertCtrl:AlertController) {
    this.refresh();
  }

  refresh(){
    var vm=this;

    this.reader=vm.navParams.get("follower");
    let target=vm.navParams.get("userid"); //la target


    if(this.reader!=null)
      this.api.get(this.reader).subscribe(rep=> {
        if(rep!=null && rep.jetons!=undefined)
            this.reader_jetons=rep.jetons[target];

        if(vm.eventData.event!=undefined){
          vm.roles[1]=vm.eventData.event.Moderators.indexOf(target)>-1;
          vm.roles[2]=vm.eventData.event.Cashiers.indexOf(target)>-1;
          vm.roles[3]=vm.eventData.event.Preparators.indexOf(target)>-1;
        }

      });

    this.api.get(target).subscribe(rep=> {
      vm.user = rep;

      if(rep.jetons!=undefined && rep.jetons[vm.reader]!=undefined)
        vm.jetons=rep.jetons[vm.reader];

      if(vm.user.readers==undefined)vm.user.readers=[];
      if(vm.user.followers!=undefined)vm.follow=vm.user.followers.indexOf(vm.reader)>-1;

      vm.api.getphotofromuser(this.user.id).subscribe(function (resp) {
        vm.photos = resp.items;
      });

      vm.api.geteventsfrom(this.user.id).subscribe(function (evts) {
        evts.items.forEach(function(evt){
          var now=new Date().getTime();
          if(evt.dtEnd<now)vm.old_events.push(evt);
          if(evt.dtStart>now)vm.next_events.push(evt);
        });
      });
    });
  }

  updaterole(role:number){
    this.eventData.updaterole(this.user.id,role,!this.roles[role]).subscribe((r)=>{
      if(r!=null){
        this.eventData.event=r;
        toast(this.toastCtrl,this.user.firstname+" "+this.translate.instant("LIB.UPDATE"));
        this.refresh();
      }
    });
  }

  openWebsite=function(user){
    Tools.openWindow(user.home,user.firstname+" website");
  };

  // joinEvent(evt){
  //   if(evt.dtEnd<this.now)
  //     this.viewCtrl.dismiss({event:evt.id});
  //   else
  //     this.viewCtrl.dismiss({event:evt.id});
  // };

  updateFollow(){

    if(this.userData.user.email.length==0){
      toast(this.toastCtrl,"PUBLICPROFIL.GIVEEMAILTOFOLLOW",this.translate);
      setTimeout(()=>{
        openModal(this.modalCtrl,PersoPage,{},()=>{
          if(this.userData.user.email.length>0)
            this.updateFollow();
          else
            this.follow=false;
        })
      },2000);
    }

    if(!this.follow)
      this.api.delFollower(this.user.id,this.reader).subscribe(rep => {});
    else
      this.api.addFollower(this.user.id,this.reader).subscribe(rep => {});;
  };

  addBlacklist(){

    this.api.blacklist(this.navParams.get("follower"),this.navParams.get("userid"),true).subscribe(r=>{
      if(r!=null)
        toast(this.toastCtrl,this.user.firstname+" "+this.translate.instant("PUBLICPROFIL.ADDTOBLACKLIST"));
    });
  }

  chargeCredits(u,credit:boolean=true){
    var vm=this;
    var title="LIB.CREDITS";
    if(!credit)title="LIB.DEBITS";

    showPopup({title: title,confirmButton: "LIB.OK",cancelButton: "LIB.CANCEL",type: "number",translate:this.translate},
      this.alertCtrl,
      (res) => {
        if(!credit)res=-res;
        if(res!=null)
          vm.api.chargecredits(vm.eventData.event.owner_id,u.id,res).subscribe(r=>{
            vm.refresh();
          });
      }
    )
  }

}



