import {Component, ElementRef, Input, ViewChild, OnInit, OnChanges, SimpleChanges} from '@angular/core';
import {DomSanitizer} from "@angular/platform-browser";

/**
 * Generated class for the SvgeditComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
  selector: 'svgedit',
  templateUrl: 'svgedit.html'
})
export class SvgeditComponent implements OnChanges {

  @Input("code") code: string="";
  @Input("shadow") shadow: boolean=false;
  @Input("width") width: string="150px";
  @Input("height") height: string="225px";
  @ViewChild('flyer') flyer: ElementRef;

  constructor(private sanitizer: DomSanitizer) {

  }

  ngOnChanges(changes: SimpleChanges): void {
    this.flyer.nativeElement.innerHTML = this.code;
    if(this.shadow)this.flyer.nativeElement.className="flyer-shadow";
  }


}




