import {Component, EventEmitter, forwardRef, Input, OnInit, Output} from '@angular/core';
import {ControlValueAccessor, NG_VALUE_ACCESSOR} from "@angular/forms";
import {noop} from "rxjs/util/noop";
import {TranslateService} from "@ngx-translate/core";


export const CUSTOM_INPUT_CONTROL_VALUE_ACCESSOR: any = {
  provide: NG_VALUE_ACCESSOR,
  useExisting: forwardRef(() => ShifurangeComponent ),
  multi: true
};


@Component({
  selector: 'shifurange',
  templateUrl: 'shifurange.html',
  providers: [CUSTOM_INPUT_CONTROL_VALUE_ACCESSOR]
})
export class ShifurangeComponent implements OnInit,ControlValueAccessor  {

  @Output('onchange') onchange: EventEmitter<any>=new EventEmitter();
  @Output('onmove') onmove: EventEmitter<any>=new EventEmitter();
  @Input("label") label:string="";
  @Input("min") min:number=0;
  //@Input("elt") elt:string="";
  @Input("icon") icon:string="";
  @Input("max") max:number=100;
  @Input("step") step:number=1;
  @Input("unite") unite:string="";

  private innerValue: any = '';
  private handle:any=null;

  //Placeholders for the callbacks which are later provided
  //by the Control Value Accessor
  private onTouchedCallback: () => void = noop;
  private onChangeCallback: (_: any) => void = noop;

  //get accessor
  get value(): any {
    return this.innerValue;
  };

  //set accessor including call the onchange callback
  set value(v: any) {
    if (v !== this.innerValue) {
      this.innerValue = v;
      this.onChangeCallback(v);
      this.onChangeValue();
    }
  }

  changeValue(score){
    this.writeValue(this.innerValue+score);
    this.onChangeValue();
  }

  onChangeValue(){
    var vm=this;
    vm.onmove.emit();
    if(this.handle!=null){
      clearTimeout(this.handle);
      this.handle=null;
    }
    this.handle=setTimeout(function(){
      vm.onchange.emit({value:vm.value});
    },1000);
  }

  constructor(public translate:TranslateService) {
  }

  writeValue(obj: any): void {
    if (obj !== this.innerValue) {
      this.innerValue = obj;
    }
  }

  registerOnChange(fn: any): void {this.onChangeCallback = fn;}
  registerOnTouched(fn: any): void {this.onTouchedCallback = fn; }


  ngOnInit(): void {
    if(this.label.indexOf(".")>-1)
      this.label=this.translate.instant(this.label);
  }


}



