import {Component, ViewChild} from '@angular/core';
import {Events, NavParams, Tabs} from 'ionic-angular';
import * as Tools from "../../app/Maintools"
import {HomePage} from "../home/home";
import {PhotosPage} from "../photos/photos";
import {BetsPage} from "../bets/bets";
import {CustomercommandPage} from "../customercommand/customercommand";
import {EventDataProvider} from "../../providers/event-data/event-data";
import {UserData} from "../../providers/user-data";
import {MessagesPage} from "../messages/messages";
import {ChartsPage} from "../charts/charts";
import {$$, buildTabs, getDelay, iif} from "../../app/Maintools";
import {SettingsProvider} from "../../providers/settings/settings";

@Component({
  templateUrl: 'tabs-page.html'
})
export class TabsPage {

  @ViewChild('myTabs') tabRef: Tabs;
  tabs:any[]=[];
  myindex:number=0;

  constructor(
      public events:Events,
      public navParams: NavParams,
      public settings:SettingsProvider,
      public eventData:EventDataProvider,
      public userData:UserData) {
    this.refresh();
  }

  refresh(){
    $$("Refresh des onglets");
    var vm=this;
    vm.tabs=[];
    this.tabs=buildTabs(
        this.userData.user,
        this.eventData.event,
        iif(this.settings.ihm=="pro","pro",""));
    for(let i=0;i<this.tabs.length;i++){
      if(this.userData.user.connexions.length>5)this.tabs[i].tabTitle="";
      this.tabs[i].shifuid=this.tabs[i].name;
      if(this.tabs[i].name==vm.navParams.data.tabName)this.myindex=i;
    }
  }

  changeTab(){
    this.userData.selectTab=this.tabRef.getSelected().root;
    if(this.eventData.event.blacklist!=undefined && this.eventData.event.blacklist.indexOf(this.userData.user.id)>-1)
      this.events.publish("event:logout",{message:"SELEVENT.BLACKLIST"});
  }

}



