import {Component, ElementRef, ViewChild} from '@angular/core';
import {AlertController, IonicPage, LoadingController} from 'ionic-angular';
import {NavController, NavParams, ViewController, ToastController} from 'ionic-angular';
import {$$, autoRotate, createPhoto, cropBase64Img} from "../../app/Maintools";
import {loading} from "../../app/Maintools";
import {openGeneral,resizeBase64Img, rotate,showPopup, toast} from "../../app/Maintools";
import {UserData} from "../../providers/user-data";
import {TranslateService} from "@ngx-translate/core";
import {ApiProvider} from "../../providers/api/api";
import {SettingsProvider} from "../../providers/settings/settings";



@IonicPage()
@Component({
  selector: 'page-image-creator',
  templateUrl: 'image-creator.html',
})
export class ImageCreatorPage {

  images:any[]=[];
  valide=false;
  @ViewChild("cadre") cadre:ElementRef;
  img:any={src:""};
  imgHD:any={src:""};
  x:number=0;
  y:number=0;
  scale=1;
  width=0;
  height=0;
  ech=100;
  angle:number=0;
  lastdist:number=0;

  private _DRAGGGING_STARTED: number=0;
  private _LAST_MOUSE_POSITION = { x: null, y: null };

  constructor(public navCtrl: NavController, public navParams: NavParams,public api:ApiProvider,public toastCtrl:ToastController,
              public viewCtrl:ViewController,public userData:UserData,public loadingCtrl:LoadingController,
              public translate:TranslateService, public alertCtrl:AlertController,
              public settings:SettingsProvider) {
  }




  ionViewDidLoad() {
    var ratio=this.navParams.data.ratio;
    if(ratio==null)ratio=1;
    var h=(this.cadre.nativeElement.clientWidth*ratio);
    this.cadre.nativeElement.style.height=h+"px";
    this.cadre.nativeElement.style.width=this.cadre.nativeElement.clientWidth+"px";


    if(this.settings.lastImages.length>0)
      this.images=this.settings.lastImages;
    else{
        this.userData.getPreviousImages(20).subscribe((rep:any)=>{
            if(rep!=null){
                this.images=[];
                rep.items.forEach((img)=>{
                    if(img.title.indexOf(".svg")==-1)
                        this.images.push(img);
                })
            }
        });
    }
  }


  /**
   *
   * @param event
   */
  mousemove(event){
    event.preventDefault();
    var dist=0;
    var change_x = 0;
    var change_y = 0;
    if(event.touches!=undefined){

      if(event.touches.length==1){
        event.offsetX=event.touches[0].clientX;
        event.offsetY=event.touches[0].clientY;
      }

      if(event.touches.length==2){
        dist=Math.abs(event.touches[0].clientX-event.touches[1].clientX)+Math.abs(event.touches[0].clientY-event.touches[1].clientY);
      }

    }

    if (this._DRAGGGING_STARTED == 1) {
      var current_mouse_position = {x: event.offsetX, y: event.offsetY };
      change_x = current_mouse_position.x - this._LAST_MOUSE_POSITION.x;
      change_y = current_mouse_position.y - this._LAST_MOUSE_POSITION.y;

      /* Save mouse position */
      this._LAST_MOUSE_POSITION = current_mouse_position;

      if(event.touches!=undefined && event.touches.length==2) {
        if (dist > this.lastdist)
          this.mousewheel({wheelDelta: 1});
        else
          this.mousewheel({wheelDelta: -1});

        this.lastdist=dist;
      }else{
        if(event.target.parentElement!=null){
          var limitX=event.target.parentElement.clientWidth-event.target.clientWidth;
          var limitY=event.target.parentElement.clientHeight-event.target.clientHeight;
          if(limitY>0)limitY=0;
          if(limitX>0)limitX=0;
          if (this.x + change_x > 0) change_x = -this.x; //|| this.x-change_x<this.lacarte.nativeElement.width
          if (this.y + change_y > 0) change_y = -this.y; //|| this.y-change_y<this.lacarte.nativeElement.height
          if (this.x + change_x < limitX ) change_x = -this.x + limitX;
          if (this.y + change_y < limitY) change_y = -this.y + limitY;
        }

        if (change_x != 0 || change_y != 0) {
          this.y = this.y + change_y;
          this.x = this.x + change_x;
          this.img.style.top = this.y + "px";
          this.img.style.left = this.x + "px";
        }
      }
    }

  }

  mousewheel(event){
    if(this.img.src.length>0){
      if(event.wheelDelta>0)
        this.scale+=0.01;
      else{
        if(this.img.width+this.x<this.img.parentElement.clientWidth)return;
        if(this.img.height+this.y<this.img.parentElement.clientHeight)return;
        this.scale-=0.01;
      }
      this.ech=this.scale*100;

      this.img.style.width=(this.width*this.scale)+"px";
      this.img.style.height=(this.height*this.scale)+"px";
    }
  }

  mousedown(event){
    this._DRAGGGING_STARTED = 1;
    if(event.touches!=undefined){
      if(event.touches.length==1){
        event.offsetX=event.touches[0].clientX;
        event.offsetY=event.touches[0].clientY;
        this._LAST_MOUSE_POSITION = { x: event.offsetX, y: event.offsetY};
      } else {
        this.lastdist=Math.abs(event.touches[0].clientX-event.touches[1].clientX)+Math.abs(event.touches[0].clientY-event.touches[1].clientY);
      }
    }
  }

  mouseup(event){
    this._DRAGGGING_STARTED=0;
  }


  onChange(fileInput){
    var vm=this;
    if (fileInput.target.files && fileInput.target.files[0]) {
      var reader = new FileReader();

      reader.onload = function (e : any) {
        vm.loadImage(e.target.result);
      };

      reader.readAsDataURL(fileInput.target.files[0]);
    }
  }

  rotateImage(angle){
    var vm=this;
    this.angle=this.angle+angle;
    this.scale=1;
    this.x=0;
    this.y=0;
    rotate(this.img.src,this.angle,0.3,(resp) => {
      vm.img.src=resp;
    });
  }

  getMyCloudFile(){
    var vm=this;
    let loader=loading(vm);
    openGeneral("instagram",this.userData.user).then(()=>{
      vm.userData.getImagesFromMyCloud().subscribe((rep:any)=>{
        loader.dismiss();
        rep.items.forEach(function (p) {
          vm.images.push(p);
        });
      },(err) => {
        loader.dismiss();
      });
    });
  }

  imagePlatform(service) {
    var vm=this;
    vm.images=[];
    showPopup({
        title: "PHOTO.KEYWORDSEARCH",
        default:this.settings.lastSearchImage,
        confirmButton: "LIB.OK",
        cancelButton: "LIB.CANCEL",
        translate:this.translate,
        type: "text"
      },this.alertCtrl,
       (res)=> {
        if(res!=null){
          res=res.replace("\#","");
          let loader=loading(vm);
          vm.userData.getImageForFlyer(service, res,null,null).subscribe((resp:any)=>{
            loader.dismiss();
            if(resp.items.length==0){
                toast(vm.toastCtrl,"IMAGECREATOR.NOIMAGE",vm.translate);
            }

            else{
                resp.items.forEach(function (p) {
                    vm.images.push(p);
                });
                vm.settings.lastImages=vm.images;
                vm.settings.lastSearchImage=res;
            }

          },(err)=>{
            loader.dismiss();
          });
        }
      });
  };


  /**
   * Fonction principal appelé pour le chargement de la preview par toutes les sources
   * @param {string} src
   */
  loadImage(src:string,func:Function=null){
    this.imgHD.src=src;
    var vm=this;
    resizeBase64Img(src,null,0.3,function(src){
      vm.x=0;
      vm.y=0;
      vm.scale=1;
      if(vm.img.src.length>0){
        if(vm.img.parentElement==vm.cadre.nativeElement)
          vm.cadre.nativeElement.removeChild(vm.img);
      }

      vm.img=new Image();
      vm.img.onload=function(){
        vm.valide=true;
        vm.width=this.width;
        vm.height=this.height;
        vm.scale=Math.max(vm.cadre.nativeElement.clientWidth/this.width,vm.cadre.nativeElement.clientHeight/this.height);
        vm.ech=vm.scale*100;

        this.style.maxWidth="10000px";
        this.style.maxHeight="10000px";

        var lx=this.width*vm.scale;
        var ly=this.height*vm.scale;
        this.style.width=lx+"px";
        this.style.height=ly+"px";

        vm.cadre.nativeElement.appendChild(this);
        vm.cadre.nativeElement.style.visibility="visible";
        this.style.position="relative";


        //Permet un cadrage initial sur le centre de l'image
        var x=(vm.cadre.nativeElement.clientWidth-lx)/4;
        var y=(vm.cadre.nativeElement.clientHeight-ly)/4;
        //this.style.left=vm.x+"px";
        //this.style.top=vm.y+"px"

        vm.mousedown({});
        vm.mousemove({preventDefault:()=>{},target:this,offsetX:x,offsetY:y,touches:[]});
        vm.mouseup({});

        if(func!=null)func();
      };

      if(src.indexOf("http")==0){
        vm.api.uploadimage(true,createPhoto(vm.userData.user,src)).subscribe(resp => {
          vm.img.crossOrigin="anonymous";
          vm.img.src=resp.content;
          if(func!=null)func();
        },(err) => {
          $$("erreur d'upload"+err);
          func();
        });
      }
      else
        vm.img.src=src;
    });
  }



  addLink(){
    var vm=this;
    showPopup({
        title: "LIB.WEBLINK",
        confirmButton: "LIB.OK",
        cancelButton: "LIB.CANCEL",
        translate:this.translate,
        type: "text"
      },this.alertCtrl,
      function (res) {
        vm.api.uploadimage(true,createPhoto(vm.userData.user,res)).subscribe(
          res => {
            if(res==null)
              toast(vm.toastCtrl,"ERROR.BADFORMAT",vm.translate);
            else
              vm.loadImage(res.content);
          }
        );
      });
  }


  //Fonction de récupération de l'image
  selImage(img){
    this.images=[];
    var l=loading(this,this.translate.instant("LIB.UPLOADING"));
    this.api.uploadimage(true,createPhoto(this.userData.user,img.photo)).subscribe(
      res => {
        this.loadImage(res.content,()=>{
          l.dismiss();
        });
      }
    ,(err)=>{
        toast(this.toastCtrl,"LIB.ERRORRETRY",this.translate);
        l.dismiss();
      });
  }

  getPhoto(event,withText=false,instantpost=true,func=undefined) {
    var vm=this;
    let l=loading(this);
    setTimeout(()=>{
      autoRotate(event.value,1.0,(res) => {
        vm.loadImage(res,()=>{
          l.dismiss();
        });
      });
    },100);
  }

  close(){
    var vm=this;
    this.img.crossOrigin="anonymous";
    var s=this.img.src;

    var quality=1e6/s.length;
    if(quality>1)quality=1;

    var x=-this.x/this.scale;
    var y=-this.y/this.scale;
    var lx=Math.min(this.cadre.nativeElement.offsetWidth/this.scale,this.img.width/this.scale);
    var ly=Math.min(this.cadre.nativeElement.offsetHeight/this.scale,this.img.height/this.scale);

    //if(getEnv()==0)s=s.replace("http://localhost:8080","");

    //On repart de la source
    let loader=loading(this);
    setTimeout(()=>{
      rotate(this.imgHD.src,this.angle,1.0,res => {
        cropBase64Img(res,
          x,y,lx,ly,
          0.5,function(resp){
            if(vm.navParams.get("upload")){
              var p=createPhoto(vm.userData.user,resp);
              vm.api.uploadimage(false,p).subscribe((resp:any) => {
                loader.dismiss();

                vm.viewCtrl.dismiss({value:resp.url});
              },(err) => {
                toast(vm.toastCtrl,"LIB.ERRORRETRY",vm.translate);
                loader.dismiss();
                vm.viewCtrl.dismiss();
              });
            }
            else {
              loader.dismiss();
              vm.viewCtrl.dismiss({value:resp})
            }
          },r=>{
            toast(vm.toastCtrl,"LIB.ERRORRETRY",vm.translate);
            loader.dismiss();
            vm.viewCtrl.dismiss();
          }
        );
      });
    },100);
  }

}



