import { Component } from '@angular/core';
import {AlertController, Events, IonicPage} from 'ionic-angular';
import {ModalController, NavController, NavParams,ToastController} from 'ionic-angular';
import {UserData} from "../../providers/user-data";
import {EventDataProvider} from "../../providers/event-data/event-data";
import {openModal, toast} from "../../app/Maintools";
import {InvitePage} from "../invite/invite";
import {ImageCreatorPage} from "../image-creator/image-creator";
import {TranslateService} from "@ngx-translate/core";
import {ClosedeventPage} from "../closedevent/closedevent";
import {PresentationsPage} from "../presentations/presentations";

/**
 * Generated class for the ProfilPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-profil',
  templateUrl: 'profil.html',
})
export class ProfilPage {

  streamingDevices=[];
  autologin:any={value:false};

  constructor(public navCtrl: NavController,
              public navParams: NavParams,
              public events: Events,
              public translate:TranslateService,
              public modalCtrl:ModalController,
              public toastCtrl:ToastController,
              public alertCtrl:AlertController,
              public eventData:EventDataProvider,
              public userData:UserData) {

    this.autologin.value=(this.eventData.event.autologin==2);

  }

  selImage(){
    var vm=this;
    openModal(this.modalCtrl,ImageCreatorPage,{ratio:3/2,upload:true},rep => {
      if(rep!=null) {
        vm.eventData.event.welcomePicture = rep.value;
      }
    })
  }

  openInvite(){
    openModal(this.modalCtrl,InvitePage);
  }

  ionViewWillLeave(){
    this.updateEvent();
  }

  upgradeEvent(){
    this.userData.upgradeTarif().subscribe((rep:any)=>{
      toast(this.toastCtrl,rep.message,this.translate);
    })
  }

  updateEvent(){
    this.eventData.event.autologin=0;
    if(this.autologin.value)this.eventData.event.autologin=2;

    this.eventData.updateevent("title,autologin,allowAnonymous,visibleOnMap,startJetons,welcomePicture,welcomeDuration,selfservice,loc_query,hTag,needValidate,minCreditToJoin,minDistance,minScoreToJoin,maxOnline,linkToOwner,laCarte,selfservice").subscribe((rep) => {
      if(rep!=null)this.userData.user=rep;
    });
  }

  openEventSum() {
    openModal(this.modalCtrl,ClosedeventPage,{event:this.eventData.event});
  }

  openPresentations() {
    openModal(this.modalCtrl,PresentationsPage,{});
  }
}



