import { Component } from '@angular/core';
import {ModalController, NavController, NavParams,ToastController} from 'ionic-angular';
import {Events, IonicPage, LoadingController} from 'ionic-angular';
import {UserData} from "../../providers/user-data";
import { AlertController } from 'ionic-angular';
import * as Tools from "../../app/Maintools"
import {TranslateService} from "@ngx-translate/core";
import {WidgetstorePage} from "../widgetstore/widgetstore";
import {$$, askforaddscreen, DOMAIN_SERVER, loading, openWindow, showPopup, tirage,toast} from "../../app/Maintools";
import {EventDataProvider} from "../../providers/event-data/event-data";

/**
 * Generated class for the ScreensPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-screens',
  templateUrl: 'screens.html',
})
export class ScreensPage {

  screens:any[] = [];
  handle:any=null;
  showAddEvent=false;
  l:any=null;

  constructor(public events:Events,public navCtrl: NavController,public loadingCtrl:LoadingController,
              public navParams: NavParams, public userData: UserData,public eventData:EventDataProvider,
              public modalCtrl:ModalController,public toastCtrl:ToastController,
              public alertCtrl: AlertController,public translate:TranslateService) {
  }

  ionViewDidLoad() {
    var vm=this;

    this.events.subscribe("screen", () => {this.refresh()});
    this.events.subscribe("user", () => {this.refresh()});
    //this.handle = setInterval(function () {vm.refresh();}, 10000);

    if(this.navParams.get("code") !=null) {
      vm.searchScreen(this.navParams.get("code"),this.navParams.get("widget"),()=>{
        setTimeout(()=>{
          if(vm.eventData.event.id==undefined){
            $$("Demande de connexion par l'écran screen");
            vm.events.publish("user:login",this.userData.user);
          }

        },1000);
      });
    }



    if(this.navParams.get("widget") !=null) {
      Tools.showPopup({
        subtitle:"S-######",
        placeholder:"######",
          title: "PROFIL.ENTERCODE",
          confirmButton: "LIB.OK",
          cancelButton: "LIB.CANCEL",
          type: "number",
          translate:this.translate},this.alertCtrl,
        (res) => {
          if(res!=null){
            this.searchScreen(res,this.navParams.get("widget"));
          }
        }
      );
    }

    this.refresh();


  }

  ionViewWillLeave(){
    clearInterval(this.handle);
  }


  searchScreen(code:string,widget:string="",func:Function=null){
    var vm=this;
    vm.userData.user.code = code;
    vm.userData.user.widgetToAdd = widget;
    var l=loading(this);
    vm.userData.senduser("code,widgetToAdd").subscribe(() => {
      l.dismiss();
      //TODO: code à optimiser pour avoir un traitement synchrone (voir la fonction addwidgettoscreen
      setTimeout(()=>{vm.refresh();if(func!=null)func();},3000);
      setTimeout(()=>{vm.refresh();},8000);
    });
  }



  addscreen(code:any,widget:string=null) {
    if(this.userData.user.tarif.max_openscreen<=this.screens.length)
      toast(this.toastCtrl,"SCREEN.ERROR_MAXSCREEN",this.translate);
    else{
      if(code.value==""){
        askforaddscreen(this,widget,(res,widget)=>{this.searchScreen(res,widget)});
      } else{
        this.searchScreen(code.value,widget);
      }
    }

  }

  /**
   *
   * @param screen
   */
  changeWidget(screen:any) {
    var vm=this;
    Tools.openModal(this.modalCtrl,WidgetstorePage, {screen: screen.id,message:"SCREEN.CHOOSEWIDGET"},(widget) => {
      if(widget!=null){
        vm.userData.setwidgetonscreen(screen.id,widget.id).subscribe(res => {
          if(this.eventData.event.id==null)setTimeout(()=>{this.refresh();},1500);
        });
      }
    });
  }

  refresh() {
    $$("Rafraichissement des écrans");
    var vm=this;
    this.userData.get().subscribe((r)=>{
      if(r!=null)this.userData.user=r;
      vm.screens = [];
      if(vm.userData.user.hasOwnProperty("screens"))
        for (let key in vm.userData.user.screens) {
          var obj = vm.userData.user.screens[key];
          obj.widget = obj.id;
          obj.id = key;

          if (!vm.navParams.hasOwnProperty("event") || obj.idEvent == vm.navParams.get("event")){
            vm.screens.push(obj);
            if(vm.userData==undefined || vm.userData.user==undefined || vm.userData.user.currentEvent==null)
              vm.showAddEvent=true;
          }

        }
    });
  }

  editScreen(screen){
    var index=this.screens.indexOf(screen);
    var vm=this;
    showPopup({
        title: "SCREEN.NAMEFORSCREEN",
        confirmButton: "LIB.OK",
        cancelButton: "LIB.CANCEL",
        default:screen.title,
        type: "text",
        translate:this.translate
      },

      this.alertCtrl,
      (res) => {
              vm.screens[index].title=res;
              vm.userData.updatescreen(vm.screens[index]).subscribe(r=>{});
            }
    )
  }

  deleteScreen(screen) {
    var vm=this;
    this.userData.deleteScreen(screen.id).subscribe((resp:any) => {
      vm.refresh();
    });
  }

  viewScreen(screen: any) {
    screen.title=screen.title+" "; //Permet de forcer l'affichage du nom
    this.userData.updatescreen(screen).subscribe(r=>{});
  }

  openFreeScreen() {
    let code:string=""+(tirage(900000)+100000);
    openWindow(DOMAIN_SERVER+"/widgets/freeScreen.html?code="+code);
    setTimeout(()=>{
      this.searchScreen(code);
      },5000);
  }
}




