import { Component } from '@angular/core';
import {
  AlertController, IonicPage, LoadingController, NavController, NavParams, ToastController,
  ViewController
} from 'ionic-angular';
import * as Tools from "../../app/Maintools"
import {UserData} from "../../providers/user-data";
import {EventDataProvider} from "../../providers/event-data/event-data";
import {loading, openModal, openWindow, showConfirm, showPopup, toast} from "../../app/Maintools";
import {TranslateService} from "@ngx-translate/core";
import {ApiProvider} from "../../providers/api/api";

/**
 * Generated class for the InvitePage page.
 *
 * Parametres : url,event,from,message,type,tab (tab destinataire)
 */

@IonicPage()
@Component({
  selector: 'page-invite',
  templateUrl: 'invite.html',
})
export class InvitePage {
  email: any = {dest: "", personal: false};
  autologin:any={value:false};
  numberOfInvitation: number = 0;
  qrcode: string = "";
  url: string = "";
  forceAnonymous:boolean=true;
  event:any=null;

  constructor(public viewCtrl: ViewController, public toastCtrl: ToastController,
              public navCtrl: NavController, public alertCtrl: AlertController,
              public navParams: NavParams,public api:ApiProvider,public loadingCtrl:LoadingController,
              public userData: UserData, public translate: TranslateService,
              public eventData: EventDataProvider) {

    //Chargement des contacts
    if (this.userData.friends.length == 0) userData.loadcontacts();

      //Initialisation de l'event
      this.event = navParams.get("event");
      if(this.event==null)this.event=this.eventData.event;
  }

  ionViewDidLoad() {
    this.autologin.value=(this.event.autologin==2);
    this.refresh();
  }

  refresh(large_url:string=null){
    var vm=this;
    if(large_url==null)large_url = this.navParams.get("url");

    if (large_url == null) {
      var l=loading(this);
      this.userData.shareevent(this.event.id, this.navParams.get("message"), this.navParams.get("tab"),"link",this.forceAnonymous).subscribe((r:any) => {
        l.dismiss();
        if(r!=null){
          vm.qrcode = r.qrcode;
          vm.url = r.url;
        }
      });
    }else{
      //On charge juste le QRCOde puisqu'on a déjà une url
      this.api.getqrcode(large_url+"&anonymous="+this.forceAnonymous,150).subscribe(r=>{
        if(r!=null){
          vm.qrcode=r.photo;
          vm.url=r.title;
        }
      })
    }
  }


  copyLink(){
    toast(this.toastCtrl,"INVITE.TUTO_COPYLINK",this.translate);
  }


  del_email(index) {
    //delfriendsemail(vm.userData.user.id,vm.userData.user.friendsEmail[index]);
    //vm.userData.user.friendsEmail.splice(index,1);
  };

  sendInvitationToFollower() {
    var vm = this;
    if(this.eventData.event==null)this.eventData.event=this.event;
    this.eventData.sendinvitetofollower(vm.numberOfInvitation).subscribe((resp:any) => {
      if(resp!=null){
        vm.userData.user = resp;
        Tools.toast(this.toastCtrl, resp.message,this.translate);
      }

    });
  };

  openContactProvider() {
    var vm = this;
    this.navCtrl.push("Perso", {filter: "facebook,contact", user: vm.userData.user.id, autoback: true, from: "invite"});
  };

  addEmail() {
    var vm = this;
    Tools.showPopup({
        title: "INVITE.EMAIL",
        placeholder: "@",
        translate: this.translate,
        type: "email"
      },
      this.alertCtrl, function (res) {
        if (res != null)
          vm.sendInvitations({email:res});
      });
  }

  sendInvitations(dest) {
    if(this.navParams.data.template!=null){
      var obj={qrcode:this.qrcode,url:this.url};
      this.api.sendtemplate(this.userData.user.email,this.userData.user.id,this.navParams.data.template,obj).subscribe(()=>{});
    }
    else {
      var vm = this;
      //if(dest==undefined)dest={email:vm.email.dest};
      if (dest.email.indexOf("facebook") == 0) {
        //clipboard.copyText(vm.url);
        var hwnd = openWindow("https://www.facebook.com/" + dest.email.replace("facebook", ""));
        //TODO: voir si on dispose de l'adresse publique pour ecrire
        // setTimeout(function(){

        //   var name=hwnd.location.href.replace("https://www.facebook.com/");
        //   hwnd.location.href="https://www.facebook.com/messages/t/"+name+"/";
        // },500);
      } else {
        vm.eventData.shareevent(vm.navParams.get("message"), dest.email, vm.userData.user.id, dest.name,this.event).subscribe(function (resp:any) {
          if (resp.error == undefined || resp.error.length == 0) {
            var lib = dest.name;
            if (lib == null) lib = dest.email;
            toast(vm.toastCtrl, "INVITE.CONFIRM", vm.translate);
            vm.userData.addfriendsemail(dest.email).subscribe(function (resp) {
            });
          }
          else
            toast(vm.toastCtrl, resp.error, vm.translate);
        });

      }
    }

    //vm.email.dest="";
  };

  sendMe(){
    this.sendInvitations({email:this.userData.user.email,name:this.userData.user.firstname});
    toast(this.toastCtrl,"INVITE.CHECKYOUREMAIL",this.translate);
  }

  makePermanentInvitation(){
    showConfirm("INVITE.CONFIRMEPERMANENTEINVITATION",this.alertCtrl,this.translate,()=>{
      this.userData.makePermanentInvitation("music").subscribe((rep)=>{
        toast(this.toastCtrl,"INVITE.CHECKYOUREMAIL",this.translate);
      });
    });
  }

  updateEvent(){
      if(this.autologin.value)this.event.autologin=2; else this.event.autologin=0;
      this.eventData.updateevent("autologin",this.event).subscribe(() => {
        this.refresh();
      });
  }


  printCode() {
    let wnd=Tools.openWindow("","Print");
    wnd.document.body.innerHTML=document.getElementById("printable").innerHTML;
    wnd.document.body.style.height="100%";
    wnd.document.body.style.textAlign="center";
    wnd.print();
    wnd.close();
  }
}



