import { Component, ViewChild } from '@angular/core';

import {MenuController, NavController, Platform, Slides} from 'ionic-angular';

import { Storage } from '@ionic/storage';

import { TabsPage } from '../tabs-page/tabs-page';
import {LoginPage} from "../login/login";
import {UserData} from "../../providers/user-data";

@Component({
  selector: 'page-tutorial',
  templateUrl: 'tutorial.html'
})

export class TutorialPage {
  showSkip = true;

  tutos:string[]=["./assets/img/tuto1.svg","./assets/img/tuto2.svg","./assets/img/tuto3.svg"];
  type_tuto:string="normal";
	@ViewChild('slides') slides: Slides;


  constructor(
    public navCtrl: NavController,public plt:Platform,
    public menu: MenuController,public userData:UserData,
    public storage: Storage,
  ) {
    this.storage.get("type").then(r=>{ //Type indique si d'ou vient l'utilisateur pour entrer dans l'appli
      this.type_tuto=r;
    });

    let lang=plt.lang();
    if(lang=="en")
      this.tutos=["./assets/img/tuto1.svg","./assets/img/tuto2.svg","./assets/img/tuto3.svg"];

    if(this.userData.user!=undefined)
      this.userData.clearhisto().subscribe(r=>{
        if(r!=null)this.userData.user=r;
      })
  }


  startApp() {

    this.navCtrl.setRoot(LoginPage).then(() => {
      this.storage.set('hasSeenTutorial', 'true');
    });
  }

  onSlideChangeStart(slider: Slides) {
    this.showSkip = !slider.isEnd();
  }

  ionViewWillEnter() {
    this.slides.update();
  }

  ionViewDidEnter() {
    // the root left menu should be disabled on the tutorial page
    this.menu.enable(false);
  }

  ionViewDidLeave() {
    // enable the root left menu when leaving the tutorial page
    this.menu.enable(true);
  }

}



