import { Component } from '@angular/core';
import {ModalController, NavParams, ToastController, ViewController} from 'ionic-angular';
import {AlertController, IonicPage, LoadingController, NavController} from 'ionic-angular';
import {EventDataProvider} from "../../providers/event-data/event-data";
import {UserData} from "../../providers/user-data";
import * as Tools from "../../app/Maintools"
import 'rxjs/add/operator/publishReplay';
import {PersoPage} from "../perso/perso";
import {createSong, getDelay, loading, openGeneral, openModal, toast} from "../../app/Maintools";
import {ApiProvider} from "../../providers/api/api";
import {TranslateService} from "@ngx-translate/core";
import {SettingsProvider} from "../../providers/settings/settings";
import {MusicchartsPage} from "../musiccharts/musiccharts";

/**
 * Generated class for the SearchPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-search',
  templateUrl: 'search.html'
})
export class SearchPage {
  songs:any[]=[];cordo
  playlists:any[]=[];
  env:number=0;
  multiselect:boolean=false;
  selected:any={value:"Search"};
  query:any={value:""};
  songs_search=[];

  showFilter:boolean=false;
  myfilter:string="";
  playlist_titles:any[]=[];
  musicServerName:string[]=null;

  constructor(public viewCtrl:ViewController,public navCtrl: NavController,public alertCtrl:AlertController,public api:ApiProvider,public settings:SettingsProvider,
              public navParams: NavParams,public eventData:EventDataProvider,public toastCtrl:ToastController,public translate:TranslateService,
              public userData:UserData,public loadingCtrl:LoadingController,public modalCtrl:ModalController) {
    var vm=this;

    this.musicServerName=["Spotify","Deezer","Cloud","YouTube","Perso"];
    this.env=Tools.getEnv();
    this.songs=this.userData.lastSearchList;

    // if(this.settings.showMusicCharts==null)
    //   this.settings.showMusicCharts=(this.eventData.event.musicServer==3);

      if(vm.userData.playlist==undefined || vm.userData.playlist.items==undefined || vm.userData.playlist.items.length==0 ||
        (this.eventData.event.limitSearchToOwnerPlaylist && vm.userData.playlistFrom!=this.eventData.event.owner_id) ||
        (!this.eventData.event.limitSearchToOwnerPlaylist && vm.userData.playlistFrom==this.eventData.event.owner_id && vm.userData.user.id!=this.eventData.event.owner_id)){
        var id=this.userData.user.id;
        if(this.eventData.event.limitSearchToOwnerPlaylist)id=this.eventData.event.owner_id;

        let diag=loading(vm,"SEARCH.PLAYLISTANALYSIS");
        vm.api.getcloudplaylist(id,true).subscribe((rep: any) => {
          vm.userData.playlist=rep;
          vm.userData.playlistFrom=id;
          diag.dismiss();
          vm.loadplaylist(rep);
        },(err) => {
          toast(vm.toastCtrl,"ERROR.GENERAL",vm.translate);
          diag.dismiss();
        });
      }
      else
        vm.loadplaylist(vm.userData.playlist);
  }

  ionViewDidLoad() {
    var vm=this;
      if(vm.navParams.get("query")==null){
        if( this.settings.lastquery!=null && ! this.settings.lastquery.startsWith("playlist")) {
          this.query.value= this.settings.lastquery;
          this.search(13);
        }
      } else{
        if(vm.navParams.get("query")=="charts")
          this.openMusicCharts();
        else{
          this.showFilter=false;
          this.query.value=vm.navParams.get("query");
          this.search(13);
        }
      }
  }



  valideFilter(code){
    if(code==13){
      this.showFilter=false;
      this.query.value=this.myfilter;
      this.settings.lastcatalogue="";
      this.settings.lastquery=this.myfilter;
      this.search(13);
    }
  }

  openPlaylistFrom(service){
    var vm=this;
    openGeneral(service,this.userData.user).then(()=>{
      vm.api.getcloudplaylist(vm.userData.user.id,true).subscribe((rep: any) => {
        if(rep!=null){
          vm.userData.playlistFrom=vm.userData.user.id;
          vm.userData.playlist=rep;
          vm.loadplaylist(rep);
        }

      });
    })
  }

  loadplaylist(rep){
    var vm=this;
    Tools.preparePlaylist(rep.items, (songs, playlist_titles) => {
      vm.playlists = playlist_titles;
      vm.playlist_titles = songs;

        if(vm.settings.lastquery!=null && vm.settings.lastquery.startsWith("playlist")) {
          vm.selected = this.settings.lastquery.replace("playlist:", "");
          vm.loadPlaylist_titles();
        }

    });
  }


  openSearch(){
    this.showFilter=!this.showFilter;
    if(!this.showFilter && this.myfilter.length>0){
      this.query.value=this.myfilter;
      this.search(13);
    }
  }

  loadPlaylist_titles(){
    var vm=this;
    this.songs=[];
    this.playlist_titles.forEach(function(title){
      if(title.playlist==vm.selected)
        vm.addResult(title,vm.songs);
    });
    if(vm.songs.length>0){
      vm.showFilter=true;
      this.settings.lastquery="playlist:"+vm.selected;
    }
  }



  viewCharts = function () {
    Tools.openWindow('http://www.uk-charts.top-source.info/2000-to-2009.shtml');
  };


  /**
   * Ajoute un titre dans les résultat de la recherche
   * @param s
   * @param list
   * @returns {any}
   */
  addResult(s,list){
    if(!s.hasOwnProperty("playlist"))
      s["playlist"]="Search";

    s["selected"]=false;

    s["formatDuration"]="";
    if(s.duration>0)s["formatDuration"]=getDelay(new Date().getTime()+s.duration*1000)


    if(s.author==undefined)s.author= s.artist;

    s.tag= s.title+" "+ s.artist;
    for(var i=0;i<list.length;i++){
      if(s.order<=list[i].order)break;
    }
    list.splice(i,0,s);
    return list;
  };

  selectPlaylist(){
    var vm=this;
    vm.query.value="";
  }


  goPerso(){
    var vm=this;
    this.navCtrl.push("perso",{filter:"deezer,spotify,drive",user:vm.userData.user.id,autoback:true,from:"search"});
  };


  add(song){
    var s=Tools.createSong(song,this.userData.user,this.eventData.event);
    this.viewCtrl.dismiss({songs:[s]});
  };


  valide(){
    var rc=[];
    this.songs.forEach(s => {
      if (s.selected) {
        rc.push(Tools.createSong(s,this.userData.user,this.eventData.event));
      }
    });
    this.viewCtrl.dismiss({songs:rc});
  }


  /**
   * Fonction utilisé en récurence pour l'ajout de titre
   * @param k
   */

  addnewsong(k,mes){
    var vm=this;
    var ls=vm.songs;
    //ls=$filter('filter')(vm.songs,{"playlist":vm.selected.value});

    this.eventData.addsong(Tools.createSong(ls[k],vm.userData.user,vm.eventData.event)).subscribe((resp:any) => {
      if(resp.message!=undefined && resp.message.length>0){
        mes=resp.message;
      }
      if(k<Math.min(ls.length,50)){
        vm.addnewsong(k+1,mes);
      }
    });
  };


  // addAll(){
  //   var mes="";
  //   var vm=this;
  //   vm.addnewsong(0,mes);
  //   if(mes.length==0)
  //     Tools.toast(vm.toastCtrl,vm.songs.length+" "+"SEARCH.CONFIRMADD",this.translate);
  //   else
  //     Tools.toast(vm.toastCtrl,mes,this.translate);
  //
  //   vm.navCtrl.push("tabs.home",{action:"addall"});
  // }


  // addAuto(){
  //   var vm=this;
  //   this.eventData.addsongsauto(5).subscribe(resp => {
  //     vm.navCtrl.push("HomePage",{action:"addauto"});
  //   });
  // }


  /**
   * Déclenche la recherche des titres
   */
  search(keycode){
    if(keycode!=13)return;

    var vm=this;
    if(vm.eventData.event.limitSearchToOwnerPlaylist){
      return;
    }

    vm.selected="Search";
    vm.myfilter="";
    vm.songs=[]; //Effacement de la recherche précédente

    var q=vm.query.value;
    if(q!=null && q.length>2){
      if(vm.songs_search.length>0)vm.songs_search=[];

      this.settings.lastquery=q;
      this.settings.lastcatalogue="";

      if(vm.eventData.event.sources.indexOf("cloud")>-1)
        vm.eventData.searchcloud(q,vm.userData.user.id,vm.eventData.event.id).subscribe((resp:any) => {
            if(resp==null)
              toast(this.toastCtrl,"ERROR.RETRY",this.translate);
            else{
              resp.items.forEach(function (song) {
                vm.songs=vm.addResult(song, vm.songs);
              });
            }

        });

      if(vm.eventData.event.sources.indexOf("local")>-1)
        vm.eventData.searchlocal(q,vm.userData.user.id,vm.eventData.event.id).subscribe((resp:any) => {
          if(resp==null)
            toast(this.toastCtrl,"ERROR.RETRY",this.translate);
          else {
            resp.items.forEach(function (song) {
              vm.songs = vm.addResult(song, vm.songs);
            });
          }
        });

      if(vm.eventData.event.sources.indexOf("deezer")+vm.eventData.event.sources.indexOf("spotify")+vm.eventData.event.sources.indexOf("youtube")>-3){
        vm.eventData.searchstream(q,vm.eventData.event.id).subscribe((resp:any) => {
          if(resp==null)
            toast(this.toastCtrl,"ERROR.RETRY",this.translate);
          else{
            resp.items.forEach(function(song){
              vm.songs=vm.addResult(song,vm.songs);
            });
          }
        });
      }
    }
  };

  cancel(){};

  showMultiSelect(){
    if(!this.multiselect)
      this.multiselect=true;
    else {
      this.songs.forEach((song)=>{
        song.selected=!song.selected;
      });
    }
  }

  openMusicCharts(){
    openModal(this.modalCtrl,MusicchartsPage,{catalogues:this.navParams.get("catalogues")},(rep)=>{
      if(rep!=null){
        this.songs=this.addResult(rep,[]);
        this.viewCtrl.dismiss({songs:this.songs});
      }
    })
  }


}



