import {Component, ElementRef, Input, OnInit, ViewChild} from '@angular/core';
import {TranslateService} from "@ngx-translate/core";

/**
 * Generated class for the ShifubuttonComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
  selector: 'shifubutton',
  templateUrl: 'shifubutton.html'
})
export class ShifubuttonComponent implements OnInit {
  @Input('icon') icon: string="";
  @Input('small') small: boolean=false;
  @Input('hidewhenclick') hidewhenclick: boolean=false;
  @Input('tips') tips: string="";
  @Input('outline') outline: boolean=false;
  @Input('color') color: string="primary";
  @Input('size') size: string="";
  @Input('label') label: string="";
  fontsize:string="normal";
  sizeicon:string="large";
  paddingLeftRight:string="12px";
  size_number:number=0;
  beclick:boolean=false;
  height="35px";

  constructor(public translate:TranslateService) {

  }

  ngOnInit(): void {
    //Les boutons icones ont tous la meme taille
    if(this.label.length==0 && this.icon.length>0 && this.size.length==0)this.size="40px";

    if(this.small){
      this.paddingLeftRight="6px";
      this.sizeicon="large";
      this.fontsize="small";
      this.height="30px";
    }
    else{
      this.sizeicon="x-large";
      this.fontsize="normal";
    }


    if(!this.size.endsWith("px"))this.size=this.size+"px";
    this.size_number=Number(this.size.replace("px",""));

    if(this.label.indexOf(".")>-1)
      this.label=this.translate.instant(this.label);

  }

  onclick(){
    this.beclick=true;
    setTimeout(()=>{this.beclick=false;},10000);
  }

}



