import {Component, Input} from '@angular/core';
import {openWindow} from "../../app/Maintools";

/**
 * Generated class for the ShifuprofilComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
  selector: 'shifuprofil',
  templateUrl: 'shifuprofil.html'
})
export class ShifuprofilComponent {
  @Input("user") user:any={};
  @Input("light") light:boolean=false;
  @Input("openprofil") openprofil:boolean=true;

  constructor() {}

  openSocial(){
    if(this.user.home!=null && this.user.home.length>0)
      openWindow(this.user.home);
  }

}



