import {Component, Input, OnInit} from '@angular/core';
import {openModal} from "../../app/Maintools";
import {PublicProfilPage} from "../../pages/public-profil/public-profil";
import {ModalController, ViewController} from "ionic-angular";
import {PersoPage} from "../../pages/perso/perso";

/**
 * Generated class for the ShifuimageprofilComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
  selector: 'shifuimageprofil',
  templateUrl: 'shifuimageprofil.html'
})
export class ShifuimageprofilComponent implements OnInit {
  @Input("user") user:any={};                     //Celui qui est consulté
  @Input("me") me:boolean=false;
    @Input("withname") withName:boolean=false;
    @Input("openprofil") openprofil:boolean=true;
  @Input("follower") follower:string=null;        //Celui qui consulte le profil
  @Input("size") size:string="40px";
  fontsize:string="xx-large";
  toggle:boolean=false;
  firstname:string="";

  constructor(public modalCtrl:ModalController) {
  }

  ngOnInit(): void {
    if(this.user.firstname!=null)
      this.firstname=this.user.firstname.substr(0,Math.min(15,this.user.firstname.length));

    var taille=this.size.replace("px","");
    if(Number(taille)>50)
      this.fontsize=(Number(taille)/15)+"em";
  }

  openProfil(){
    if(this.openprofil==false)return;
    if(this.me)
      openModal(this.modalCtrl,PersoPage,{from:null});
    else
      openModal(this.modalCtrl,PublicProfilPage,{userid:this.user.id,follower:this.follower});
  }

}



