import {Component, ElementRef, EventEmitter, Input, Output, ViewChild} from '@angular/core';
import {TranslateService} from "@ngx-translate/core";
import {ApiProvider} from "../../providers/api/api";
import {LoadingController} from "ionic-angular";
import {loading} from "../../app/Maintools";

/**
 * Generated class for the ShifufileComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
  selector: 'shifufile',
  templateUrl: 'shifufile.html'
})
export class ShifufileComponent {

  @Input("size") size:string="";
  @Input("icon") icon:string="";
  @Input("tips") tips:string="";
  @Input("hourglass") hourglass:boolean=false;
  @Input("label") label:string="";
  @Output('onTake') onTake: EventEmitter<any>=new EventEmitter();
  @ViewChild('inputElement') inputElement: ElementRef;

  constructor(public loadingCtrl:LoadingController,
              public translate:TranslateService,
              public api:ApiProvider) {
  }

  onChange(event){
    var file=event.target.files[0];
    var reader:any=new FileReader();
    var l=null;
    if(this.hourglass)l=loading(this);
    reader.onload=()=>{
      if(l!=null)l.dismiss();
      var type=file.type;
      if(type==""){
        var pos=file.name.lastIndexOf(".")+1;
        if(pos>-1)
          type="application/"+file.name.substr(pos);
        else
          type="application/octet-stream";
      }
      this.onTake.emit({length:file.size,size:file.size,mega:file.size/(1024*1024),value:reader.result,file:file.name,format:type});
    }
    setTimeout(()=>{ //Permet l'apparition du sablier
        reader.readAsDataURL(file);
    },500);
  }


  takePhoto(){
    this.inputElement.nativeElement.click();
  }

}



