import {Component, Input} from '@angular/core';
import {getDelay} from "../../app/Maintools";
import {TranslateService} from "@ngx-translate/core";

/**
 * Generated class for the ShifutimerComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
  selector: 'shifutimer',
  templateUrl: 'shifutimer.html'
})
export class ShifutimerComponent {

  @Input("label") label: string="";
  @Input("end") end: number=0;
  @Input("server") server: number=null;
  @Input("noSecond") noSecond: boolean=false;
  @Input("onlyPositif") onlyPositif:boolean =false;
  handle:any=null;
  delay:string="";

  constructor() {
    var vm=this;
    this.handle=setInterval(()=>{
      vm.refresh();
    },1000);
  }

  refresh(){
    if(this.end<new Date().getTime()){
      if(this.handle!=null){
        clearInterval(this.handle);
        this.handle=null;
      }
    }
    else{
      if(!this.onlyPositif || this.end>new Date().getTime()){
        var out=getDelay(this.end,"fr","jours",this.server);
        if(this.noSecond)out=out.split(":")[0];
        if(this.label!="")
          this.delay=this.label+" "+out;
        else
          this.delay=out;
      }
    }

  }

}



