import {Component, ElementRef, Inject, Input, OnInit, Renderer2, ViewChild} from '@angular/core';
import {UserData} from "../../providers/user-data";
import * as Tools from "../../app/Maintools"
import { Storage } from '@ionic/storage';
import {openHelp, tirage} from "../../app/Maintools";
import {TranslateService} from "@ngx-translate/core";
import {DOCUMENT} from "@angular/common";

/**
 * Generated class for the TutoComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
  selector: 'tuto',
  templateUrl: 'tuto.html'
})
export class TutoComponent implements OnInit {
  @Input('label') label: string="";
  @Input('if') _if: boolean=true;
  @Input('icon') icon: string="";
  @Input('help') help: string="";
  @Input('show') eltToShow: string="";
  @Input('delay') delay: number=15;
  @Input('pseudo') pseudo: string="";
  @Input('start') start: number=0.2;
  @Input('position') position:string="relative"; //Peut être a titre pour centrer le message

  show:boolean=false;
  alignText:string="left";
  labels:string[]=[];

  constructor(private renderer: Renderer2,public userData:UserData,public storage: Storage,public translate:TranslateService) {
  }

  getPosition(el) {
    var xPos = 0;
    var yPos = 0;

    while (el) {
      if (el.tagName == "BODY") {
        // deal with browser quirks with body/window/document and page scroll
        var xScroll = el.scrollLeft || document.documentElement.scrollLeft;
        var yScroll = el.scrollTop || document.documentElement.scrollTop;

        xPos += (el.offsetLeft - xScroll + el.clientLeft);
        yPos += (el.offsetTop - yScroll + el.clientTop);
      } else {
        // for all other non-BODY elements
        xPos += (el.offsetLeft - el.scrollLeft + el.clientLeft);
        yPos += (el.offsetTop - el.scrollTop + el.clientTop);
      }

      el = el.offsetParent;
    }
    return {
      x: xPos,
      y: yPos
    };
  }

  ngOnInit(): void {

    if(this.label.indexOf("%%")>-1){
      this.label=this.label.split("%%")[0]+tirage(Number(this.label.split("%%")[1]));
    }

    if(this.label.length>0)
      this.label=this.translate.instant(this.label);

    var vm=this;
    var code=Tools.hashCode(this.label);
    if(this.icon.length==0 || this.position=="title")this.alignText="center";

    if(this._if && this.label.length>0) {

      setTimeout(() => {
        this.storage.get(this.pseudo + ":" + this.label).then((rep) => {
          if ((vm.userData.user.history == undefined && rep == null) || (vm.userData.user.history != undefined && vm.userData.user.history.indexOf(code) == -1)) {
            vm.show = true;
            vm.userData.user.history += ";" + Tools.hashCode(this.label);
            vm.userData.senduser("history").subscribe(() => {});

            //Fait clignoter l'element designé
            var elt = document.getElementById(vm.eltToShow);
            if(elt==null){
              var l_elts=document.getElementsByName(vm.eltToShow);
              if(l_elts.length>0)elt=l_elts[0];
            }
            if (elt != null) {
              elt.style.animationName = "clignote";
              elt.style.animationDuration = "1s";
              elt.style.animationIterationCount = "5";
            }

            if (vm.userData.user.history == undefined)
              vm.storage.set(this.pseudo + ":" + this.label, "viewed");
            else {
              setTimeout(() => {
                vm.show = false;
              }, this.delay * 1000);
            }
          }

          //Decoupe du label
          this.labels = this.label.split("<br>");
        });
      }, this.start* 1000);
    }
  }

  onMouseUp(){
    setTimeout(()=>{this.show = false;},500);
  }

  onHelp(){
    if(this.help.length>0)
      openHelp(this.help);
  }

}



