import {Component, Input, OnInit} from '@angular/core';
import {getDelay, openModal} from "../../app/Maintools";
import {ModalController} from "ionic-angular";
import {ClosedeventPage} from "../../pages/closedevent/closedevent";
import {TranslateService} from "@ngx-translate/core";

/**
 * Generated class for the ShifueventComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
  selector: 'shifuevent',
  templateUrl: 'shifuevent.html'
})
export class ShifueventComponent implements OnInit {

  @Input("event") event:any;
  @Input("lang") lang:string="fr";
  now:number=0;
  delay:string="";

  //@Input("user") user:any; //Va permettre de changer l'affichage

  constructor(public modalCtrl:ModalController,public translate:TranslateService) {
    this.now=new Date().getTime();
  }

  ngOnInit(): void {
    this.delay=getDelay(this.event.dtStart,this.lang);
  }


}



