import {Component, EventEmitter, Input, Output} from '@angular/core';
import {UserData} from "../../providers/user-data";
import {TranslateService} from "@ngx-translate/core";

/**
 * Generated class for the ShifubetComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
  selector: 'shifubet',
  templateUrl: 'shifubet.html'
})
export class ShifubetComponent {

  @Input('bet') bet:any;
  @Input('status') status:string="show";
  @Output('onchange') onchange: EventEmitter<any>=new EventEmitter();

  constructor(public userData:UserData,public translate:TranslateService) {

  }

  vote(bet,note){
    this.userData.setreputation(bet.id,note).subscribe(()=>{
      this.onchange.emit();
    });
  };


}



