import { Component } from '@angular/core';
import * as Tools from "../../app/Maintools"
import {EventDataProvider} from "../../providers/event-data/event-data";
import {UserData} from "../../providers/user-data";
import {AlertController, Events, IonicPage} from "ionic-angular";
import { ItemSliding, NavController, NavParams, ToastController} from "ionic-angular";
import {$$, DELAY_TO_REFRESH, showPopup, toast} from "../../app/Maintools";
import {TranslateService} from "@ngx-translate/core";

/**
 * Generated class for the MessagesPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-messages',
  templateUrl: 'messages.html',
})
export class MessagesPage {

  message:any={type: Tools.TYPE_MESSAGE,text:"",tags: ["message"]};
  messages:any[]=[];
  emoji:any={}
  scheduled_messages:any[]=[];
  toggled: boolean = false;
  messageLimitByUser=0;
  openPopup:Function;

  constructor( public events: Events,public navCtrl: NavController, public navParams: NavParams,public toastCtrl:ToastController,public translate:TranslateService,
               public alertCtrl: AlertController,public eventData:EventDataProvider,public userData:UserData) {
    this.message.text=translate.instant("MESSAGES.YOURMESSAGE");
  }

  setPopupAction(fn: any) {
    this.openPopup = fn;
  }


  setScore(mes,step){
    var vote={event:this.eventData.event.id,from:this.userData.user.id,value:step,description:""};
    mes.canVote=false;
    this.userData.sendvote(mes.id,vote).subscribe((rep) => {
      if(rep!=null)this.userData.user=rep;
    });
  };

  ionViewDidLoad() {
    Tools.subscribe("message",this);
  }

  ionViewDidEnter () {
    setTimeout(()=>{this.refresh(true);},DELAY_TO_REFRESH);
    setTimeout(()=>{this.removeDefaultMessage();},1500);
  }


  writemessage(key){
    if(key==13){
      this.openPopup(false);
      this.sendmessage();
    }

  }



  sendmessage(delay:number=-1){
    if(this.message.text.length==0)return;
    this.message.from=this.userData.user;
    this.message.anonymous=this.userData.user.anonymous;
    this.message.photo="";

    if(delay<=0) //Les messages sont imédiatement ajoutés pour donner l'ilusion d'un envoi rapide
      this.messages.splice(0,0,JSON.parse(JSON.stringify(this.message)));
    else{
      this.message.dtStart=new Date().getTime()+delay*60*1000;
      this.scheduled_messages.splice(0,0,JSON.parse(JSON.stringify(this.message)));
    }

    this.eventData.sendmessage(delay,this.message).subscribe(resp => {
      if(resp==null){
        toast(this.toastCtrl,"MESSAGE.ERRORLIMIT",this.translate);
      }
    });

    this.message.text="";

  }




  programMessage(){
    showPopup({title:"MESSAGE.ENTERDELAY",placeholder:"",confirmButton:"LIB.OK",cancelButton:"LIB.CANCEL",type:"number",translate:this.translate},this.alertCtrl,(res)=>{
      this.sendmessage(Number(res));
    });
  }




  deleteMessage(id){
    var vm=this;
    this.eventData.deletemessage(id,this.userData.getId()).subscribe(rep=>{
      toast(vm.toastCtrl,"MESSAGE.DELETED",this.translate);
      vm.refresh();
    });
  }



  deleteScheduledMessage(item){
    var vm=this;
    this.eventData.deletemessage(item.id,vm.userData.user.id).subscribe(()=>{
      vm.refresh(true);
    });
  }





  refresh(force:boolean=false) {
    var vm = this;

    if(!force && this.userData.selectTab!=undefined && this.userData.selectTab!="MessagesPage")return;

    $$("Récupération des derniers messages");
    this.eventData.getlastmessages(30).subscribe((resp:any) => {
      if (resp.items.length > 0) {
        vm.messages = [];
        resp.items.forEach(function (item) {
          if (item.hasOwnProperty("votes") && item.from.id != vm.userData.user.id) {
            item.canVote = true;
            item.votes.forEach(function (vote) {
              if (item.canVote && vote.from == vm.userData.user.id) {
                item.canVote = false;
              }
            });
          } else if (item.from.id != vm.userData.user.id) item.canVote = true;

          vm.messages.push(item);
        });
      }
    });

    this.eventData.getscheduledmessages(20).subscribe((resp:any) => {
      if (resp.items.length > 0)
        vm.scheduled_messages = resp.items;
      else
        vm.scheduled_messages=[];
    });
  }



  openContextMenu(evt:Event,item: ItemSliding) {
    //TODO: en chantier
    evt.preventDefault();
    evt.stopPropagation();
    let swipeAmount = 194; //set your required swipe amount
    item.startSliding(swipeAmount);
    item.moveSliding(swipeAmount);

    item.setElementClass('active-options-right', true);
    item.setElementClass('active-swipe-right', true);
  }



  updateLimitByUser(){
    this.eventData.updateevent("messageLimitByUser").subscribe(()=>{});
  }

  changeLimitByUser(){
    this.eventData.event.messageLimitByUser[1]=1;
  }

  getFocus(){
    this.openPopup(false);
  }

  removeDefaultMessage() {
    if(this.message.text==this.translate.instant("MESSAGES.YOURMESSAGE"))
      this.message.text="";
  }
}



