import { Component } from '@angular/core';
import {AlertController, IonicPage, LoadingController, NavController, NavParams, ViewController} from 'ionic-angular';
import {SettingsProvider} from "../../providers/settings/settings";
import {ApiProvider} from "../../providers/api/api";
import {createSong, loading} from "../../app/Maintools";
import {EventDataProvider} from "../../providers/event-data/event-data";
import {UserData} from "../../providers/user-data";

/**
 * Generated class for the MusicchartsPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-musiccharts',
  templateUrl: 'musiccharts.html',
})
export class MusicchartsPage {
  songs:any[]=[];
  charts:any[]=[];
  catalogues:string[]=["official-uk-songs","Digital France"];
  catalogue_sel:string="ChartsInFrance";
  year_sel:string="";
  years:string[]=["2001","2002","2011","2017","2018","2019"];

  constructor(public navCtrl: NavController, public navParams: NavParams,public api:ApiProvider,
              public eventData:EventDataProvider, public userData:UserData,public viewCtrl:ViewController,
              public alertCtrl:AlertController,public settings:SettingsProvider,public loadingCtrl:LoadingController) {
    this.catalogues=this.navParams.get("catalogues");
    if(this.settings.lastcatalogue==""){
      this.settings.lastcatalogue=this.catalogues[0];
      this.settings.lastyear=""+(new Date().getFullYear()-1);
    }

  }

  ionViewDidLoad() {
    this.catalogue_sel=this.settings.lastcatalogue;
    this.year_sel=this.settings.lastyear;
    this.catalogue_change();
  }

  catalogue_change(force:boolean=false) {
    this.settings.lastcatalogue=this.catalogue_sel;
    this.settings.lastquery="";
    if(this.settings.musicChartsItems==null || force){
      var l=loading(this);
      this.api.getcharts(this.catalogue_sel).subscribe((rep)=>{
        l.dismiss();
        if(rep!=null){
          this.settings.musicChartsItems=rep.items;
          this.charts=rep.items;
          this.years=[];
          rep.items.forEach(s=>{
            var year=new Date(Number(s.dtStart)).getFullYear();
            if(this.years.indexOf(""+year)==-1)this.years.push(""+year);
          });
          this.years.sort().reverse();
          if(this.year_sel==null || this.years.indexOf(this.year_sel)==-1)this.year_sel=this.years[this.years.length-1];
          this.year_change();
        }
      });
    } else {
      this.charts=this.settings.musicChartsItems;
    }


  }

  load_songs(){

  }

  /**
   * Récupération des tires
   */
  year_change() {
    this.settings.lastyear=this.year_sel;
    this.songs=[];
    this.charts.forEach((song)=>{
      if(new Date(Number(song.dtStart)).getFullYear()==Number(this.year_sel)){
        var s:any=createSong({
          title:song.title,
          text:song.id,
          origin:-1, //designe une source issue des charts
          author:song.artist,
          duration:0,
          picture:song.picture,
          computer:""},this.userData.user,this.eventData.event);

        s.position=song.position;
        var rc=false;
        for(var i=0;i<this.songs.length && !rc;i++){
          if(this.songs[i].position>s.position){
            this.songs.splice(i,0,s);
            rc=true;
          }
        }
        if(!rc)this.songs.push(s);
      }
    });

  }

  add(song){
    var s=createSong(song,this.userData.user,this.eventData.event);
    this.viewCtrl.dismiss(s);
  };


  year_inc(step: number) {
    let y=this.year_sel;
    do{
      this.year_sel=""+(Number(this.year_sel)+step);
    }
    while(this.years.indexOf(this.year_sel)==-1 && Number(this.year_sel)>1950 && Number(this.year_sel)<2030);

    if(this.years.indexOf(this.year_sel)==-1)
      this.year_sel=y;
    else
      this.year_change();
  }
}



