import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import {Platform} from "ionic-angular";
import {ApiProvider} from "../api/api";
import {DOMAIN_APPLI, isLocal} from "../../app/Maintools";

/*
  Generated class for the SettingsProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class SettingsProvider {
  app_name:string="Shifumix";
  app_version:string="0.6.001";
  version:number=0;
  lastSearchImage:string="\#";
  lastImages:any[]=[];

  musicChartsItems:any[]=null;

  code:string=null;
  ihm:string="perso";
  autologin:number=0;
  connexion:string="";
  login_email: string = "";
  lastyear: string="";
  lastcatalogue: string="";
  lastquery: string="";
  dest: string="";
  logo:string="./assets/img/logo440diskonly.png";
  isLocal:boolean=false;

  about_url:string="https://shifumixweb.appspot.com/about.html";
  forum_url:string="https://www.tapatalk.com/groups/shifumix/forum-f2/";
  feedback_url:string="https://feedback.userreport.com/ef6875fa-89ce-4b14-8917-b702aec7c6cc/";
  blog_url:string="https://shifumixwww.wixsite.com/shifumix/blog";

  theme:string="";
  services: any=null;

  private lastRefresh: number[]=[]; //Dernier rafraichissement de la page

  //Designe l'adresse du theme

  constructor(public platform:Platform,public api:ApiProvider) {
    this.version=Number(this.app_version.substr(0,this.app_version.lastIndexOf(".")-1));
    this.ihm=platform.getQueryParam("ihm");if(this.ihm==null)this.ihm="perso";
    this.connexion=platform.getQueryParam("connexion");if(this.connexion==null)this.connexion="";
    this.autologin=platform.getQueryParam("autologin");if(this.autologin==null)this.autologin=0;
    this.login_email=platform.getQueryParam("login_email");if(this.login_email==null)this.login_email="";
    this.theme=platform.getQueryParam("theme");if(this.theme==null)this.theme="";
    this.isLocal=(DOMAIN_APPLI.indexOf("localhost")>-1);
  }

  //test : http://localhost:8101/?server=local&theme=http://localhost:8080/assets/data/theme_google.json
  /**
   * Charge un theme, sera utile pour faire une déclinaison en marque blanche
   */
  loadtheme(){
    if(this.theme.length>0){
      this.api.loadtheme(this.theme).subscribe((th:any)=>{
        this.logo=th.logo;
        this.app_name=th.app_name;
        this.about_url=th.about_url;
        this.forum_url=th.forum_url;
        this.feedback_url=th.feedback_url;
        this.blog_url=th.blog_url;
      });
    }
  }

  /**
   * Permet d'évitement les échos d'order (notamment le double déclenchement du onSave de presentation)
   * @param {string} page
   * @param {boolean} force
   * @param {number} delay
   * @returns {boolean}
   */
  mustRefresh(page:string,delayInMilliseconds:number=2) {
    if((new Date().getTime()-this.lastRefresh[page])<delayInMilliseconds)return false;
    this.lastRefresh[page]=new Date().getTime();
    return true;
  }
}



