import {Component, ElementRef, ViewChild} from '@angular/core';
import {IonicPage, NavController, NavParams, ViewController} from 'ionic-angular';
import {TranslateService} from "@ngx-translate/core";
import {DomSanitizer} from "@angular/platform-browser";
import {UserData} from "../../providers/user-data";

/**
 * Generated class for the TermOfUsePage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-term-of-use',
  templateUrl: 'term-of-use.html',
})
export class TermOfUsePage {

  @ViewChild("page") page:ElementRef;

  constructor(public navCtrl: NavController, public navParams: NavParams,
              public translate:TranslateService,public domSanitizer:DomSanitizer,
              public userData:UserData,public viewCtrl:ViewController) {

  }

  ionViewDidEnter(){
    this.page.nativeElement.contentDocument.body.innerHTML=this.navParams.get("CG");
  }

}



