import {Component, ViewChild} from '@angular/core';
import {Content, NavController, NavParams, ToastController, ViewController} from 'ionic-angular';
import {AlertController, IonicPage, LoadingController, ModalController} from 'ionic-angular';
import {UserData} from "../../providers/user-data";
import {ApiProvider} from "../../providers/api/api";
import {loading, openGeneral, openModal, openWindow,toast} from "../../app/Maintools";
import {PublicProfilPage} from "../public-profil/public-profil";
import {AddeventPage} from "../addevent/addevent";
import {TranslateService} from "@ngx-translate/core";
import {EventDataProvider} from "../../providers/event-data/event-data";

/**
 * Generated class for the ClosedeventPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-closedevent',
  templateUrl: 'closedevent.html',
})
export class ClosedeventPage {
  @ViewChild(Content) content: Content;
  orderby:any={};
  event:any={};
  users:any[]=[];
  winner:any={};
  songs:any[]=[];
  photos:any[]=[];
  user:any={};
  messages:any[]=[];
  surveys:any[]=[];
  questions:any[]=[];
  bets:any[]=[];
  presentations:any[]=[];
  loteries:any[]=[];
  allmessages:string="";
  hInterval=null;
  save:string=null; //Permet d'enregistrer l'identificant de eventData.event pour ne pas risquer de l'écraser

  constructor(public userData:UserData,public api:ApiProvider,public alertCtrl:AlertController,public loadingCtrl:LoadingController,public eventData:EventDataProvider,
              public viewCtrl:ViewController,public navCtrl: NavController,public toastCtrl:ToastController,public translate:TranslateService,
              public navParams: NavParams,public modalCtrl:ModalController) {
    var vm=this;
    vm.orderby={critere:"creditEvent"};
    this.event=this.navParams.get("event");
  }


  ionViewWillEnter(){
    var vm=this;
    if(vm.eventData.event!=null)vm.save=vm.eventData.event.id;
    vm.eventData.event.id=vm.event.id;
    this.api.getpresents(this.event.id,true).subscribe((resp)=>{
      vm.users=resp.items;
      var rc=false;

      resp.items.forEach((item)=>{
        if(item.id==this.userData.user.id)rc=true;
      });
      if(rc==false)this.viewCtrl.dismiss({message:'ERROR.NOTAUTHORIZED'});

      //vm.users=$filter("orderBy")(resp.result.items,"creditEvent",true);
      if(vm.event.activities.indexOf("bets")>-1){ //Si l'evt incluait des paris
        if(vm.user.hasOwnProperty("length") && vm.users.length>0){
          vm.winner=vm.users[0];
          vm.users.splice(0,1);
        }
      }

      vm.api.getplayedsong(vm.event.id).subscribe(function(res){
        vm.songs=res.items;
      });

      vm.api.slideshow(vm.event.id,null).subscribe(function(res2){
        vm.photos=res2.items;
      });

    });

    vm.api.getloteries(vm.event.id).subscribe(function(resp) {
      vm.loteries=resp.items;
    });

    vm.api.getbets(vm.event.id).subscribe(function(resp){
      vm.surveys=[];
      vm.bets=[];
      if(resp.items!=undefined)
        resp.items.forEach((bet) => {
          if(bet.type==4)vm.bets.push(bet);
          if(bet.type==5)vm.surveys.push(bet);
          if(bet.type==9)vm.questions.push(bet);
        });
    });

    vm.allmessages="";
    vm.api.getlastmessages(vm.event.id,500).subscribe(function(resp){
      vm.messages=resp.items;
      resp.items.forEach((i)=>{vm.allmessages+=i+"\n";});
    });

    vm.eventData.getpresentations().subscribe(function(resp){
      vm.presentations=[];
      resp.items.forEach((p)=>{if(p.ended)vm.presentations.push(p);});
    });

    if(vm.save!=null)vm.eventData.event.id=vm.save;
  }

  openProfil(user){
    openModal(this.modalCtrl,PublicProfilPage,{userid:user.id});
  }

  /**
   * Créé un événement depuis un modele
   * @param evt
   */
  createEvent(evt:any){
    var vm=this;
    this.userData.initevent(evt).subscribe((modele:any) => {
      modele.option={title:modele.title,address:modele.address,date:modele.date,teaser:modele.description};
      modele.dtStart=Number(modele.dtStart);
      modele.dtEnd=Number(modele.dtEnd);
      openModal(this.modalCtrl,AddeventPage,{user:this.userData.user,lat:evt.lat,lng:evt.lng,zoom:5,modele:modele},function(){
        vm.viewCtrl.dismiss();
      });
    });
  }

  uploadto(service) {
    var vm=this;
    openGeneral(service,this.userData.user).then(() => {
      let loader=loading(vm,"Photo Transfert ...",this.photos.length);
      vm.userData.uploadto(vm.event.id,service).subscribe((resp:any) => {
        loader.dismiss();
        toast(vm.toastCtrl,resp.message, vm.translate);
      });
    });
  }

  createPlaylist(service){
    var l=loading(this,"Playlist transfert ...",60);
    this.api.createplaylist(this.event.id,this.userData.user.id,service).subscribe(r=>{
      if(r.errorCode==0)
        toast(this.toastCtrl,"Playlist transférée");
      else
        toast(this.toastCtrl,"ERROR.GENERAL");
      l.dismiss();
    });
  };


  downloadAll(){
    if(this.event.closed)
      openWindow(this.event.zipAlbum);
    else
      toast(this.toastCtrl,"ERROR.EVENTMUSTBEFINISHED",this.translate);
  };

  scrollTo(elementId:string) {
    let yOffset = document.getElementById(elementId);
    if(yOffset!=null)
      this.content.scrollTo(0, yOffset.offsetTop, 2000)
  }

  downloadTheMovie() {
    openWindow(this.event.theMovie);
  }


  download(pres:any){
    openWindow(pres.support,"download");
  }


  copyMessage() {
    toast(this.toastCtrl,"LIB.TEXTINCLIPBOARD",this.translate);
  }
}



