import { Component } from '@angular/core';
import {AlertController, IonicPage, ModalController, NavController, NavParams, ViewController} from 'ionic-angular';
import * as Tools from "../../app/Maintools"
import {ApiProvider} from "../../providers/api/api";
import {openModal, showConfirm} from "../../app/Maintools";
import {ImageCreatorPage} from "../image-creator/image-creator";
import {TranslateService} from "@ngx-translate/core";

@IonicPage()
@Component({
  selector: 'page-htmleditor',
  templateUrl: 'htmleditor.html',
})
export class HtmleditorPage {
  widget:any={};
  code:string="";
  categories:string="";


  constructor(public modalCtrl:ModalController,public api:ApiProvider,public translate:TranslateService,
              public navCtrl: NavController, public navParams: NavParams,
              public viewCtrl:ViewController,public alertCtrl:AlertController) {
    this.widget=navParams.get("widget");
    var vm=this;
    var id=vm.widget.id;
    if(id=="")id=vm.widget.parentid;

    this.categories=this.translate.instant("WIDGET.CATEGORIES");

    if(this.widget.code.indexOf("<body>")>0)
      this.code=Tools.extract(this.widget.code,"<body>","</body>");
    else
      this.code=this.widget.code;
  }


  saveWidget(){
    if(this.code.indexOf("<body>")>-1)this.code=Tools.extract(this.code,"<body>","</body>");
    this.widget.code=this.code;
    if(this.widget.id=="")
      this.widget.id="widget"+new Date().getTime();

    this.viewCtrl.dismiss({widget:this.widget});
  };

  quit(){
    showConfirm("LIB.CANCELMODIF",this.alertCtrl,this.translate,() => this.viewCtrl.dismiss());
  }

  updateWidgetPicture(){
    var vm=this;
    openModal(this.modalCtrl,ImageCreatorPage,{},function(resp){
      if(resp!=null)
        vm.widget.picture=resp.value;
    })
  }

  updateAct(f:any){
    this.widget.activity=f.value;
  }

}



