import { Component } from '@angular/core';
import {IonicPage, NavController, NavParams, ToastController, ViewController} from 'ionic-angular';
import * as Tools from "../../app/Maintools"
import {TranslateService} from "@ngx-translate/core";
import {UserData} from "../../providers/user-data";
import {MUSICSERVER_SPOTIFY,MUSICSERVER_YOUTUBE} from "../../app/Maintools";
import {MUSICSERVER_DEEZER, MUSICSERVER_LOCAL, MUSICSERVER_PERSO} from "../../app/Maintools";
import {getEnv, toast} from "../../app/Maintools";
import {ApiProvider} from "../../providers/api/api";
import {SettingsProvider} from "../../providers/settings/settings";

@IonicPage()
@Component({
  selector: 'page-musicserver',
  templateUrl: 'musicserver.html',
})
export class MusicserverPage {
  description="";
  services=[];
  musicServer:number=3;
  streamingPlatforms=[];
  streamingPlatforms_sel="";
  from:string="";

  constructor(public userData:UserData,public translate:TranslateService,public toastCtrl:ToastController,
              public navCtrl: NavController, public navParams: NavParams,public api:ApiProvider,public settings:SettingsProvider,
              public viewCtrl:ViewController) {

    var token_deezer="deezer";
    if(getEnv()==0)token_deezer="deezer_local"; //modification pour tester deezer en local

    let availableOffers = [
      {"label":"Spotify","value": "0","description":"MUSICSERVER.SPOTIFYDESCRIPTION",token:"spotify",minTarif:0,minVersion:1},
      {"label":"Deezer","value": "1","description":"MUSICSERVER.DEEZERDESCRIPTION",token:token_deezer,minTarif:0,minVersion:1},
      {"label":"Vos MP3","value": "2","description":"MUSICSERVER.MULTIPLEDESCRIPTION",token:"drive",minTarif:4,minVersion:2},
      {"label":"Youtube","value": "3","description":"MUSICSERVER.YOUTUBEDESCRIPTION",minTarif:3,minVersion:1},
      {"label":"Manuel","value": "4","description":"MUSICSERVER.PERSODESCRIPTION",minTarif:3,minVersion:1}
    ];

    availableOffers.forEach((t) => {
      //if(t.minTarif<=this.userData.user.tarif.tarif && version>=t.minVersion)
      this.streamingPlatforms.push(t);
    });

    this.from=this.navParams.get("from");
    this.streamingPlatforms_sel = this.streamingPlatforms[this.userData.user.musicServer];
    this.services = Tools.getAllServices(this.settings.services,"spotify,deezer",this.settings.version);
    this.musicServer=this.userData.user.musicServer;
  }

  selectPlatform(event) {
    var vm = this;
    var item = this.streamingPlatforms[this.musicServer];
    if (item != null) {
      this.description= item.description;
      vm.userData.user.musicServer=Number(event.value);
      if (item.hasOwnProperty("token")){
          setTimeout(()=>{
            Tools.openGeneral(item.token, vm.userData.user).then((resp)=> {
              if(resp){
                vm.userData.get().subscribe((u:any)=>{
                  vm.userData.user.musicServer=Number(event.value);
                  vm.userData.user.accessTokens=u.accessTokens;
                  vm.userData.clearPlaylist();
                  setTimeout(()=>{
                    vm.api.getcloudplaylist(vm.userData.user.id,false).subscribe((rep: any) => {
                      if(rep!=null){
                        vm.userData.playlist=rep.items;
                        vm.userData.playlistFrom=vm.userData.user.id;
                      }
                    });
                  },5000);
                })

              }
            },()=>{
              toast(this.toastCtrl,"ERROR.GENERAL",this.translate);
            });
          },1000);

      }
    }
  }

  valide(){
    let err="ERROR.NOACCESS";
    if(this.userData.user.accessTokens!=undefined){
      if(this.userData.user.musicServer==MUSICSERVER_SPOTIFY && this.userData.user.accessTokens.spotify!=undefined)err="";
      if(this.userData.user.musicServer==MUSICSERVER_DEEZER && (this.userData.user.accessTokens.deezer!=undefined || this.userData.user.accessTokens.deezer_local!=undefined))err="";
    }
    if(this.userData.user.musicServer==MUSICSERVER_YOUTUBE || this.userData.user.musicServer==MUSICSERVER_LOCAL)err="";
    if(this.userData.user.musicServer==MUSICSERVER_PERSO)err="";
    if(err.length>0)
      toast(this.toastCtrl,err,this.translate);
    else {
      this.userData.senduser("musicServer").subscribe((rep:any)=> {
        if(rep!=null){
          if(rep.message.length==0)
            this.viewCtrl.dismiss({user:rep,musicServer:this.userData.user.musicServer});
          else
            toast(this.toastCtrl,rep.message,this.translate);
        }
      });
    }
  }
}



