import { Camera, CameraOptions } from '@ionic-native/camera';
import { Component } from '@angular/core';
import {AlertController, Events, IonicPage, LoadingController, ModalController, NavController, NavParams,ToastController} from 'ionic-angular';
import * as Tools from "../../app/Maintools"
import {EventDataProvider} from "../../providers/event-data/event-data";
import {UserData} from "../../providers/user-data";
import {DELAY_TO_REFRESH, openModal, showPopup, toast} from "../../app/Maintools";
import {autoRotate,  cropToSquare, loading} from "../../app/Maintools";
import {ImageCreatorPage} from "../image-creator/image-creator";
import {PublicProfilPage} from "../public-profil/public-profil";
import {TranslateService} from "@ngx-translate/core";
import {isIos} from "ionic-angular/umd/platform/platform-utils";
import {PushNotificationsService} from "ng-push";
import {ApiProvider} from "../../providers/api/api";

@IonicPage()
@Component({
  selector: 'page-photos',
  templateUrl: 'photos.html',
})

export class PhotosPage {

  options: CameraOptions = {quality: 100,destinationType: this.camera.DestinationType.DATA_URL,encodingType: this.camera.EncodingType.JPEG,mediaType: this.camera.MediaType.PICTURE};

  lastphoto:any={from:{id:""},photo:"",validate:true,text:"",canVote:false};
  lastRefresh:number=0;
  photos:any[]=[];
  message:string=""; //Message d'attente pendant l'envoi des photos

  constructor( public events: Events,public navCtrl: NavController, public navParams: NavParams,public loadingCtrl:LoadingController,public api:ApiProvider,
              public alertCtrl: AlertController,public modalCtrl:ModalController,public toastCtrl:ToastController,
              public eventData:EventDataProvider,public userData:UserData,public translate:TranslateService,public notifs:PushNotificationsService,
              public camera:Camera) {
  }

  ionViewDidEnter () {
    setTimeout(()=>{this.refresh(true);},DELAY_TO_REFRESH);
  }

  refreshEvent(){
    this.eventData.updateevent("addPhotoOnlyByOwner,needValidate").subscribe(r=>{});
  }

  addTag(idphoto:string){
    showPopup({title:"PHOTO.ENTERMESSAGE",placeholder:"",confirmButton:"LIB.VALIDATE",cancelButton:"LIB.CANCEL",type:"text",translate:this.translate}
      ,this.alertCtrl,(res) => {
        if (res != null){
          this.eventData.validatemessage(idphoto,res).subscribe(r=>{this.refresh()});
        }
      });
  }

  addModerator(){
    this.events.publish("moderator:add");
  }

  validatephoto(){
      this.eventData.validatemessage(this.lastphoto.id).subscribe(r=>{this.refresh()});
  };

  sendphoto(photo:any,legend:string="",func=null){
    var photo=Tools.createPhoto(this.userData.user,photo,legend,"photo");
    this.eventData.sendphoto(photo).subscribe(resp => {
      if(this.eventData.event.needValidate && this.userData.user.connexions.length<10)toast(this.toastCtrl,"PHOTOS.INFOVALIDATION",this.translate);
      this.refresh(true);
      if(this.photos.length>0)this.selLastPhoto(this.photos[0]);
      if(func!=null)func();
    },(err) => {
      this.refresh();
      if(func!=null)func();
    });
  };

  addFile(){
    var vm=this;
    openModal(this.modalCtrl,ImageCreatorPage,{ratio:1,maxsize:2500},(resp) => {
      if(resp!=null){
        vm.sendphoto(resp.value);
      }
    });
  }

  refresh(force:boolean=false){
    var vm=this;

    var showAllPhoto=(vm.userData.user.id==vm.eventData.event.owner.id || !vm.eventData.event.needValidate);

    if(!force && this.userData.selectTab!=undefined && this.userData.selectTab!="PhotosPage")return;

    this.eventData.getlastphoto(this.userData.user.id,false,0,5).subscribe((r:any) => {
      if(r!=null && r.items.length>0){
        vm.photos=[];
        var message:any={};
        r.items.forEach((photo) => {
          if(photo.dtCreate>this.lastRefresh && photo.from.id!=this.userData.user.id){
            message={
              title:"Shifumix - photo",
              body:"New photo from "+photo.from.firstname,
              image:photo.photo,
              vibrator:[200, 100, 200]
            };
            this.lastRefresh=photo.dtCreate;
          }
          if(photo.validate || this.eventData.event.Moderators.indexOf(this.userData.user.id)>-1 || photo.from.id==this.userData.user.id)
            vm.photos.push(photo); //Si la photo est validé ou si l'on est moderateur ou si on est le propriétaire
        });


        if(!force && message.hasOwnProperty("title"))Tools.notif(this.notifs,message,this.translate);

        //Recherche si la dernière photo sélectionner est bien dans la liste
        var rc=false;
        for(var pos=0;pos<Math.min(5,vm.photos.length);pos++){
          if(vm.photos[pos].Id==vm.lastphoto.Id){
            rc=true;
            break;
          }
        }

        if(!rc || this.userData.user.id==this.eventData.event.owner_id)pos=0;
        vm.selLastPhoto(vm.photos[pos]); //Permet la mise a jour de la photo en cours de visu
        }
    });
  }

  ionViewDidLoad() {
    this.events.subscribe("photo",() => {this.refresh()});
  }


  showUser(u){
    openModal(this.modalCtrl,PublicProfilPage,{userid: u.id,from:"tab.photos"});
  };


  onSetScore(id,step){
    var vote={event:this.eventData.event.id,from:this.userData.user.id,value:step,description:""};
    this.lastphoto.canVote=false;
    this.userData.sendvote(id,vote).subscribe((rep) => {
      if(rep!=null){
        if(this.userData.user.message.length>0)Tools.toast(this.toastCtrl,this.userData.user.message,this.translate);
        this.refresh(true);
      }
    });
  };

  getPhoto(event,withText=false,func=undefined) {
    if(event.value==undefined)return;
    this.message="PHOTO.SENDING";
    setTimeout(()=>{
        autoRotate(event.value,1,(r) => {
          this.message="PHOTO.SENDING";
          cropToSquare(r,0.75,(resp) => {
            this.message="PHOTO.SENDING";
            this.sendphoto(resp,"",()=>{
              this.message="";
            });
          });
        });
    },100);
  }

  addmessage(id){
    showPopup({title:"PHOTO.ENTERMESSAGE",default:this.lastphoto.text,placeholder:"",confirmButton:"LIB.VALIDATE",cancelButton:"LIB.CANCEL",type:"text",translate:this.translate}
      ,this.alertCtrl,(res) => {
        if (res != null){
          this.eventData.addlegend(this.lastphoto.id,res).subscribe(r=>{
            this.lastphoto.text=res;
            this.refresh();
          });
        }
      });
  }

  deleteMessage(id){
    var vm=this;
    this.eventData.deletemessage(id,this.userData.getId()).subscribe(rep=>{
      toast(vm.toastCtrl,"PHOTO.DELETED",this.translate);
      this.refresh(true);
    });
  }

  selLastPhoto(photo){
    var vm=this;
    vm.lastphoto=photo;
    if(vm.lastphoto.hasOwnProperty("votes") && vm.lastphoto.from.id!=vm.userData.user.id){
      vm.lastphoto.canVote=true;
      vm.lastphoto.votes.forEach(function (vote) {
        if (vm.lastphoto.canVote && vote.from == vm.userData.user.id){
          vm.lastphoto.canVote = false;
        }
      });
    } else
    if(vm.lastphoto.from.id!=vm.userData.user.id)vm.lastphoto.canVote=true;
  }


  downloadPhotos(col){
    col.items.forEach(function(photo){
    });
  };


  addtoblacklist(usertoblacklist:string,messageidtodelete:string) {
    this.deleteMessage(messageidtodelete);
    this.api.blacklist(this.userData.user.id,usertoblacklist,true).subscribe(()=>{});
  }
}



