import {Component, ElementRef, ViewChild} from '@angular/core';
import {Platform,ToastController,ViewController} from 'ionic-angular';
import {AlertController,IonicPage,NavController,NavParams} from 'ionic-angular';
import {TranslateService} from "@ngx-translate/core";

import {tirage} from "../../app/Maintools";
import {UserData} from "../../providers/user-data";
import {ApiProvider} from "../../providers/api/api";
import {SettingsProvider} from "../../providers/settings/settings";
import {DomSanitizer} from "@angular/platform-browser";

/**
 * Generated class for the LoginavatarPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-loginavatar',
  templateUrl: 'loginavatar.html',
})
export class LoginavatarPage {

  @ViewChild("container") container:ElementRef;
  pseudo:string="";
  from:string="";

  picture:string="./assets/img/wait.gif";
  currentCategory:string="";
  message:string="";
  index: number=0;
  female:boolean=false;
  items:any[]=[];
  avatars:any[]=[];
  size=6;
  notRobot:boolean=false;
  handle:any=null;
  sel_category: string = "";

  constructor(
    public sanitizer: DomSanitizer,
    public navCtrl: NavController, public navParams: NavParams,public alertCtrl:AlertController,public api:ApiProvider,
              public viewCtrl:ViewController,public userData: UserData,public settings:SettingsProvider,
              public toastCtrl:ToastController,public translate:TranslateService,public platform:Platform) {
    this.index=tirage(20);
    if(this.settings.isLocal || this.navParams.get("from")=="perso")this.notRobot=true;
  }

  ionViewDidEnter() {
    this.sel_category="figurinepop";
    if(this.settings.ihm=="pro"){
      if(this.pseudo==null || this.pseudo.length==0)this.pseudo="MonPseudo";
      this.sel_category="pro";

    }


    this.changeCategory({value:this.sel_category});

    this.message=this.navParams.get("message");
    setTimeout(()=>{this.message="";},3000);
    this.random(this.sel_category,()=>{
      if(this.navParams.get("autologin")==2)this.anonymousLogin();
    });

    this.size=4*2;
  }

  random(category:string,func:Function=null){
    this.currentCategory=category;
    if(category!="pro"){
      this.api.getavatar(category).subscribe((rep)=>{
        this.picture=rep.url;
        this.pseudo=rep.name;
        if(func!=null)func();
      });
    }
  }

  anonymousLogin () {
    if(this.pseudo.length>0){
      if(this.currentCategory=="pixels")
        this.gen_avatar(true,()=>{
          this.viewCtrl.dismiss({picture:this.picture,pseudo:this.pseudo});
        });
      else
        this.viewCtrl.dismiss({picture:this.picture,pseudo:this.pseudo});
    }
  }

  selAvatar(rep:any){
      this.picture=rep.url;
      if(rep.title!=null && rep.title.length>0)this.pseudo=rep.title;
  }

  changeAvatar(sens: number) {
    if(this.index+sens>0)this.index+=sens*this.size;
    if(this.index+this.size>this.avatars.length)this.index=this.avatars.length-20;
    if(this.index<0)this.index=0;

    this.items=[];
    for(var k=0;k<Math.min(this.size,this.avatars.length);k++){
      if(k+this.index<this.avatars.length)
        this.items.push({
          title:this.avatars[k+this.index][1],
          picture:this.avatars[k+this.index][2]
        });
    }
  }

  changeCategory(evt: any) {
    this.currentCategory=evt.value;
    if(evt.value=="pixels"){
      var pseudos=["loulou","riri","fifi","coco","kevin","bryan","gégé"];
      this.pseudo=pseudos[tirage(pseudos.length)];
      this.change_sexe("Male");
      this.gen_avatar(true);
    }
    else{
      this.picture="";
      if(this.currentCategory=="pro"){
        this.api.getAvatarsPro(36,this.pseudo).subscribe((rep:any)=>{
          this.avatars=[];
          var items=rep.avatars.split("#;#");
          items.forEach(s=>{
            this.avatars.push(["pro","",this.sanitizer.bypassSecurityTrustResourceUrl(s)]);
          });
          this.index=0;
          this.changeAvatar(0);
        });
      }else{
        this.api.getAvatars(evt.value).subscribe((rep:any)=>{
          this.avatars=rep.items;
          this.index=tirage(rep.items.length-20);
          this.changeAvatar(0);
          this.refresh_avatar();
        });
      }

    }
  }

  gen_avatar(save:boolean,func=null) {
    this.api.avatargenerator(this.pseudo,this.female,save).subscribe((r:any)=>{
      if(r!=null)
        this.picture=r.url;
      if(func!=null)func();
    });
  }

  change_sexe(val:any){
    this.female=false;
    if(val.value=="Female")this.female=true;
    this.gen_avatar(false);
  }

  refresh_avatar() {
    if(this.pseudo!=null && this.pseudo.indexOf("@gtest.com")>-1)
      this.notRobot=true;

    if(this.currentCategory=="pixels" || this.currentCategory=="pro"){
      clearTimeout(this.handle);
      this.handle=setTimeout(()=>{
        if(this.currentCategory=="pixels")
          this.gen_avatar(false);
        else
          this.changeCategory({value:"pro"});
      },1000);
    }
  }



  resolved(event){
    this.notRobot=true;
  }

    openGallery(gal:string) {
      let size = 40;
      let start = Math.max(1, this.index)
    }
}



