import {Component, ViewChild} from '@angular/core';
import {AlertController, Content, LoadingController, NavParams, ToastController, ViewController} from 'ionic-angular';
import {Events, IonicPage, ModalController, NavController} from 'ionic-angular';
import {ApiProvider} from "../../providers/api/api";
import {UserData} from "../../providers/user-data";
import {$$, contain, loading, openModal, showPopup, toast} from "../../app/Maintools";
import {ClosedeventPage} from "../closedevent/closedevent";
import {TranslateService} from "@ngx-translate/core";
import {AddeventPage} from "../addevent/addevent";
import {InvitePage} from "../invite/invite";

/**
 * Generated class for the OldeventsPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-oldevents',
  templateUrl: 'oldevents.html',
})
export class OldeventsPage {
  events:any[]=[];
  myevents:any[]=[];

  @ViewChild(Content) content: Content;

  constructor(public navCtrl: NavController, public userData:UserData,public alertCtrl:AlertController,
              public api:ApiProvider, public navParams: NavParams,public loadingCtrl:LoadingController,
              public translate:TranslateService,public eventBus:Events,public toastCtrl:ToastController,
              public modalCtrl:ModalController) {
  }

  ionViewDidLoad(){
    this.refresh();
  }

  refresh(){
    //var l=loading(this,"Loading",3);
    //Récupération des meilleurs événements pour moi
    var vm=this;
      vm.api.geteventsfrom(this.userData.user.id,20,false).subscribe(r=>{
        if(r!=null && r.items!=undefined){
          vm.myevents=[];
          vm.events=[];

          r.items.forEach((evt) => {
            if(evt.owner_id==this.userData.user.id)
              vm.myevents.push(evt);
            else{
              //L'événement ne doit pas être dans les événements conseillés pour moi
              vm.events.push(evt);
            }
          });
        }
      });
  }

  deleteEvent(evt){
    $$("Suppression de "+evt.id);
    this.userData.delevent(evt.id).subscribe(r=>{
      this.refresh();
    });
  }

  joinEvent(evt) {
    var vm=this;
    var param = {event: evt,user: vm.userData.user,type: 1,url: "index.html?event=" + evt.id + "&from=" + vm.userData.user.id};
    vm.eventBus.publish("event:login", param);
  }

  openEvent(evt){
    openModal(this.modalCtrl,ClosedeventPage,{event:evt});
  }

  addEvent(){
    openModal(this.modalCtrl,AddeventPage,{user:this.userData.user},(evt)  => {
      this.refresh();
    });
  }

  inviteEvent(evt){
    openModal(this.modalCtrl,InvitePage,{event:evt});
  }


  // scrollTo(elementId:string) {
  //   let yOffset = document.getElementById(elementId).offsetTop;
  //   this.content.scrollTo(0, yOffset, 4000)
  // }
}



