import { Component } from '@angular/core';
import {NavController, NavParams,ToastController, ViewController} from 'ionic-angular';
import {AlertController, Events, IonicPage, ModalController} from 'ionic-angular';
import {UserData} from "../../providers/user-data";
import * as Main from "../../app/Maintools"
import {ScreensPage} from "../screens/screens";
import {MusicserverPage} from "../musicserver/musicserver";
import {TranslateService} from "@ngx-translate/core";
import {DOMAIN_SERVER, openModal, openWindow, showConfirm, showPopup} from "../../app/Maintools";
import {autoRotate, createPhoto, getEnv,toast} from "../../app/Maintools";
import {ImageCreatorPage} from "../image-creator/image-creator";
import {TarifsPage} from "../tarifs/tarifs";
import {ApiProvider} from "../../providers/api/api";
import {InvitePage} from "../invite/invite";
import {LoginemailPage} from "../loginemail/loginemail";
import {ProfilPage} from "../profil/profil";
import {LoginavatarPage} from "../loginavatar/loginavatar";
import {PushNotificationsService} from "ng-push";
import {SettingsProvider} from "../../providers/settings/settings";


/**
 * Generated class for the PersoPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-perso',
  templateUrl: 'perso.html'
})
export class PersoPage {
  images:any[]=[];
  public openPopup: Function;
  friends:any[]=[];
  email:string="";
  n_connexions:number=0;
  services:any[]=[];
  from:string="";
  tarifs=["Basic","Pro","Premium","Pilote"];
  users_blacklist:any[]=[];
  users_follow:any[]=[];

  toggled: boolean = false;

  constructor(public modalCtrl:ModalController,public navCtrl: NavController,public toastCtrl:ToastController,public notifs:PushNotificationsService,
              public navParams: NavParams,public userData:UserData,public alertCtrl:AlertController,public viewCtrl:ViewController,
              public translate:TranslateService,public events:Events,public api:ApiProvider,public settings:SettingsProvider) {

    this.from=this.navParams.get("from");
    this.services=Main.getAllServices(this.settings.services,navParams.get("filter"),this.userData.user.connexions.length,this.settings.version);
    this.n_connexions=this.userData.user.connexions.length;
  }


  handleSelection(event) {
    this.userData.user.picture=event.char;
    this.toggled=!this.toggled;
    this.updateUser();
  }

  profils= [
    {"lib": "Perso","value": Main.PROFIL_PERSO,"help":""},
    {"lib": "Public","value": Main.PROFIL_PROPUBLIC,"help":""},
    {"lib": "Entreprise","value": Main.PROFIL_PROPRIVE,"help":""}
  ];

  creditAccount(){
    if(getEnv()==0){
      this.userData.user.realCredits+=20;
      this.userData.senduser("realCredits").subscribe(()=>{});
    }

  }

  selImage(){
    var vm=this;
    openModal(this.modalCtrl,ImageCreatorPage,{maxsize:600},function(resp){
      if(resp!=null)
        vm.userData.sendflyer(createPhoto(vm.userData.user,resp.value)).subscribe((r:any)=>{
          vm.userData.user.picture=r.url;
        })
    })
  }

  opentarifs(){
    openModal(this.modalCtrl,TarifsPage,{user:this.userData.user},r => {

    });
  }

  setPopupAction(fn: any) {
    this.openPopup = fn;
  }

  ionViewDidLoad() {
      this.refreshList();
      if(!this.userData.user.hasOwnProperty("followers"))this.userData.user["followers"]=[];

      //vm.refreshList(this.userData.user);
      for(let i=0;i<20;i++)this.userData.user.connexions.push(i);
  }

  openHome(){
    openWindow(this.userData.user.home);
  }


  openScreens(){
    this.navCtrl.push(ScreensPage,{user:this.userData.user.id});
  }

  updateUser(){
    //if(this.userData.user.email!=this.email && this.email.length>0)this.userData.user.email=this.email;
    this.userData.senduser("home,picture,musicServer,profil,tags,firstname,publicPatterns,picture,defaultAddress,humeur").subscribe(rep => {});
  }

  ionViewWillLeave(){
    this.updateUser();
  }

  updatePersoPhoto=function(res){
    var vm=this;
    autoRotate(res.value,0.3,(photo) => {
      vm.userData.sendflyer(createPhoto(vm.userData.user,photo)).subscribe(r=>{
        vm.userData.user.picture=r.url;
      });
    });
  };

  openMusicServer(){
    var vm=this;
    Main.openModal(this.modalCtrl,MusicserverPage,{user:this.userData.user},function(user){
      vm.userData.user=user;
      vm.updateUser();
    });
  }

  isActive(item){
    if(item.checked)
      return "primary";
    else
      return "secondary";
  }

  openTarifs(){
    openModal(this.modalCtrl,TarifsPage,{},resp => {
    });
  }

  onClickService(service) {
    var vm = this;
    if(!service.checked){
        Main.openGeneral(service.token, this.userData.user).then(()=> {
          //On récupére une mise a jour de l'utilisateur pour vérifier s'il a la connexion
          vm.updateUser();
          vm.userData.get().subscribe((resp:any) => {
            if(resp!=null){
              //TODO: a trouver mieux
              if(vm.userData.user.email!=null && vm.userData.user.email.length==0 && resp.email.length>0){
                toast(vm.toastCtrl,"PERSO.PROFILCHANGE",vm.translate);
                vm.events.publish("user:refresh",resp);
              }
              vm.userData.user = resp;
              vm.refreshList(service);
            }
          });
        });
      } else {
        vm.userData.revoketoken(service.token).subscribe(r=>{
          vm.userData.user=r;
          if(service.token=="deezer" || service.token=="spotify")vm.userData.clearPlaylist();
          vm.userData.playlist=[];
          vm.refreshList(service);
        });
      }
  };

   refreshList(new_service=""){
     this.userData.getUsersBlacklist().subscribe((resp:any )=>{
       if(resp!=null)this.users_blacklist=resp.items;
     });

     this.userData.getusersifollow().subscribe((resp:any )=>{
       if(resp!=null)this.users_follow=resp.items;
     });

     this.services.forEach((service)=>{
       var status=this.userData.user.hasOwnProperty("accessTokens") && this.userData.user.accessTokens.hasOwnProperty(service.token);
       service.checked=status;
     });

     if(new_service=="contact" || new_service=="facebook")this.userData.loadcontacts();
   };

   razUser(){
     var vm=this;
     showPopup({title:"PERSO.RAZUSER",placeholder:"",confirmButton:"OK",cancelButton:"LIB.CANCEL",type:"text",translate:this.translate}
       ,this.alertCtrl,(res) => {
          if(res=="yes")this.userData.razme().subscribe(r=>{
            vm.events.publish("user:logout");
          });
       });
   }

   removeFromBlacklist(user){
     this.api.blacklist(this.userData.user.id,user.id,false).subscribe(()=>{
       this.refreshList();
     });
   }

   resetSecretCode(){
     //TODO: a completer
   }

   showPrivacy(){
     openWindow(DOMAIN_SERVER+"/privacy.html?lang="+this.userData.user.lang);
   }

   showHelp(){
     openWindow(DOMAIN_SERVER+"/help_for_options.html?lang="+this.userData.user.lang);
   }

  giveEmail() {
    openModal(this.modalCtrl,LoginemailPage,{from:ProfilPage.name,pseudo:this.userData.user.firstname},(res)=>{
      if(res!=null){
        this.userData.user.firstname=res.firstname;
        this.userData.user.email=res.email;
        this.updateUser();
        this.userData.senduser("email").subscribe((r:any)=>{
          if(r.message.length==0){
            toast(this.toastCtrl,"PERSO.PROFILCHANGE",this.translate);
            this.userData.user=r;
          }
          else
            toast(this.toastCtrl,r.message,this.translate);
        });
      }
    });
  }

  selAvatar() {
    openModal(this.modalCtrl,LoginavatarPage,{pseudo:false,from:"perso"},(rep)=>{
      if(rep!=null)
        this.userData.user.picture=rep.picture;
    });
  }

  enableNotifications() {
    this.notifs.requestPermission();
  }

  removeUserFollow(user: any) {
    this.api.delFollower(this.userData.user.id,user.id).subscribe(()=>{
      setTimeout(()=>{this.refreshList();},1000)
    });
  }
}



